import { t } from '@konrad/reweb-aem';

export const isRequired = (message) => (value) => {
    return !value && value !== 0 && value !== false
        ? message
            ? message
            : t('This field is required.')
        : undefined;
};

export const isValidZipCode = (message) => (value) => {
    let zipCodeRegex = /^\d{5}(-\d{4})?$/;
    return !zipCodeRegex.test(value)
        ? message
            ? message
            : t('Invalid Zip Code format')
        : undefined;
};

export const emailIsValid = (message) => (value) => {
    let emailRegex = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return value && !emailRegex.test(value) ? (message ? message : t('Invalid email.')) : undefined;
};

//Us Phones validations
export const phoneNumberValid = (message) => (value) => {
    let phoneRegex = /^(1\s?)?((\([0-9]{3}\))|[0-9]{3})[\s-]?[\0-9]{3}[\s-]?[0-9]{4}$/;
    return !phoneRegex.test(value) ? (message ? message : t('Invalid phone number.')) : undefined;
};

export const isValidCustomerNumber = (cusNumLength, cusNumChars, customerNumberLabel, message) => (
    value
) => {
    let defaultMessage = 'Invalid ' + customerNumberLabel.toLowerCase() + '.';
    return (cusNumLength !== '' && value.length !== cusNumLength) ||
        value.indexOf(cusNumChars) !== 0
        ? message
            ? message
            : t(defaultMessage)
        : undefined;
};
