import React from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';
import { ALIGN_ITEMS } from '../../../../lib/constants';

const GridRowContent = (props) => {
    const { className, distribution, children, numCols, alignItems, gutters } = props;

    let cols = numCols === '3' ? 'three' : 'two';
    let dist = numCols === '3' && distribution;

    return (
        <div
            className={cx(
                'ak-grid-row__columns',
                { [`ak-grid-row__columns-${cols}`]: cols },
                { [`ak-grid-row--align-${alignItems}`]: alignItems },
                { [`ak-grid-row--distribute-1-2-1`]: dist },
                { [`ak-grid-row--gutters-${gutters}`]: gutters },
                className
            )}
        >
            {children}
        </div>
    );
};

GridRowContent.defaultProps = {
    numCols: 2,
    alignItems: 'top',
    gutters: 'default',
};

GridRowContent.propTypes = {
    className: PropTypes.string,
    alignItems: PropTypes.oneOf(ALIGN_ITEMS),
    distribution: PropTypes.bool,
};

GridRowContent.displayName = 'GridRowContent';

export default GridRowContent;
