import React, { useEffect, useState } from 'react';
import cx from 'classnames';
import { BaseButton, BaseTitle, BaseText, BaseIcon, BaseImage } from '../base';
import { t, linkService } from '@konrad/reweb-aem';
import { Subtitle, OfferDetails } from '../index';
import PropTypes from 'prop-types';
import { TERMS_AND_CONDITIONS_FOLDER, OFFER_TYPE_LABEL } from '../../lib/constants';

const Offer = (props) => {
    const {
        title,
        icon,
        duration,
        price,
        priceLabel,
        priceDescription,
        greenOfferImage,
        greenOfferText,
        marketingTitle,
        marketingBackground,
        enrollmentUrl,
        showDetails,
        productId,
        moreDetails,
        bestFor,
        edcLogo,
        edcName,
        animate,
        priorityCode,
        multifieldPriceBreakdown,
        isBundle,
        gasOffer,
        electricityOffer,
        gasIcon,
        electricityIcon,
        edcList,
        bundleId,
    } = props;

    const [drawerShow, setDrawerShow] = useState(false);

    const handleDrawerClose = () => {
        setDrawerShow(false);
    };

    const handleDrawerOpen = () => {
        setDrawerShow(true);
    };

    const renderContentDescription = ({
        price,
        priceLabel,
        priceDescription,
        index,
        offerType,
    }) => {
        const offerTypeIcon = offerType === 'GAS' ? gasIcon : electricityIcon;

        return (
            <div key={index} className="ak-offer__content-description-wrapper">
                <BaseText className="ak-offer__pricing" level="4">
                    <span className="highlight">{price}</span>
                    {priceLabel}
                </BaseText>
                <div className="ak-offer__description-block">
                    <div className="ak-offer__offer-type-info">
                        <BaseTitle
                            className="ak-offer__offer-type"
                            headingLevel="h3"
                            overrideStyle="subheading"
                            text={OFFER_TYPE_LABEL[offerType]}
                        />
                        {offerTypeIcon && (
                            <BaseImage className="ak-offer__offer-type-icon" url={offerTypeIcon} />
                        )}
                    </div>

                    <BaseText className="ak-offer__description" level="4" text={priceDescription} />
                </div>
            </div>
        );
    };

    const parsedEnrollmentUrl = `${linkService.get(enrollmentUrl)}?offer=${
        isBundle ? bundleId : productId
    }`;

    const generatePdfUrl = () => {
        if (!isBundle) {
            return TERMS_AND_CONDITIONS_FOLDER + '/' + priorityCode + '.pdf';
        } else {
            let pdfLinks = [];
            [gasOffer, electricityOffer].forEach((item) => {
                if (item) {
                    pdfLinks.push({
                        offerType: item.offerType,
                        link:
                            item && item.priorityCode
                                ? TERMS_AND_CONDITIONS_FOLDER + '/' + item.priorityCode + '.pdf'
                                : null,
                    });
                }
            });
            return pdfLinks;
        }
    };

    return (
        <div className="ak-offer">
            <div
                className={cx('ak-offer__wrapper', {
                    'ak-offer__wrapper--animate': animate,
                })}
            >
                <div className={`ak-offer__header ak-offer__header--${marketingBackground}`}>
                    <Subtitle text={marketingTitle} />
                </div>
                <div className="ak-offer__content">
                    <div className="ak-offer__content-header">
                        <BaseTitle className="ak-offer__title" headingLevel="h3">
                            {title}
                            {icon && <BaseImage className="ak-offer__title-icon" url={icon} />}
                        </BaseTitle>
                        <BaseText className="ak-offer__duration" level="3">
                            {duration}
                        </BaseText>
                    </div>

                    {isBundle
                        ? [gasOffer, electricityOffer].map((offerItem, index) => {
                              if (offerItem && offerItem.title) {
                                  return renderContentDescription({
                                      price: offerItem.price,
                                      priceLabel: offerItem.priceLabel,
                                      priceDescription: offerItem.priceDescription,
                                      index: index,
                                      offerType: offerItem.offerType,
                                  });
                              }
                              return null;
                          })
                        : renderContentDescription({
                              price,
                              priceLabel,
                              priceDescription,
                          })}

                    <BaseButton
                        className="ak-offer__content-cta"
                        type="primary"
                        url={parsedEnrollmentUrl}
                    >
                        {t('Enroll Now')}
                    </BaseButton>
                </div>
                {showDetails && (
                    <div className="ak-offer__footer">
                        <button
                            aria-label={t('Open the more details drawer')}
                            className="ak-offer__footer-cta"
                            onClick={handleDrawerOpen}
                        >
                            {t('View Details')}
                            <BaseIcon name="info-icon" />
                        </button>
                    </div>
                )}
            </div>

            <OfferDetails
                title={title}
                description={moreDetails}
                planDescription={bestFor}
                priceBreakdownArray={multifieldPriceBreakdown}
                amount={price}
                amountTerm={priceLabel}
                drawerShow={drawerShow}
                time={duration}
                timeTerm={t('Term')}
                greenOfferImage={greenOfferImage}
                greenOfferText={greenOfferText}
                pdfLink={generatePdfUrl()}
                handleDrawerClose={handleDrawerClose}
                enrollmentUrl={parsedEnrollmentUrl}
                edcLogo={edcLogo}
                edcName={edcName}
                showCTA={true}
                isBundle={isBundle}
                gasOffer={gasOffer}
                electricityOffer={electricityOffer}
                edcList={edcList}
                priorityCode={priorityCode}
                // fixedRatePlanDescription={fixedRatePlanDescription}
            />
        </div>
    );
};

Offer.displayName = 'Offer';

Offer.propTypes = {
    title: PropTypes.string,
    pricing: PropTypes.string,
    subtitle: PropTypes.string,
    duration: PropTypes.string,
    description: PropTypes.string,
    viewDetails: PropTypes.string,
    pricingLabel: PropTypes.string,
    isMostPopular: PropTypes.bool,
    viewDetailsUrl: PropTypes.string,
};

export default Offer;
