import React, { useRef, useState } from 'react';
import cx from 'classnames';
import PropTypes from 'prop-types';
import { BaseIcon, BaseText, BaseButton } from '../index';
import { t } from '@konrad/reweb-aem';

const BaseFileUpload = React.forwardRef((props, ref) => {
    const { id, label, className, onChange, multiple, accept, error, required, name } = props;

    const inputFileRef = useRef(null);
    const [files, setFiles] = useState([]);
    const [validTypeError, setValidTypeError] = useState(false);

    function handleBtnClick(e) {
        e.preventDefault();
        inputFileRef.current.click();
    }

    function fileListToArray(list) {
        const array = [];
        for (var i = 0; i < list.length; i++) {
            if (validTypes(list.item(i))) {
                array.push(list.item(i));
                setValidTypeError(false);
            } else {
                setValidTypeError(true);
            }
        }
        return array;
    }

    function validTypes(file) {
        const fileExtension = file.name.split('.').pop().toLowerCase();
        let str = accept.toLowerCase();
        const acceptedTypes = str.replace(/\s/g, '').split(',');
        const isValid = acceptedTypes.find((x) => x === `.${fileExtension}`);
        return isValid;
    }

    function onFilesAdded(e) {
        const files = e.target.files;
        if (onChange) {
            const array = fileListToArray(files);
            setFiles(array);
            onChange(array, e);
            e.target.value = null;
        }
    }

    function removeFiles(e, index) {
        e.preventDefault();
        files.splice(index, 1);
        setFiles([...files]);
        onChange([...files], e);
    }

    return (
        <div className={'base-file-upload'}>
            {label && (
                <label className={'base-file-upload__label'} htmlFor={id}>
                    <BaseText text={label} level="4" />
                </label>
            )}
            <input
                id={id}
                required={required}
                type="file"
                ref={inputFileRef}
                multiple={multiple}
                onChange={onFilesAdded}
                accept={accept}
                name={name}
                className={cx('base-file-upload__input', className)}
            />
            <div
                className={cx('base-file-upload__wrapper', {
                    'base-file-upload__wrapper--error': error,
                })}
            >
                <BaseIcon name="file-text" className="base-file-upload__icon" />
                <div className="base-file-upload__text">
                    <BaseText
                        className="base-file-upload__text--left"
                        text={t('Upload your file here')}
                        level="3"
                    />
                    <BaseButton
                        ref={ref}
                        className="base-file-upload__text--right"
                        type="pagination"
                        altText={t('Click to upload a file')}
                        onClick={handleBtnClick}
                    >
                        {t('Browse')}
                    </BaseButton>
                </div>
            </div>
            {validTypeError && (
                <BaseText
                    className="base-file-upload__error-message"
                    text={t('Error - that file type is not accepted')}
                    level={'3'}
                />
            )}
            <div className="base-file-upload__filelist">
                {files.length > 0 &&
                    files.map((file, index) => (
                        <div key={file.name} className="base-file-upload__file">
                            <div className="base-file-upload__file--left">
                                <BaseIcon name="circle-check" />
                                <BaseText
                                    className="base-file-upload__file--text"
                                    text={t(file.name)}
                                    level="3"
                                />
                            </div>
                            <BaseButton
                                className="base-file-upload__file--close"
                                type="pagination"
                                hasIconOnly
                                onClick={(e) => removeFiles(e, index)}
                                altText={t(`Delete ${file.name}`)}
                            >
                                <BaseIcon name="close-sign" />
                            </BaseButton>
                        </div>
                    ))}
            </div>
        </div>
    );
});

BaseFileUpload.propTypes = {
    id: PropTypes.string,
    name: PropTypes.string,
    label: PropTypes.string,
    multiple: PropTypes.bool,
    required: PropTypes.bool,
    className: PropTypes.string,
    accept: PropTypes.string.isRequired,
    onChange: PropTypes.func.isRequired,
};

export default BaseFileUpload;
