import React from 'react';
import uuidv4 from 'uuid/v4';
import cx from 'classnames';
import { linkService } from '@konrad/reweb-aem';
import { SearchBar } from '../index';
import ScrollLock from '../../lib/utils/scrollLock';
import HeaderBanner from './HeaderBanner';
import { BaseLink, BaseText, BaseTitle, BaseImage, BaseIcon } from '../base';

const TAB_KEY = 9;
const ESC_KEY = 27;
const EVENTS = {
    KEY_DOWN: 'keydown',
    CLICK: 'click',
};

const CLASSES = {
    MOUSE_IN: 'mouse-in',
    MOUSE_OUT: 'mouse-out',
    OPEN: 'open',
    CLOSED: 'closed',
    BLURRY: 'blurry-overlay',
};

class DesktopHeader extends React.PureComponent {
    state = {
        openCategory: null,
        isSearchOpen: false,
    };

    handleKeyDown = (e) => {
        if (e.keyCode === ESC_KEY) {
            const { isSearchOpen, openCategory } = this.state;
            if (isSearchOpen) {
                this.closeSearchbar();
            }
            if (openCategory) {
                this.closeCategory();
            }
        }
    };

    handleClickOutside = ({ target }) => {
        const header = document.querySelector('.ak-header');
        const isClickInside = header.contains(target);
        if (!isClickInside) {
            this.closeCategory();
        }
    };

    openCategory = (category, target) => {
        const setFocusToOpenCategory = () => {
            const openCategoryContainer = document.querySelector('.ak-header__secondary-nav-link');
            openCategoryContainer.focus();
            document.body.classList.add(CLASSES.BLURRY);
        };
        document.addEventListener(EVENTS.KEY_DOWN, this.handleKeyDown);
        document.body.addEventListener(EVENTS.CLICK, this.handleClickOutside);
        ScrollLock.lockBody();
        this.setState(
            {
                openCategory: {
                    ...category,
                    openerRef: target,
                },
                isSearchOpen: false,
            },
            setFocusToOpenCategory
        );
    };

    closeCategory = (e) => {
        if (!e || (e.keyCode === TAB_KEY && !e.shiftKey)) {
            document.removeEventListener(EVENTS.KEY_DOWN, this.handleKeyDown);
            document.body.classList.remove(CLASSES.BLURRY);
            ScrollLock.unlockBody();
            document.body.removeEventListener(EVENTS.CLICK, this.handleClickOutside);
            this.setState({ openCategory: null });
        }
    };

    toggleSearchbar = () => {
        const { isSearchOpen, openCategory } = this.state;
        if (isSearchOpen) {
            document.removeEventListener(EVENTS.KEY_DOWN, this.handleKeyDown);
        } else {
            document.addEventListener(EVENTS.KEY_DOWN, this.handleKeyDown);
        }
        if (openCategory) {
            this.closeCategory();
        }
        const focusElement = isSearchOpen ? '.ak-header__search-btn' : '.ak-search-form__input';
        const cb = () => document.querySelector(focusElement).focus();
        this.setState(
            (prevState) => ({
                isSearchOpen: !prevState.isSearchOpen,
                openCategory: null,
            }),
            cb
        );
    };

    closeSearchbar = (e) => {
        e.preventDefault();
        document.removeEventListener(EVENTS.KEY_DOWN, this.handleKeyDown);
        this.setState({ isSearchOpen: false }, () =>
            document.querySelector('.ak-header__search-btn').focus()
        );
    };

    onMouseEnter = ({ target }) => {
        const element = target;
        element.classList.add(CLASSES.HOVER);
    };

    onMouseLeave = ({ target }) => {
        const element = target;
        element.classList.remove(CLASSES.HOVER);
    };

    renderCategories = (category, index) => {
        const { openCategory } = this.state;
        const hasLinks = category.links && category.links.length;
        const isOpen = openCategory && category.categoryTitle === openCategory.categoryTitle;
        const buttonClass = hasLinks ? (isOpen ? CLASSES.OPEN : CLASSES.CLOSED) : '';
        const handleClick = ({ target }) => {
            if (isOpen) {
                this.closeCategory();
            } else {
                this.openCategory(category, target.id);
            }
        };

        const currentLink = this.props.currentPage;
        let selected = false;

        //Sets the selected underline under the active section
        if (category.links) {
            for (let i = 0; i < category.links.length; i++) {
                if (
                    category.links[i] &&
                    category.links[i].url &&
                    currentLink === category.links[i].url
                ) {
                    selected = true;
                }
            }
        }
        return (
            <li className="ak-header__nav-item" key={uuidv4()}>
                <button
                    className={cx(`ak-header__nav-item__button ${buttonClass}`, {
                        selected: selected,
                    })}
                    id={`ak-header__nav-item__button-${index}`}
                    onClick={handleClick}
                    onMouseEnter={this.onMouseEnter}
                    onMouseLeave={this.onMouseLeave}
                >
                    {category.categoryTitle}
                </button>
            </li>
        );
    };

    renderSectionLinks = (link, index, sectionLinks) => {
        const isFirstLink = index === 0;
        const isLastLink = index === sectionLinks.length - 1;
        const handleFocusOut = (e) => {
            if (isLastLink) {
                return this.closeCategory(e);
            }
            if (isFirstLink && e.keyCode === TAB_KEY && e.shiftKey) {
                e.preventDefault();
                return this.closeCategory();
            }
        };
        return (
            <li className="ak-header__secondary-nav-item" key={`header-section-link${index}`}>
                <BaseImage className="ak-header__secondary-nav-icon" url={link.iconUrl} />
                <BaseLink
                    className="ak-header__secondary-nav-link"
                    url={linkService.get(link.url)}
                    external={link.target === 'external'}
                    onKeyDown={isFirstLink || isLastLink ? handleFocusOut : undefined}
                >
                    {link.text}
                </BaseLink>
            </li>
        );
    };

    renderOpenCategory = (headerHeight) => {
        const { openCategory } = this.state;
        const sectionLinks = openCategory.links.map(this.renderSectionLinks);
        return (
            <div className="ak-header__category" style={{ top: `${headerHeight}px` }}>
                <div className="ak-header__category-container">
                    <div className="ak-header__category-details">
                        <BaseTitle
                            className="ak-header__category-title"
                            text={openCategory.sectionTitle}
                            headingLevel="h2"
                            overrideStyle="h3"
                        />
                        <BaseText
                            className="ak-header__category-description"
                            text={openCategory.sectionDescription}
                            level="4"
                        />
                    </div>
                    <ul className="ak-header__secondary-nav">{sectionLinks}</ul>
                </div>
            </div>
        );
    };

    componentDidUpdate(prevProps, prevState, snapshot) {
        const prevCategory = prevState.openCategory;
        const { openCategory } = this.state;
        if (prevCategory && !openCategory) {
            const openerCategoryBtn = document.getElementById(prevCategory.openerRef);
            openerCategoryBtn.focus();
        }
    }

    render = () => {
        const {
            ctaUrl,
            ctaText,
            ctaTarget,
            categories,
            logoUrl,
            logoAltText,
            searchResultsPath,
            hideSearchBar,
            displayBanner,
            bannerTitle,
            titleHeadingLevel,
            bannerDescription,
            bannerCtaLabel,
            bannerCtaLink,
            bannerCtaLinkTarget,
            isEdit,
            headerHeight,
            handleResize,
        } = this.props;

        const { openCategory, isSearchOpen } = this.state;

        return (
            <header className={`ak-header ${openCategory || isSearchOpen ? CLASSES.OPEN : ''}`}>
                <HeaderBanner
                    displayBanner={displayBanner}
                    bannerTitle={bannerTitle}
                    titleHeadingLevel={titleHeadingLevel}
                    bannerDescription={bannerDescription}
                    bannerCtaLabel={bannerCtaLabel}
                    bannerCtaLink={bannerCtaLink}
                    bannerCtaLinkTarget={bannerCtaLinkTarget}
                    isEdit={isEdit}
                    headerHeight={headerHeight}
                    handleResize={handleResize}
                />
                <nav className="ak-header__nav">
                    <BaseLink className="ak-header__brand" url={linkService.get(logoUrl)}>
                        <span className="sr-only">{logoAltText}</span>
                        <BaseIcon name="brand-logo-color" />
                    </BaseLink>
                    <ul className="ak-header__primary-nav">
                        {categories.map(this.renderCategories)}
                    </ul>
                    <div className="ak-header__additional-content">
                        {!hideSearchBar && (
                            <button
                                className={`ak-header__search-btn ${
                                    isSearchOpen ? CLASSES.OPEN : CLASSES.CLOSED
                                }`}
                                aria-expanded={false}
                                aria-controls="header-search-bar"
                                aria-label="Open search bar"
                                onClick={this.toggleSearchbar}
                            >
                                <BaseIcon name="search-icon" />
                            </button>
                        )}
                        <BaseLink
                            className="ak-header__cta"
                            url={linkService.get(ctaUrl)}
                            external={ctaTarget === 'external'}
                        >
                            {ctaText}
                        </BaseLink>
                    </div>
                </nav>
                <div
                    className={`ak-header__category-wrapper ${
                        openCategory ? CLASSES.OPEN : CLASSES.CLOSED
                    }`}
                >
                    {openCategory && this.renderOpenCategory(headerHeight)}
                </div>
                {!hideSearchBar && (
                    <SearchBar
                        placeholder="Search"
                        searchResultsPath={searchResultsPath}
                        popUpId="header-search-bar"
                        closePopUp={this.closeSearchbar}
                        isSearchOpen={isSearchOpen}
                    />
                )}
            </header>
        );
    };
}

export default DesktopHeader;
