import React from 'react';
import PropTypes from 'prop-types';
import { BaseLink, BaseText, BaseImage } from '../base';
import { linkService, t } from '@konrad/reweb-aem';

const EnrollmentHeader = (props) => {
    const { logo, logoUrl, phoneLabel, phoneNumber, phoneIcon } = props;

    return (
        <header className="ak-enrollment-header">
            <div className="ak-enrollment-header__container">
                <BaseLink className="ak-enrollment-header__nav-icon" url={linkService.get(logoUrl)}>
                    <span className="sr-only">{t('Logo')}</span>
                    <BaseImage className="ak-enrollment-header__nav-icon" url={logo} />
                </BaseLink>
                {phoneNumber && (
                    <div className="ak-enrollment-header__content">
                        <BaseText className="ak-enrollment-header__label" level={'4'}>
                            {phoneLabel}
                        </BaseText>
                        <div className="ak-enrollment-header__phone-container">
                            <BaseLink url={`tel:+${phoneNumber && phoneNumber.replace(/-/g, '')}`}>
                                <BaseImage
                                    className="ak-enrollment-header__phone-icon"
                                    url={phoneIcon}
                                />
                                <BaseText level="3" text={phoneNumber} wrapper="span" />
                            </BaseLink>
                        </div>
                    </div>
                )}
            </div>
        </header>
    );
};

EnrollmentHeader.displayName = 'EnrollmentHeader';

EnrollmentHeader.propTypes = {
    logo: PropTypes.string,
    phoneIcon: PropTypes.string,
    phoneNumber: PropTypes.string,
    phoneLabel: PropTypes.string,
};

export default EnrollmentHeader;
