import React, { useEffect, useState } from 'react';
import { t, linkService } from '@konrad/reweb-aem';
import withBreakpoint from '../../lib/utils/withBreakpoint';
import MobileHeader from './MobileHeader';
import DesktopHeader from './DesktopHeader';

const Header = (props) => {
    const [headerHeight, setHeaderHeight] = useState(0);

    const logoAltText = props.logoAltText || t('Logo');
    const categories =
        (props.multifieldCategories &&
            props.multifieldCategories.map((category) => ({
                ...category,
                links: category.multifieldNavigationItems,
            }))) ||
        [];

    const newProps = {
        categories,
        logoAltText,
        searchResultsPath: linkService.get(props.searchLink),
        logoUrl: linkService.get(props.logoUrl),
        ...props,
    };

    const handleResize = () => {
        const currentHeaderHeight = document.getElementsByClassName('ak-header')[0].offsetHeight;
        if (headerHeight !== currentHeaderHeight) {
            setHeaderHeight(currentHeaderHeight);
        }
    };

    useEffect(() => {
        handleResize();

        window.addEventListener('resize', handleResize);

        return () => {
            window.removeEventListener('resize', handleResize);
        };
    }, []);

    return props.isDesktop ? (
        <DesktopHeader {...newProps} headerHeight={headerHeight} handleResize={handleResize} />
    ) : (
        <MobileHeader {...newProps} headerHeight={headerHeight} handleResize={handleResize} />
    );
};

Header.displayName = 'Header';

export default withBreakpoint(Header);
