import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { t, linkService } from '@konrad/reweb-aem';
import withBreakpoint from '../../lib/utils/withBreakpoint';
import { BaseButton, BaseIcon, BaseInput } from '../base';

const BLOCK_NAME = 'ak-search-bar';

const CLASSES = {
    OPEN: 'open',
    CLOSED: 'closed',
};

const SearchBar = (props) => {
    const [query, setQuery] = useState('');

    const { isSearchOpen, placeholder, popUpId, closePopUp, searchResultsPath, isDesktop } = props;

    const handleSearchSubmit = (e) => {
        e.preventDefault();
        const parsedUrl = linkService.get(searchResultsPath);
        window.location.href = `${window.location.origin}${parsedUrl}?q=${query}`;
    };

    const updateQuery = (val) => setQuery(val);

    return (
        <div
            className={`${BLOCK_NAME} ${isSearchOpen ? CLASSES.OPEN : CLASSES.CLOSED}`}
            id={popUpId}
        >
            <form className="ak-search-form" onSubmit={handleSearchSubmit} noValidate>
                <BaseInput
                    className="ak-search-form__input"
                    value={query}
                    onChange={updateQuery}
                    placeholder={placeholder}
                    required
                    ariaLabel={placeholder}
                />
                <BaseButton isSubmit hasIconOnly={!isDesktop} className={`ak-search-form__btn`}>
                    {isDesktop ? (
                        <span>Search</span>
                    ) : (
                        <BaseIcon className="ak-search-form__btn--icon" name="search-icon" />
                    )}
                </BaseButton>
                {popUpId && (
                    <button
                        aria-label={t('Close Search Form')}
                        className="ak-search-form__close-btn"
                        aria-expanded="true"
                        aria-controls={popUpId}
                        onClick={closePopUp}
                    >
                        <BaseIcon name="close-sign" />
                    </button>
                )}
            </form>
        </div>
    );
};

SearchBar.displayName = 'SearchBar';

SearchBar.propTypes = {
    placeholder: PropTypes.string.isRequired,
    searchResultsPath: PropTypes.string.isRequired,
    popUpId: PropTypes.string,
    closePopUp: PropTypes.func,
    isSearchOpen: PropTypes.bool,
};

export default withBreakpoint(SearchBar);
