import React, { useState } from 'react';
import cx from 'classnames';
import uuidv4 from 'uuid/v4';
import akronAPI from '../../api';
import { PlanModal } from '../index';
import { linkService, t } from '@konrad/reweb-aem';
import withBreakpoint from '../../lib/utils/withBreakpoint';
import { isRequired, isValidZipCode } from '../../lib/fieldValidations';
import { BaseText, BaseButton, BaseIcon, BaseInput, Box } from '../base';
import PropTypes from 'prop-types';

const ZipCodeForm = (props) => {
    const {
        url,
        search,
        ctaText,
        isMobile,
        className,
        noEDCLink,
        modalTitle,
        modalDescription,
    } = props;

    const inputRef = React.createRef();
    const parsedUrl = linkService.get(url);
    const parsedNoEDCUrl = linkService.get(noEDCLink);
    const zipCodeValidators = [isRequired(t('Please add a Zip Code')), isValidZipCode()];

    const [id] = useState(`zip-code-${uuidv4()}`);
    const [zipCode, setZipCode] = useState('');
    const [inputHover, setInputHover] = useState(false);
    const [inputFocus, setInputFocus] = useState(false);
    const [errorMessage, setErrorMessage] = useState('');
    const [modalShow, setModalShow] = useState(false);
    const [utilities, setUtilities] = useState([]);
    const [loading, setLoading] = useState(false);

    const handleFormSubmit = (e) => {
        e.preventDefault();

        let validationMessage = '';
        let parsedZipCode = zipCode.trim();

        for (let validator in zipCodeValidators) {
            validationMessage = zipCodeValidators[validator](zipCode);
            if (validationMessage) {
                break;
            }
        }

        if (validationMessage) {
            inputRef && inputRef.current.focus();
            setErrorMessage(validationMessage);
        } else {
            setErrorMessage(t(''));
            setLoading(true);
            akronAPI
                .getEDCByZip(zipCode)
                .then((response) => {
                    setLoading(false);
                    const utilities = response.utilities;
                    if (utilities && utilities.length) {
                        if (utilities.length > 1) {
                            handleModalOpen(utilities);
                        } else {
                            window.location.href = `${window.location.origin}${parsedUrl}?zip=${parsedZipCode}&edc=${response.utilities[0].code}`;
                        }
                    } else {
                        window.location.href = `${window.location.origin}${parsedNoEDCUrl}`;
                    }
                })
                .catch(() => {
                    setLoading(false);
                    window.location.href = `${window.location.origin}${parsedNoEDCUrl}`;
                });
        }
    };

    const handleModalClose = () => {
        setModalShow(false);
    };

    const handleModalOpen = (utilities) => {
        setModalShow(true);
        setUtilities(utilities);
    };

    return (
        <>
            <PlanModal
                modalTitle={modalTitle}
                handleModalClose={handleModalClose}
                modalDescription={modalDescription}
                modalShow={modalShow}
                utilities={utilities}
                zipCode={zipCode}
                url={url}
            />
            <form
                noValidate
                className={cx('ak-zip-code-form', className, {
                    'ak-zip-code-form--invalid': errorMessage,
                })}
                onSubmit={handleFormSubmit}
            >
                <Box
                    className={cx('ak-zip-code-form__form-wrapper', {
                        'ak-zip-code-form__form-wrapper--focus': inputFocus,
                        'ak-zip-code-form__form-wrapper--hover': inputHover,
                    })}
                >
                    {!isMobile && <BaseIcon className="ak-zip-code-form__icon" name="location" />}
                    <BaseInput
                        id={id}
                        ref={inputRef}
                        value={zipCode}
                        placeholder={search}
                        error={errorMessage}
                        mask="99999"
                        maskChar=""
                        onChange={(val) => setZipCode(val)}
                        onBlur={() => setInputFocus(false)}
                        onFocus={() => setInputFocus(true)}
                        onMouseEnter={() => setInputHover(true)}
                        onMouseleave={() => setInputHover(false)}
                        className="ak-zip-code-form__input-text"
                        required
                    />
                    <BaseButton
                        isSubmit
                        loading={loading}
                        className="ak-zip-code-form__submit-button"
                        altText={t('Submit Zip Code')}
                    >
                        {isMobile ? (
                            <BaseIcon name="white-arrow" />
                        ) : (
                            <BaseText text={ctaText ? ctaText : t('Find Offers')} type="cta" />
                        )}
                    </BaseButton>
                </Box>
                {errorMessage && (
                    <BaseText
                        id={`${id}-error`}
                        className="ak-zip-code-form__error-message"
                        text={errorMessage}
                        level="3"
                    />
                )}
            </form>
        </>
    );
};

ZipCodeForm.displayName = 'ZipCodeForm';

ZipCodeForm.propTypes = {
    url: PropTypes.string,
    search: PropTypes.string,
    ctaText: PropTypes.string,
    isMobile: PropTypes.bool,
    className: PropTypes.string,
    handleModalOpen: PropTypes.func,
};

export default withBreakpoint(ZipCodeForm);
