import React from 'react';
import cx from 'classnames';
import PropTypes from 'prop-types';
import svgMap from '../../../icons';

const BaseIcon = (props) => {
    const { className, name, altText } = props;
    const SvgIcon = svgMap[name];

    if (!SvgIcon) {
        console.error(`Icon not found with the name ${name}`);
        return null;
    }

    return (
        <SvgIcon
            className={cx('base-icon', className)}
            alt={altText || undefined}
            aria-label={altText || undefined}
            aria-hidden={!altText ? true : undefined}
        />
    );
};

BaseIcon.propTypes = {
    altText: PropTypes.string,
    name: PropTypes.oneOf(Object.keys(svgMap)),
};

export default BaseIcon;
