import React, { useState, useEffect } from 'react';
import cx from 'classnames';
import { Box, BaseText, BaseTitle, BaseCheckbox } from '../base';

const InvestorsRelations = (props) => {
    const { subtitle, title, description, checkboxLabel, children } = props;
    const contentRef = React.createRef();
    const checkboxRef = React.createRef();
    const [disclaimerCheckbox, setDisclaimerCheckbox] = useState(false);

    useEffect(() => {
        const currentNode = contentRef.current;
        const elements = currentNode.querySelectorAll('button, [href], input, select, textarea');
        if (!disclaimerCheckbox) {
            checkboxRef.current.removeAttribute('tabindex');
            elements.forEach((el) => {
                el.setAttribute('tabindex', '-1');
            });
        } else {
            checkboxRef.current.setAttribute('tabindex', '-1');
            elements.forEach((el) => {
                el.removeAttribute('tabindex');
            });
        }
    }, [disclaimerCheckbox]);

    return (
        <Box
            direction="row"
            justify="center"
            alignment="center"
            className="ak-investors-relations__content"
        >
            <div
                className={cx('ak-investors-relations__disclaimer', {
                    'ak-investors-relations__opacity': disclaimerCheckbox,
                })}
            >
                {disclaimerCheckbox && <div className="ak-investors-relations__overlay"></div>}
                <BaseText className="ak-investors-relations__subtitle" text={subtitle} level="4" />
                <BaseTitle
                    className="ak-investors-relations__title"
                    text={title}
                    headingLevel="h2"
                    overrideStyle="h4"
                />
                <BaseText
                    className="ak-investors-relations__description"
                    text={description}
                    level="4"
                />
                <BaseCheckbox
                    background
                    ref={checkboxRef}
                    text={checkboxLabel}
                    value={'disclaimer'}
                    checked={disclaimerCheckbox}
                    onChange={(val) => setDisclaimerCheckbox(val)}
                />
            </div>
            <div className="ak-investors-relations__children-content">
                {!disclaimerCheckbox && <div className="ak-investors-relations__overlay"></div>}
                <div
                    className={cx('ak-investors-relations__children', {
                        'ak-investors-relations__opacity': !disclaimerCheckbox,
                    })}
                    ref={contentRef}
                >
                    {children}
                </div>
            </div>
        </Box>
    );
};

InvestorsRelations.displayName = 'InvestorsRelations';

export default InvestorsRelations;
