import React from 'react';
import { BaseTitle, Box, BaseImage, BaseText, BaseButton, BaseModal, BaseIcon } from '../base';
import { useModal } from '../hooks/useModal';
import { t } from '@konrad/reweb-aem';
import cx from 'classnames';
import parse from 'html-react-parser';
import PropTypes from 'prop-types';

const Video = (props) => {
    const { title, subtitle, thumbnail, imageAltText, video } = props;

    const [modalShow, setModalShow, toggleModal] = useModal();
    const iframe = `<iframe src=${video} frameborder="0" title="video" width="100%" height="100%"></iframe>`;
    const BLOCK_NAME = 'ak-video';
    const handleModalClose = () => setModalShow(false);

    return (
        <Box direction="row" justify="center" alignment="center" className="ak-video__container">
            <BaseModal modalState={modalShow} handleClose={handleModalClose} id={BLOCK_NAME}>
                <div className={cx('ak-video__video', { 'ak-video__show': modalShow })}>
                    {parse(iframe)}
                </div>
            </BaseModal>
            <BaseImage url={thumbnail} altText={imageAltText} className="ak-video__thumbnail" />
            <Box direction="col" justify="left" alignment="left" className="ak-video__content">
                <BaseButton
                    ariaExpanded="false"
                    ariaControls={BLOCK_NAME}
                    hasIconOnly
                    onClick={toggleModal}
                    type="primary"
                    className="ak-video__btn-play"
                    altText={t('Open Video Modal')}
                >
                    <BaseIcon name="play-white" className="ak-video__icon" />
                </BaseButton>
                <BaseTitle text={title} headingLevel="h4" />
                <BaseText text={subtitle} level="4" />
            </Box>
        </Box>
    );
};

Video.displayName = 'Video';

Video.propTypes = {
    title: PropTypes.string,
    subtitle: PropTypes.string,
    thumbnail: PropTypes.string,
    imageAltText: PropTypes.string,
    video: PropTypes.string,
};

export default Video;
