import React, { useState, useEffect } from 'react';
import { t } from '@konrad/reweb-aem';
import { Offer, Message } from '../index';
import withBreakpoint from '../../lib/utils/withBreakpoint';
import TabsNavigation from '../ak-tabs-navigation/ak-tabs-navigation';
import { BaseButton, BaseTitle, BaseText, BaseImage } from '../base';

const PlansFrame = (props) => {
    const TOTAL_PER_ROW = 3;

    // const [errorMsg, setErrorMsg] = useState('');
    // const [promoCode, setPromoCode] = useState('');
    // const [modalShow, setModalShow] = useState(false);
    // const [modalPromoCode, setModalPromoCode] = useState('');

    const [offersIter, setOffersIter] = useState({
        GAS: 0,
        ELECTRICITY: 0,
        BUNDLE: 0,
    });
    const [allOffers, setAllOffers] = useState([]);
    const [currentIndex, setCurrentIndex] = useState(0);

    const {
        // Content Props
        edc,
        zip,
        electricityOffers,
        gasOffers,
        bundleOffers,
        isEdit,
        validEDC,
        subtitle,
        showMore,
        phoneIcon,
        phoneTitle,
        availableOffers,
        gasTab,
        electricTab,
        bundleTab,

        // Promo Code Modal Props
        // promoCodeLabel,
        // promoCodeModalCTA,
        // promoCodeModalTitle,
        // promoCodeModalLabel,
        // promoCodeModalErrorMsg,
        // promoCodeModalPlaceholder,

        // Error Message Props
        ctaText,
        errorIcon,
        errorTitle,
        description,
        headingLevel,
        ctaLinkError,
        ctaLinkTargetError,
    } = props;

    useEffect(() => {
        if (validEDC && (offersIter.GAS > 0 || offersIter.ELECTRICITY > 0)) {
            const container = document.querySelector('.ak-plans-frame__content-wrapper');
            container.scrollIntoView(true);
        }
    }, [offersIter, validEDC]);

    useEffect(() => {
        const offersArray = [];
        [bundleOffers, gasOffers, electricityOffers].forEach((offer) => {
            if (offer.offersList.length > 0) {
                offersArray.push(offer);
            }
        });
        setAllOffers(offersArray);
    }, [electricityOffers, gasOffers, bundleOffers]);

    // const isCodeValid = () => false; //Eventually should validate against the API
    // const openModal = () => {
    //     setModalShow(true);
    //     setErrorMsg('');
    // };
    // const handleClearPromoCode = () => setPromoCode('');
    //
    // const handleModalClose = () => {
    //     setModalShow(false);
    //     setModalPromoCode('');
    // };
    // const handleModalSubmit = () => {
    //     if (isCodeValid(modalPromoCode)) {
    //         setPromoCode(modalPromoCode);
    //         handleModalClose();
    //     } else {
    //         setErrorMsg(promoCodeModalErrorMsg);
    //     }
    // };

    const offsetValues = {
        GAS: offersIter.GAS * TOTAL_PER_ROW,
        ELECTRICITY: offersIter.ELECTRICITY * TOTAL_PER_ROW,
        BUNDLE: offersIter.BUNDLE * TOTAL_PER_ROW,
    };

    const hasMoreOffers = {
        GAS: (offersIter.GAS + 1) * TOTAL_PER_ROW <= gasOffers.offersList.length - 1,
        ELECTRICITY:
            (offersIter.ELECTRICITY + 1) * TOTAL_PER_ROW <= electricityOffers.offersList.length - 1,
        BUNDLE: (offersIter.BUNDLE + 1) * TOTAL_PER_ROW <= bundleOffers.offersList.length - 1,
    };

    const tabTitles = {
        GAS: gasTab || 'Gas only',
        ELECTRICITY: electricTab || 'Electricity only',
        BUNDLE: bundleTab || 'Bundle',
    };

    const renderCards = (offer, index, offerType) => {
        let animateOffer =
            offersIter[offerType] === 0
                ? true
                : index >= (offersIter[offerType] - 1) * TOTAL_PER_ROW + TOTAL_PER_ROW;

        if (offer.isBundle) {
            let edcList = [];

            if (offer.bundleGasOffer && offer.bundleGasOffer.edcDetails) {
                edcList.push({
                    offerType: 'GAS',
                    edcName: offer.bundleGasOffer.edcDetails.name,
                    edcLogo: offer.bundleGasOffer.edcDetails.logo,
                });
            }
            if (offer.bundleElectricityOffer && offer.bundleElectricityOffer.edcDetails) {
                edcList.push({
                    offerType: 'ELECTRICITY',
                    edcName: offer.bundleElectricityOffer.edcDetails.name,
                    edcLogo: offer.bundleElectricityOffer.edcDetails.logo,
                });
            }

            return (
                <Offer
                    gasOffer={offer.bundleGasOffer}
                    electricityOffer={offer.bundleElectricityOffer}
                    edcList={edcList}
                    {...offer}
                    animate={animateOffer}
                    key={index}
                />
            );
        }

        return (
            <Offer
                edcName={edc.name}
                edcLogo={edc.logo}
                {...offer}
                animate={animateOffer}
                key={index}
            />
        );
    };

    const generateMultifieldItems = () => {
        const multifieldItems = allOffers.map((item) => {
            return {
                title: tabTitles[item.type],
            };
        });
        return multifieldItems;
    };

    const generateTabList = () => {
        return allOffers.map((offer) => {
            return {
                component: (
                    <div className="ak-plans-frame__content-wrapper">
                        <div className="ak-plans-frame__content">
                            <div className="ak-plans-frame__content-header">
                                <BaseTitle
                                    className="ak-plans-frame__content-title"
                                    text={availableOffers}
                                    headingLevel="h2"
                                    overrideStyle="h4"
                                />
                                {/*{promoCode ? (*/}
                                {/*        <div className='ak-plans-frame__content-promo-code-wrapper'>*/}
                                {/*            <BaseText className='ak-plans-frame__content-promo-code-label'*/}
                                {/*                      wrapper='span' level='4' text={t('Promo code ')}/>*/}
                                {/*            <BaseText className='ak-plans-frame__content-promo-code-value'*/}
                                {/*                      wrapper='span' level='4' text={promoCode}/>*/}
                                {/*            <BaseButton hasIconOnly*/}
                                {/*                        type="tertiary"*/}
                                {/*                        onClick={handleClearPromoCode}*/}
                                {/*                        className='ak-plans-frame__content-promo-code-clear'*/}
                                {/*                        altText={t('Close')}>*/}
                                {/*                <BaseIcon name="close-sign"/>*/}
                                {/*            </BaseButton>*/}
                                {/*        </div>*/}
                                {/*    ) :*/}
                                {/*    (<button className='base-link ak-plans-frame__content-promo-code'*/}
                                {/*             onClick={openModal}>*/}
                                {/*        <BaseText wrapper='span' level='4' text={promoCodeLabel}/>*/}
                                {/*    </button>)}*/}
                            </div>
                            <div className="ak-plans-frame__plans">
                                {offer.offersList
                                    .slice(0, offsetValues[offer.type] + TOTAL_PER_ROW)
                                    .map((offerItem, index) =>
                                        renderCards(offerItem, index, offer.type)
                                    )}
                            </div>
                            <div className="ak-plans-frame__content-footer">
                                {hasMoreOffers[offer.type] && (
                                    <button
                                        aria-label={t('Show More Offers')}
                                        onClick={() => {
                                            const temp = Object.assign({}, offersIter);
                                            temp[offer.type] = offersIter[offer.type] + 1;
                                            setOffersIter(temp);
                                        }}
                                        className="base-link ak-plans-frame__content-show-more"
                                    >
                                        {showMore}
                                    </button>
                                )}
                            </div>
                        </div>
                    </div>
                ),
            };
        });
    };

    const changeTabHandler = (index) => {
        setCurrentIndex(index);
    };

    return (
        <div className="ak-plans-frame">
            {!isEdit && (!validEDC || allOffers.length === 0) ? (
                <Message
                    title={errorTitle}
                    ctaLink={ctaLinkError}
                    ctaText={ctaText}
                    description={description}
                    headingLevel={headingLevel}
                    ctaLinkTarget={ctaLinkTargetError}
                    fileReference={errorIcon}
                />
            ) : (
                <>
                    {/*<BaseModal modalState={modalShow} handleClose={handleModalClose}*/}
                    {/*           id='ak-plans-frame__promo-code' isContentModal={true}*/}
                    {/*           title={promoCodeModalTitle}>*/}
                    {/*    <>*/}
                    {/*        <div className='ak-plans-frame__modal-content'>*/}
                    {/*            <BaseInput*/}
                    {/*                required*/}
                    {/*                type={'text'}*/}
                    {/*                id='promo-code-input'*/}
                    {/*                value={modalPromoCode}*/}
                    {/*                label={promoCodeModalLabel}*/}
                    {/*                placeholder={promoCodeModalPlaceholder}*/}
                    {/*                onChange={val => setModalPromoCode(val)}/>*/}
                    {/*            <BaseButton className='ak-plans-frame__modal-cta' type='primary'*/}
                    {/*                        onClick={handleModalSubmit}>{promoCodeModalCTA}</BaseButton>*/}
                    {/*        </div>*/}
                    {/*        {errorMsg && (<div className='ak-plans-frame__modal-error-msg'>*/}
                    {/*            <BaseText level='4' wrapper='span' text={errorMsg}/>*/}
                    {/*        </div>)}*/}
                    {/*    </>*/}
                    {/*</BaseModal>*/}
                    <div className="ak-plans-frame__header">
                        <div className="ak-plans-frame__header-container">
                            <div className="ak-plans-frame__header-container__left">
                                <BaseTitle
                                    className="ak-plans-frame__title"
                                    text={zip}
                                    headingLevel="h1"
                                />
                                <BaseText
                                    className="ak-plans-frame__subtitle"
                                    text={subtitle}
                                    level="1"
                                />
                            </div>
                            <div className="ak-plans-frame__header-container__right">
                                <BaseText
                                    className="ak-plans-frame__phone-title"
                                    text={phoneTitle}
                                    level="3"
                                />
                                <BaseButton
                                    className="ak-plans-frame__phone-link"
                                    url={`tel:${edc.phoneNumber}`}
                                    type="tertiary"
                                >
                                    <BaseImage
                                        className="ak-plans-frame__phone-link-icon"
                                        url={phoneIcon}
                                    />
                                    <span className="text">{edc.phoneNumber}</span>
                                </BaseButton>
                            </div>
                        </div>
                    </div>
                    {allOffers.length > 0 && (
                        <TabsNavigation
                            className="ak-plans-frame__tabs-navigation"
                            multifieldItems={generateMultifieldItems()}
                            wcSlots={{ tabList: generateTabList() }}
                            passedCurrentIndex={currentIndex}
                            changeTabHandler={changeTabHandler}
                        />
                    )}
                </>
            )}
        </div>
    );
};

PlansFrame.displayName = 'PlansFrame';

export default withBreakpoint(PlansFrame);
