import React from 'react';
import uuidv4 from 'uuid/v4';
import { TabsContent } from './elements';
import cx from 'classnames';
import { wcmService } from '@konrad/reweb-aem';

const TabsNavigation = (props) => {
    const { multifieldItems, wcSlots, isEdit, changeTabHandler, passedCurrentIndex } = props;

    let currentIndex = passedCurrentIndex || 0;

    multifieldItems.forEach((i, index) => {
        i.ref = React.createRef();
        i.id = uuidv4();
        wcSlots.tabList[index].ref = React.createRef();
    });

    return (
        <div
            className={cx(
                { 'ak-tabs-navigation--edit': !wcmService.isDisabled() },
                'ak-tabs-navigation__container'
            )}
            key={uuidv4()}
        >
            <TabsContent
                className={'ak-tabs-navigation'}
                multifieldItems={multifieldItems}
                currentIndex={currentIndex}
                wcSlots={wcSlots}
                isEdit={isEdit}
                changeTabHandler={changeTabHandler}
            />
        </div>
    );
};

TabsNavigation.defaultProps = {
    multifieldItmes: [],
};

TabsNavigation.displayName = 'TabsNavigation';

export default TabsNavigation;
