import React from 'react';
import { linkService } from '@konrad/reweb-aem';
import HeaderBanner from './HeaderBanner';
import ScrollLock from '../../lib/utils/scrollLock';
import { BaseLink, BaseIcon } from '../base';

const CLASSES = {
    OPEN: 'open',
    CLOSED: 'closed',
};

class MobileHeader extends React.PureComponent {
    state = {
        isNavOpen: false,
        expandedCategory: null,
    };

    openMobileNav = (e) => {
        ScrollLock.lockBody();
        this.setState({ isNavOpen: true });
    };

    closeMobileNav = (e) => {
        ScrollLock.unlockBody();
        this.setState({ isNavOpen: false });
    };

    renderSectionLinks = (link, index) => {
        return (
            <li className="ak-header__accordion-list-item" key={index}>
                <BaseLink
                    className="ak-header__accordion-link"
                    url={linkService.get(link.url)}
                    external={link.target === 'external'}
                >
                    {link.text}
                </BaseLink>
            </li>
        );
    };

    //TODO: Export the accordion behavior to a separate component for better performance.
    expandCategory = (categoryId) => {
        this.setState({ expandedCategory: categoryId });
    };

    renderCategories = (category, index) => {
        const { expandedCategory } = this.state;
        const isCatExpanded = expandedCategory === index;
        const handleClick = (e) => {
            if (isCatExpanded) {
                this.setState({ expandedCategory: null });
            } else {
                this.expandCategory(index);
            }
        };
        const contentStyle = {
            height: isCatExpanded ? 8 + category.links.length * 40 + 'px' : '0px',
        };
        return (
            <li className="ak-header__accordion ak-header__mobile-nav-item" key={index}>
                <button className="ak-header__accordion-btn" onClick={handleClick}>
                    <span className="ak-header__accordion-text">{category.categoryTitle}</span>
                    <span
                        className={`ak-header__accordion-icon ${
                            isCatExpanded ? CLASSES.OPEN : CLASSES.CLOSED
                        }`}
                        aria-hidden={true}
                    ></span>
                </button>
                <div className="ak-header__accordion-content" style={contentStyle}>
                    {isCatExpanded && (
                        <ul className="ak-header__accordion-list">
                            {category.links.map(this.renderSectionLinks)}
                        </ul>
                    )}
                </div>
            </li>
        );
    };

    render = () => {
        const {
            ctaUrl,
            ctaText,
            ctaTarget,
            categories,
            logoUrl,
            logoAltText,
            searchResultsPath,
            displayBanner,
            bannerTitle,
            titleHeadingLevel,
            bannerDescription,
            bannerCtaLabel,
            bannerCtaLink,
            bannerCtaLinkTarget,
            isEdit,
            headerHeight,
            handleResize,
        } = this.props;

        const { isNavOpen } = this.state;
        const popUpProps = {};
        if (!isNavOpen) {
            popUpProps['tabIndex'] = '-1';
        }
        return (
            <header className="ak-header">
                <HeaderBanner
                    displayBanner={displayBanner}
                    bannerTitle={bannerTitle}
                    titleHeadingLevel={titleHeadingLevel}
                    bannerDescription={bannerDescription}
                    bannerCtaLabel={bannerCtaLabel}
                    bannerCtaLink={bannerCtaLink}
                    bannerCtaLinkTarget={bannerCtaLinkTarget}
                    isEdit={isEdit}
                    headerHeight={headerHeight}
                    handleResize={handleResize}
                />
                <div className="ak-header__container">
                    <BaseLink className="ak-header__brand" url={linkService.get(logoUrl)}>
                        <span className="sr-only">{logoAltText}</span>
                        <BaseIcon name="brand-logo-color" />
                    </BaseLink>
                    <button
                        aria-label="Open Navigation"
                        className="ak-header__nav-open"
                        aria-expanded="false"
                        aria-controls="header-mobile-nav"
                        onClick={this.openMobileNav}
                    >
                        <BaseIcon name="hamburger-menu" />
                    </button>
                </div>
                <div
                    id="header-mobile-nav"
                    className={`ak-header__mobile ${isNavOpen ? '' : 'hidden'}`}
                    {...popUpProps}
                    role="dialog"
                    aria-modal="true"
                >
                    <div className="ak-header__mobile-content">
                        <BaseLink className="ak-header__brand" url={linkService.get(logoUrl)}>
                            <span className="sr-only">{logoAltText}</span>
                            <BaseIcon name="brand-logo-color" />
                        </BaseLink>
                        <nav className="ak-header__mobile-nav">
                            <ul className="ak-header__mobile-nav-list">
                                {categories.map(this.renderCategories)}
                                <li className="ak-header__mobile-nav-item">
                                    <BaseLink
                                        className="ak-header__search-btn"
                                        url={searchResultsPath}
                                    >
                                        Search
                                    </BaseLink>
                                </li>
                                <li className="ak-header__mobile-nav-item">
                                    <BaseLink
                                        className="ak-header__cta"
                                        url={linkService.get(ctaUrl)}
                                        external={ctaTarget === 'external'}
                                    >
                                        {ctaText}
                                    </BaseLink>
                                </li>
                            </ul>
                        </nav>
                        <button
                            aria-label="Close Navigation"
                            className="ak-header__nav-close"
                            aria-expanded="true"
                            aria-controls="header-mobile-nav"
                            onClick={this.closeMobileNav}
                        >
                            <BaseIcon name="close-sign" />
                        </button>
                    </div>
                </div>
            </header>
        );
    };
}

export default MobileHeader;
