import React from 'react';
import { BaseCarousel, BaseIcon } from '../../../base';
import PropTypes from 'prop-types';

const Carousel = (props) => {
    const { render, showNavigationCarousel } = props;

    let arrowLeft = <BaseIcon className="ak-testimonial__icon" name="arrow-left" />;
    let arrowRight = <BaseIcon className="ak-testimonial__icon" name="arrow-right" />;

    let quotationMarks = <BaseIcon className="ak-testimonial__quotation" name="quotation-marks" />;

    return (
        <div>
            {quotationMarks}
            <BaseCarousel
                className={'ak-testimonial'}
                slidesPerView={1}
                spaceBetween={0}
                render={render}
                prevButton={arrowLeft}
                nextButton={arrowRight}
                showNavigationCarousel={showNavigationCarousel}
            />
        </div>
    );
};

Carousel.displayName = 'Carousel';

Carousel.propTypes = {
    render: PropTypes.func,
    showNavigationCarousel: PropTypes.bool,
};

export default Carousel;
