import React from 'react';
import { BaseImage, BaseTitle, Box } from '../base';

const EdcDetails = (props) => {
    const { logo, name, code } = props;

    return (
        <Box className="ak-edc-details__container" direction="col" alignment="left">
            <BaseImage className="ak-edc-details__logo" url={logo} />
            <BaseTitle headingLevel="h4" text={`Name : ${name}`} />
            <BaseTitle headingLevel="h4" text={`Code : ${code}`} />
        </Box>
    );
};

EdcDetails.displayName = 'EdcDetails';

export default EdcDetails;
