import React from 'react';
import cx from 'classnames';
import PropTypes from 'prop-types';
import { BaseTitle, BaseImage, Box, BaseButton, BaseText } from '../base';
import { linkService } from '@konrad/reweb-aem';

const MediaWithText = (props) => {
    const {
        headingLevel,
        title,
        fileReference,
        imageAltText,
        subtitle,
        description,
        layout,
        ctaLinkTarget,
        ctaLink,
        ctaText,
        subtitleIcon,
    } = props;
    const TagElement = headingLevel;

    return (
        <Box
            direction="row"
            justify="center"
            alignment="center"
            className="ak-media-with-text__container"
        >
            {layout === 'imageLeft' && (
                <BaseImage
                    className="ak-media-with-text__image"
                    url={fileReference}
                    altText={imageAltText}
                />
            )}
            <div
                className={cx(
                    `ak-media-with-text__text`,
                    `${
                        layout === 'imageLeft'
                            ? 'ak-media-with-text__left-margin'
                            : 'ak-media-with-text__right-margin'
                    }`
                )}
            >
                <div className="ak-media-with-text__subtitleWapper">
                    <BaseText type="sub" text={subtitle} className="ak-media-with-text__subtitle" />
                    {subtitleIcon && (
                        <BaseImage
                            className="ak-media-with-text__subtitleIcon"
                            url={subtitleIcon}
                        />
                    )}
                </div>
                <BaseTitle
                    text={title}
                    headingLevel={TagElement}
                    className="ak-media-with-text__title"
                />
                <BaseText
                    wrapper="div"
                    level="2"
                    className="ak-media-with-text__description"
                    text={description}
                    isRichText
                />
                {ctaLink && (
                    <BaseButton
                        url={linkService.get(ctaLink)}
                        type="secondary"
                        altText={ctaText}
                        external={ctaLinkTarget === 'external'}
                        children={ctaText}
                        className="ak-media-with-text__button"
                    />
                )}
            </div>
            {layout === 'imageRight' && (
                <BaseImage
                    className="ak-media-with-text__image"
                    url={fileReference}
                    altText={imageAltText}
                />
            )}
        </Box>
    );
};

MediaWithText.displayName = 'MediaWithText';

MediaWithText.defaultProps = {
    headingLevel: 'h1',
};

MediaWithText.propTypes = {
    headingLevel: PropTypes.string,
    title: PropTypes.string,
    fileReference: PropTypes.string,
    imageAltText: PropTypes.string,
    subtitle: PropTypes.string,
    description: PropTypes.string,
    layout: PropTypes.string,
    ctaLinkTarget: PropTypes.string,
    ctaLink: PropTypes.string,
    ctaText: PropTypes.string,
};

export default MediaWithText;
