import React, { useState } from 'react';
import cx from 'classnames';

const TabsHeader = (props) => {
    const { className, multifieldItems, activeIndex, changeIndex, changeTabHandler } = props;

    const [focusIndex, setFocusIndex] = useState(activeIndex);

    const handleClick = (event, index) => {
        changeIndex(event, index);
        setFocusIndex(index);
    };

    const onKeyDown = (e) => {
        if (e.keyCode === 39 || e.keyCode === 37) {
            e.stopPropagation();
            e.preventDefault();
            handleTabMove(e, e.keyCode === 39, true);
        } else if (e.keyCode === 13) {
            handleToEnter(e);
        }
    };

    const handleToEnter = (e) => {
        let item = multifieldItems[focusIndex];
        document.getElementById(item.id).click();
    };

    const handleTabMove = (e, moveRight) => {
        let currentIndex = moveRight ? focusIndex + 1 : focusIndex - 1;

        //Circular tabs movement
        if (currentIndex < 0) {
            currentIndex = multifieldItems.length - 1;
        } else if (currentIndex >= multifieldItems.length) {
            currentIndex = 0;
        }

        if (currentIndex < multifieldItems.length && currentIndex >= 0) {
            let item = multifieldItems[currentIndex];
            document.getElementById(item.id).focus();
            setFocusIndex(currentIndex);
        }
    };

    return (
        <div className={`${className}__header-container`}>
            <div className={`${className}__line`} />
            <ul
                className={`${className}__header`}
                role={'tablist'}
                onKeyDown={(event) => onKeyDown(event)}
            >
                {multifieldItems &&
                    multifieldItems.map((tab, i) => {
                        const isActive = activeIndex === i;
                        return (
                            <li key={tab.id} role={'presentation'}>
                                <button
                                    id={tab.id}
                                    role={'tab'}
                                    className={cx('ak-tabs-navigation__tab-button', {
                                        'ak-tabs-navigation__tab-button--active': isActive,
                                    })}
                                    aria-selected={activeIndex === i}
                                    tabIndex={activeIndex === i ? '0' : '-1'}
                                    data-index-button={i}
                                    onClick={(e) => {
                                        if (changeTabHandler) {
                                            changeTabHandler(i);
                                        }
                                        handleClick(e, i);
                                    }}
                                >
                                    {tab.title}
                                </button>
                            </li>
                        );
                    })}
            </ul>
        </div>
    );
};

TabsHeader.displayName = 'TabsHeader';

export default TabsHeader;
