import React, { useState, useRef } from 'react';
import { linkService, t } from '@konrad/reweb-aem';
import {
    BaseText,
    BaseTitle,
    BaseInput,
    BaseButton,
    BaseSelect,
    BaseTextArea,
    BaseFileUpload,
} from '../../../base';
import {
    STATES,
    HEAR_ABOUT_US,
    METHOD_OF_CONTACT,
    UTILITY,
    APPROXIMATE_BILL,
    FACILITY_TYPE,
    BUSINESS_TYPE,
    DECISION_TIMEFRAME,
    CAPTCHA_VALUE,
} from '../../../../lib/constants';
import akronAPI from '../../../../api';
import useForm from './useForm';
import validate from './formValidations';
import ReCAPTCHA from 'react-google-recaptcha';

const Form = (props) => {
    const {
        stepOneLabel,
        stepOneTitle,
        firstNameLabel,
        firstNamePlaceholder,
        lastNameLabel,
        lastNamePlaceholder,
        emailAddressLabel,
        emailAddressPlaceholder,
        phoneNumberLabel,
        phoneNumberPlaceholder,
        jobTitleLabel,
        jobTitlePlaceholder,
        companyLabel,
        companyPlaceholder,
        mailingAddressLabel,
        mailingAddressPlaceholder,
        cityLabel,
        cityPlaceholder,
        stateLabel,
        statePlaceholder,
        zipCodeLabel,
        zipCodePlaceholder,
        preferredContactLabel,
        preferredContactPlaceholder,
        stepTwoLabel,
        stepTwoTitle,
        utilityLabel,
        utilityPlaceholder,
        approximateMonthlyBillLabel,
        approximateMonthlyBillPlaceholder,
        facilityTypeLabel,
        facilityTypePlaceholder,
        businessTypeLabel,
        businessTypePlaceholder,
        decisionTimeframeLabel,
        decisionTimeframePlaceholder,
        uploadRecentBillLabel,
        stepThreeLabel,
        stepThreeTitle,
        howDidYouHearLabel,
        howDidYouHearPlaceholder,
        commentsLabel,
        commentsPlaceholder,
        ctaLabel,
        ctaLink,
        ctaLinkTarget,
        captchaToken,
    } = props;

    const firstNameId = 'firstName';
    const lastNameId = 'lastName';
    const emailAddressId = 'emailAddress';
    const phoneNumberId = 'phoneNumber';
    const jobTitleId = 'jobTitle';
    const companyId = 'company';
    const mailingAddressId = 'mailingAddress';
    const cityId = 'city';
    const stateId = 'state';
    const zipCodeId = 'zipCode';
    const preferredContactId = 'preferredContact';
    const utilityId = 'utility';
    const approximateMonthlyBillId = 'approximateMonthlyBill';
    const facilityTypeId = 'facilityType';
    const businessTypeId = 'businessType';
    const decisionTimeframeId = 'decisionTimeframe';
    const uploadRecentBillId = 'uploadRecentBill';
    const howDidYouHearId = 'howDidYouHear';
    const commentsId = 'comments';

    const ref = {};
    const [sentFormErrorMessage, setSentFormErrorMessage] = useState('');
    const [loading, setLoading] = useState(false);
    const { values, handleChange, handleSubmit, errors, setValues } = useForm(
        handleForm,
        validate,
        setSentFormErrorMessage,
        ref
    );

    //Submit function
    function handleForm() {
        let json;
        let requestAQuoteForm = {};
        const data = new FormData();

        if (Object.entries(errors).length === 0 && errors.constructor === Object) {
            requestAQuoteForm = {
                first_name: values.firstName,
                last_name: values.lastName,
                phone_number: values.phoneNumber,
                email_address: values.emailAddress,
                company: values.company,
                job_title: values.jobTitle,
                mailing_address: values.mailingAddress,
                city: values.city,
                state: values.state,
                zip_code: values.zipCode,
                preferred_contact: values.preferredContact,
                utility: values.utility,
                approximate_monthly_bill: values.approximateMonthlyBill,
                facility_type: values.facilityType,
                business_type: values.businessType,
                decision_timeframe: values.decisionTimeframe,
                how_did_you_hear: values.howDidYouHear,
                questions_comments: values.comments,
                [CAPTCHA_VALUE]: values.captcha,
            };

            json = JSON.stringify(requestAQuoteForm, (k, v) => (v === undefined ? '' : v));
            data.set('data', json);
            values.uploadRecentBill && data.append('recent_bill', values.uploadRecentBill[0]);
            setLoading(true);
            akronAPI.requestQuoteForm(data).then(
                (result) => redirect(result),
                (error) => showErrorMessage(error)
            );
        }
    }

    const redirect = (result) => {
        setLoading(false);
        if (ctaLinkTarget === 'internal') {
            window.location.href = linkService.get(ctaLink);
        } else {
            window.open(linkService.get(ctaLink), '_blank');
        }
    };

    const showErrorMessage = (error) => {
        setLoading(false);
        setValues({ ...values, captcha: '' });
        ref.recaptchaRef && ref.recaptchaRef.current.reset();
        setSentFormErrorMessage(t('Email could not be sent'));
    };

    return (
        <form noValidate onSubmit={handleSubmit} className={'ak-request-a-quote__form'}>
            <fieldset className={'ak-request-a-quote__fieldset'}>
                <div className={'ak-request-a-quote__input ak-request-a-quote__input--full'}>
                    <BaseText
                        className={'ak-request-a-quote__subtitle'}
                        level={'4'}
                        wrapper={'div'}
                        text={t(stepOneLabel)}
                    />
                </div>
                <div
                    className={
                        'ak-request-a-quote__input ak-request-a-quote__input--full ak-request-a-quote__title'
                    }
                >
                    <BaseTitle headingLevel="h2" overrideStyle={'h4'} text={t(stepOneTitle)} />
                </div>
                <div className={'ak-request-a-quote__row'}>
                    <div className={'ak-request-a-quote__input'}>
                        <BaseInput
                            id={firstNameId}
                            required
                            ref={(ref.firstName = React.createRef())}
                            type={'text'}
                            value={values.firstName || ''}
                            label={firstNameLabel}
                            error={errors.firstName}
                            placeholder={firstNamePlaceholder}
                            onChange={(val, e) => handleChange(val, e)}
                        />
                        {errors.firstName && (
                            <BaseText
                                className="ak-request-a-quote__error-message"
                                text={errors.firstName}
                                level={'3'}
                            />
                        )}
                    </div>
                    <div className={'ak-request-a-quote__input'}>
                        <BaseInput
                            id={lastNameId}
                            required
                            ref={(ref.lastName = React.createRef())}
                            type={'text'}
                            value={values.lastName || ''}
                            label={lastNameLabel}
                            error={errors.lastName}
                            placeholder={lastNamePlaceholder}
                            onChange={(val, e) => handleChange(val, e)}
                        />
                        {errors.lastName && (
                            <BaseText
                                className="ak-request-a-quote__error-message"
                                text={errors.lastName}
                                level={'3'}
                            />
                        )}
                    </div>
                </div>
                <div className={'ak-request-a-quote__row'}>
                    <div className={'ak-request-a-quote__input'}>
                        <BaseInput
                            id={emailAddressId}
                            required
                            ref={(ref.emailAddress = React.createRef())}
                            type={'email'}
                            name="emailAddress"
                            value={values.emailAddress || ''}
                            label={emailAddressLabel}
                            onChange={(val, e) => handleChange(val, e)}
                            error={errors.emailAddress}
                            placeholder={emailAddressPlaceholder}
                        />
                        {errors.emailAddress && (
                            <BaseText
                                className="ak-request-a-quote__error-message"
                                text={errors.emailAddress}
                                level={'3'}
                            />
                        )}
                    </div>
                    <div className={'ak-request-a-quote__input'}>
                        <BaseInput
                            id={phoneNumberId}
                            required
                            ref={(ref.phoneNumber = React.createRef())}
                            maskChar={''}
                            name="phoneNumber"
                            value={values.phoneNumber || ''}
                            mask={'(999) 999-9999'}
                            label={phoneNumberLabel}
                            placeholder={phoneNumberPlaceholder}
                            onChange={(val, e) => handleChange(val, e)}
                            error={errors.phoneNumber}
                        />
                        {errors.phoneNumber && (
                            <BaseText
                                className="ak-request-a-quote__error-message"
                                text={errors.phoneNumber}
                                level={'3'}
                            />
                        )}
                    </div>
                </div>
                <div className={'ak-request-a-quote__row'}>
                    <div className={'ak-request-a-quote__input'}>
                        <BaseInput
                            id={jobTitleId}
                            required
                            ref={(ref.jobTitle = React.createRef())}
                            type="text"
                            name="jobTitle"
                            value={values.jobTitle || ''}
                            label={jobTitleLabel}
                            onChange={(val, e) => handleChange(val, e)}
                            error={errors.jobTitle}
                            placeholder={jobTitlePlaceholder}
                        />
                        {errors.jobTitle && (
                            <BaseText
                                className="ak-request-a-quote__error-message"
                                text={errors.jobTitle}
                                level={'3'}
                            />
                        )}
                    </div>
                    <div className={'ak-request-a-quote__input'}>
                        <BaseInput
                            id={companyId}
                            required
                            ref={(ref.company = React.createRef())}
                            type="text"
                            value={values.company || ''}
                            label={companyLabel}
                            placeholder={companyPlaceholder}
                            onChange={(val, e) => handleChange(val, e)}
                            error={errors.company}
                        />
                        {errors.company && (
                            <BaseText
                                className="ak-request-a-quote__error-message"
                                text={errors.company}
                                level={'3'}
                            />
                        )}
                    </div>
                </div>
                <div className={'ak-request-a-quote__row'}>
                    <div className={'ak-request-a-quote__input'}>
                        <BaseInput
                            id={mailingAddressId}
                            ref={(ref.mailingAddress = React.createRef())}
                            type={'email'}
                            value={values.mailingAddress || ''}
                            label={mailingAddressLabel}
                            onChange={(val, e) => handleChange(val, e)}
                            placeholder={mailingAddressPlaceholder}
                        />
                    </div>
                    <div className={'ak-request-a-quote__input'}>
                        <BaseInput
                            id={cityId}
                            ref={(ref.city = React.createRef())}
                            type="text"
                            value={values.city || ''}
                            label={cityLabel}
                            placeholder={cityPlaceholder}
                            onChange={(val, e) => handleChange(val, e)}
                        />
                    </div>
                </div>
                <div className={'ak-request-a-quote__row'}>
                    <div className={'ak-request-a-quote__input'}>
                        <BaseSelect
                            ref={(ref.state = React.createRef())}
                            id={stateId}
                            value={values.state || ''}
                            name="state"
                            label={stateLabel}
                            ariaLabel={`${stateId}-label`}
                            placeholder={statePlaceholder}
                            onChange={(selectedOption) =>
                                setValues({ ...values, state: selectedOption })
                            }
                            className={'ak-request-a-quote__select'}
                            ariaLabelList={`${stateId}-label`}
                            options={STATES}
                        />
                    </div>
                    <div className={'ak-request-a-quote__input'}>
                        <BaseInput
                            id={zipCodeId}
                            required
                            ref={(ref.zipCode = React.createRef())}
                            mask="99999"
                            type="text"
                            name="zipCode"
                            value={values.zipCode || ''}
                            label={zipCodeLabel}
                            placeholder={zipCodePlaceholder}
                            onChange={(val, e) => handleChange(val, e)}
                            error={errors.zipCode}
                        />
                        {errors.zipCode && (
                            <BaseText
                                className="ak-request-a-quote__error-message"
                                text={errors.zipCode}
                                level={'3'}
                            />
                        )}
                    </div>
                </div>
                <div className={'ak-request-a-quote__row'}>
                    <div className={'ak-request-a-quote__input'}>
                        <BaseSelect
                            ref={(ref.preferredContact = React.createRef())}
                            id={preferredContactId}
                            name="preferredContact"
                            label={preferredContactLabel}
                            ariaLabel={`${preferredContactId}-label`}
                            placeholder={preferredContactPlaceholder}
                            onChange={(selectedOption) =>
                                setValues({ ...values, preferredContact: selectedOption })
                            }
                            className={'ak-request-a-quote__select'}
                            ariaLabelList={`${preferredContactId}-label`}
                            options={METHOD_OF_CONTACT}
                        />
                    </div>
                </div>
            </fieldset>
            <fieldset className={'ak-request-a-quote__fieldset'}>
                <div className={'ak-request-a-quote__input ak-request-a-quote__input--full'}>
                    <BaseText
                        className={'ak-request-a-quote__subtitle'}
                        level={'4'}
                        wrapper={'div'}
                        text={t(stepTwoLabel)}
                    />
                </div>
                <div
                    className={
                        'ak-request-a-quote__input ak-request-a-quote__input--full ak-request-a-quote__title'
                    }
                >
                    <BaseTitle headingLevel="h2" overrideStyle={'h4'} text={t(stepTwoTitle)} />
                </div>
                <div className={'ak-request-a-quote__row'}>
                    <div className={'ak-request-a-quote__input'}>
                        <BaseSelect
                            ref={(ref.utility = React.createRef())}
                            id={utilityId}
                            required
                            label={utilityLabel}
                            error={errors.utility}
                            ariaLabel={`${utilityId}-label`}
                            placeholder={utilityPlaceholder}
                            onChange={(selectedOption) =>
                                setValues({ ...values, utility: selectedOption })
                            }
                            className={'ak-request-a-quote__select'}
                            ariaLabelList={`${utilityId}-label`}
                            options={UTILITY}
                        />
                        {errors.utility && (
                            <BaseText
                                className="ak-request-a-quote__error-message"
                                text={errors.utility}
                                level={'3'}
                            />
                        )}
                    </div>
                    <div className={'ak-request-a-quote__input'}>
                        <BaseSelect
                            ref={(ref.approximateMonthlyBill = React.createRef())}
                            id={approximateMonthlyBillId}
                            required
                            label={approximateMonthlyBillLabel}
                            error={errors.approximateMonthlyBill}
                            ariaLabel={`${approximateMonthlyBillId}-label`}
                            placeholder={approximateMonthlyBillPlaceholder}
                            onChange={(selectedOption) =>
                                setValues({ ...values, approximateMonthlyBill: selectedOption })
                            }
                            className={'ak-request-a-quote__select'}
                            ariaLabelList={`${approximateMonthlyBillId}-label`}
                            options={APPROXIMATE_BILL}
                        />
                        {errors.approximateMonthlyBill && (
                            <BaseText
                                className="ak-request-a-quote__error-message"
                                text={errors.approximateMonthlyBill}
                                level={'3'}
                            />
                        )}
                    </div>
                </div>
                <div className={'ak-request-a-quote__row'}>
                    <div className={'ak-request-a-quote__input'}>
                        <BaseSelect
                            ref={(ref.facilityType = React.createRef())}
                            id={facilityTypeId}
                            label={facilityTypeLabel}
                            ariaLabel={`${facilityTypeId}-label`}
                            placeholder={facilityTypePlaceholder}
                            onChange={(selectedOption) =>
                                setValues({ ...values, facilityType: selectedOption })
                            }
                            className={'ak-request-a-quote__select'}
                            ariaLabelList={`${facilityTypeId}-label`}
                            options={FACILITY_TYPE}
                        />
                    </div>
                    <div className={'ak-request-a-quote__input'}>
                        <BaseSelect
                            ref={(ref.businessType = React.createRef())}
                            id={businessTypeId}
                            label={businessTypeLabel}
                            ariaLabel={`${businessTypeId}-label`}
                            placeholder={businessTypePlaceholder}
                            onChange={(selectedOption) =>
                                setValues({ ...values, businessType: selectedOption })
                            }
                            className={'ak-request-a-quote__select'}
                            ariaLabelList={`${businessTypeId}-label`}
                            options={BUSINESS_TYPE}
                        />
                    </div>
                </div>
                <div className={'ak-request-a-quote__row'}>
                    <div className={'ak-request-a-quote__input'}>
                        <BaseSelect
                            ref={(ref.decisionTimeframe = React.createRef())}
                            id={decisionTimeframeId}
                            label={decisionTimeframeLabel}
                            ariaLabel={`${decisionTimeframeId}-label`}
                            placeholder={decisionTimeframePlaceholder}
                            onChange={(selectedOption) =>
                                setValues({ ...values, decisionTimeframe: selectedOption })
                            }
                            className={'ak-request-a-quote__select'}
                            ariaLabelList={`${decisionTimeframeId}-label`}
                            options={DECISION_TIMEFRAME}
                        />
                    </div>
                </div>
                <div className={'ak-request-a-quote__row'}>
                    <div className={'ak-request-a-quote__input ak-request-a-quote__input--full'}>
                        <BaseFileUpload
                            id={uploadRecentBillId}
                            ref={(ref.uploadRecentBill = React.createRef())}
                            name={'uploadRecentBill'}
                            onChange={(val, e) => handleChange(val, e)}
                            label={uploadRecentBillLabel}
                            accept=".pdf, .png, .JPEG"
                        />
                    </div>
                </div>
            </fieldset>
            <fieldset className={'ak-request-a-quote__fieldset'}>
                <div className={'ak-request-a-quote__input ak-request-a-quote__input--full'}>
                    <BaseText
                        className={'ak-request-a-quote__subtitle'}
                        level={'4'}
                        wrapper={'div'}
                        text={t(stepThreeLabel)}
                    />
                </div>
                <div
                    className={
                        'ak-request-a-quote__input ak-request-a-quote__input--full ak-request-a-quote__title'
                    }
                >
                    <BaseTitle headingLevel="h2" overrideStyle={'h4'} text={t(stepThreeTitle)} />
                </div>
                <div className={'ak-request-a-quote__row'}>
                    <div className={'ak-request-a-quote__input'}>
                        <BaseSelect
                            id={howDidYouHearId}
                            label={howDidYouHearLabel}
                            ariaLabel={`${howDidYouHearId}-label`}
                            placeholder={howDidYouHearPlaceholder}
                            onChange={(selectedOption) =>
                                setValues({ ...values, howDidYouHear: selectedOption })
                            }
                            className={'ak-request-a-quote__select'}
                            ariaLabelList={`${howDidYouHearId}-label`}
                            options={HEAR_ABOUT_US}
                        />
                    </div>
                </div>
                <div className={'ak-request-a-quote__row'}>
                    <div className={'ak-request-a-quote__input ak-request-a-quote__input--full'}>
                        <BaseTextArea
                            id={commentsId}
                            name="comments"
                            value={values.comments || ''}
                            label={commentsLabel}
                            onChange={(val, e) => handleChange(val, e)}
                            placeholder={commentsPlaceholder}
                        />
                    </div>
                </div>
                <div className={'ak-request-a-quote__row'}>
                    <div
                        className={
                            'ak-request-a-quote__input ak-request-a-quote__input--full ak-request-a-quote__captcha'
                        }
                    >
                        <ReCAPTCHA
                            ref={(ref.recaptchaRef = useRef(null))}
                            sitekey={captchaToken}
                            onChange={(val) => setValues({ ...values, captcha: val })}
                        />
                        {errors.captchaErrorMessage && (
                            <BaseText
                                id="captcha-error"
                                className="ak-request-a-quote__error-message"
                                text={errors.captchaErrorMessage}
                                level={'3'}
                            />
                        )}
                    </div>
                </div>
            </fieldset>
            <div className={'ak-request-a-quote__row ak-request-a-quote__row--aligned-right'}>
                <BaseButton
                    className={'ak-request-a-quote__submit'}
                    loading={loading}
                    altText={t('Submit Request a quote Form')}
                >
                    {ctaLabel}
                </BaseButton>
            </div>
            {sentFormErrorMessage && (
                <BaseText
                    id={`submit-error`}
                    className="ak-request-a-quote__invalid"
                    text={sentFormErrorMessage}
                    level={'3'}
                />
            )}
        </form>
    );
};

Form.displayName = 'Form';

export default Form;
