import React from 'react';
import PropTypes from 'prop-types';
import { BaseImage, BaseText } from '../index';
import cx from 'classnames';

const BaseInstructions = (props) => {
    const {
        className,
        fileReference,
        explanatoryIconAlternativeText,
        explanatoryTitle,
        description,
    } = props;

    return (
        <div className={cx('base-instructions', className)}>
            {fileReference && (
                <BaseImage
                    className="base-instructions__image"
                    url={fileReference}
                    alt={explanatoryIconAlternativeText}
                />
            )}
            <BaseText
                className="base-instructions__title"
                level={'3'}
                wrapper={'div'}
                text={explanatoryTitle}
            />
            <BaseText level={'4'} wrapper={'div'} text={description} />
        </div>
    );
};

BaseInstructions.displayName = 'BaseInstructions';

BaseInstructions.propTypes = {
    className: PropTypes.string,
    fileReference: PropTypes.string,
    explanatoryIconAlternativeText: PropTypes.string,
    explanatoryTitle: PropTypes.string,
    description: PropTypes.string,
};

export default BaseInstructions;
