import React from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';
import { BACKGROUND_TYPES } from '../../../lib/constants';

const BaseContainer = (props) => {
    const { id, bgType, bgColor, className, bgImageUrl, children } = props;

    const bgStyles = {};

    if (bgType === 'color') {
        bgStyles.backgroundColor = bgColor;
    }

    if (bgType === 'image') {
        bgStyles.backgroundImage = bgImageUrl ? `url(${encodeURI(bgImageUrl)})` : undefined;
        bgStyles.backgroundRepeat = 'no-repeat';
        bgStyles.backgroundPosition = 'center';
        bgStyles.backgroundSize = 'cover';
    }

    return (
        <div id={id} style={bgType !== 'empty' ? bgStyles : undefined} className="base-container">
            <div className={cx('base-container__content', className)}>{children}</div>
        </div>
    );
};

BaseContainer.defaultProps = {
    bgType: 'empty',
};

BaseContainer.propTypes = {
    id: PropTypes.string,
    bgColor: PropTypes.string,
    className: PropTypes.string,
    bgImageUrl: PropTypes.string,
    bgType: PropTypes.oneOf(BACKGROUND_TYPES),
};

export default BaseContainer;
