import React from 'react';
import cx from 'classnames';
import { BaseImage, BaseTitle, BaseText, BaseButton } from '../base';
import { linkService, wcmService } from '@konrad/reweb-aem';
import PropTypes from 'prop-types';
import { HEADING_LEVELS } from '../../lib/constants';

const Message = (props) => {
    const {
        title,
        ctaLink,
        ctaText,
        description,
        headingLevel,
        ctaLinkTarget,
        fileReference,
    } = props;

    return (
        <div className="ak-message">
            <div
                className={cx('ak-message__container', {
                    'ak-message__container--publish': wcmService.isDisabled(),
                })}
            >
                {fileReference && <BaseImage className={'ak-message__icon'} url={fileReference} />}
                <BaseTitle
                    className={'ak-message__title'}
                    text={title}
                    overrideStyle={'h1'}
                    headingLevel={headingLevel}
                    alignment={'center'}
                />
                <BaseText className={'ak-message__description'} text={description} />
                <BaseButton
                    className={'ak-message__button'}
                    url={linkService.get(ctaLink)}
                    target={ctaLinkTarget}
                >
                    {ctaText}
                </BaseButton>
            </div>
        </div>
    );
};

Message.displayName = 'Message';

Message.defaultProps = {
    title: PropTypes.string,
    ctaLink: PropTypes.string,
    ctaText: PropTypes.string,
    description: PropTypes.string,
    headingLevel: PropTypes.oneOf(HEADING_LEVELS),
    ctaLinkTarget: PropTypes.string,
    fileReference: PropTypes.string,
};

export default Message;
