import React from 'react';
import cx from 'classnames';
import PropTypes from 'prop-types';
import { ALIGNMENT_OPTIONS, HEADING_LEVELS } from '../../../lib/constants';

const BaseTitle = (props) => {
    const { id, headingLevel, text, alignment, className, children } = props;

    const TagElement = headingLevel;
    const style = props.overrideStyle || (headingLevel === 'div' ? 'h1' : headingLevel);

    return (
        <TagElement
            id={id}
            className={cx(
                'base-title',
                `base-title--alignment-${alignment}`,
                `base-title--type-${style}`,
                className
            )}
        >
            {text || children}
        </TagElement>
    );
};

BaseTitle.displayName = 'BaseTitle';

BaseTitle.defaultProps = {
    alignment: 'left',
    headingLevel: 'h1',
};

BaseTitle.propTypes = {
    id: PropTypes.string,
    className: PropTypes.string,
    headingLevel: PropTypes.oneOf(HEADING_LEVELS),
    alignment: PropTypes.oneOf(ALIGNMENT_OPTIONS),
    overrideStyle: PropTypes.oneOf(HEADING_LEVELS),
    text: PropTypes.string,
};

export default BaseTitle;
