import React, { useState } from 'react';
import cx from 'classnames';
import { BaseLink, BaseText, BaseTitle } from '../base';
import { linkService, wcmService } from '@konrad/reweb-aem';
import { ZipCodeForm } from '../index';
import withBreakpoint from '../../lib/utils/withBreakpoint';

const Hero = (props) => {
    const {
        multifieldBottomImages,
        backgroundImage,
        blackTitle,
        blueTitle,
        secondBlackTitle,
        subtitle,
        description,
        descriptionLink,
        descriptionLinkUrl,
        descriptionLinkTarget,
        ctaLink,
        ctaText,
        placeholder,
        modalTitle,
        modalDescription,
        noEDCLink,
        isDesktop,
    } = props;

    const [randomNumber, setRandomNumber] = useState(
        multifieldBottomImages && multifieldBottomImages.length
            ? Math.floor(Math.random() * multifieldBottomImages.length)
            : null
    );
    const randomImage = randomNumber !== null ? multifieldBottomImages[randomNumber].image : '';
    const randomMobileImage =
        randomNumber !== null ? multifieldBottomImages[randomNumber].mobileImage : '';

    const topBlockStyles = {
        backgroundImage: `url(${encodeURI(backgroundImage)})`,
    };
    const bottomBlockStyles = {
        backgroundImage: `url(${encodeURI(randomImage)})`,
    };

    const mobileBottomBlockStyles = {
        backgroundImage: `url(${encodeURI(randomMobileImage)})`,
    };

    return (
        <div className="ak-hero" style={topBlockStyles}>
            <div
                className={cx('ak-hero__container', {
                    'ak-hero__container--publish': wcmService.isDisabled(),
                })}
            >
                <div className="ak-hero__container-top">
                    <BaseTitle className="ak-hero__title" headingLevel="h1">
                        <span className="first">{`${blackTitle} `}</span>
                        {blueTitle && <span className="highlight second">{`${blueTitle} `}</span>}
                        {secondBlackTitle && <span className="third">{secondBlackTitle}</span>}
                    </BaseTitle>
                    <BaseText className="ak-hero__subtitle" text={subtitle} level="1" />
                </div>
                <div className="ak-hero__bottom-image-container">
                    {isDesktop || randomMobileImage === '' || randomMobileImage === undefined ? (
                        <div className="ak-hero__bottom-image" style={bottomBlockStyles}></div>
                    ) : (
                        <div
                            className="ak-hero__bottom-image"
                            style={mobileBottomBlockStyles}
                        ></div>
                    )}
                </div>
                <div className="ak-hero__container-bottom">
                    <ZipCodeForm
                        noEDCLink={noEDCLink}
                        url={ctaLink}
                        search={placeholder}
                        ctaText={ctaText}
                        modalDescription={modalDescription}
                        modalTitle={modalTitle}
                    />
                    <BaseText className="ak-hero__description" level="3">
                        <span className="ak-hero__description-text">{description}</span>
                        <BaseLink
                            className="ak-hero__description-link"
                            url={linkService.get(descriptionLinkUrl)}
                            external={descriptionLinkTarget === 'external'}
                        >
                            {descriptionLink}
                        </BaseLink>
                    </BaseText>
                </div>
            </div>
        </div>
    );
};

Hero.displayName = 'Hero';

export default withBreakpoint(Hero);
