import React from 'react';
import { BaseImage } from '../base';

const Logos = (props) => {
    const { multifieldImages } = props;

    const renderLogos = ({ logo, altText }, index) => (
        <div className="ak-logos__logo-wrapper" key={index}>
            <BaseImage className="ak-logos__logo" url={logo} altText={altText} />
        </div>
    );

    return (
        <div className="ak-logos">
            <div className="ak-logos__container">{multifieldImages.map(renderLogos)}</div>
        </div>
    );
};

Logos.displayName = 'Logos';

export default Logos;
