import React from 'react';
import cx from 'classnames';
import { ZipCodeForm } from '../index';
import { BaseTitle, BaseText } from '../base';

const FindYourPlan = (props) => {
    const {
        url,
        title,
        search,
        ctaText,
        noEDCLink,
        modalTitle,
        bgImageUrl,
        description,
        headingLevel,
        modalDescription,
    } = props;

    const bgStyle = {};

    if (bgImageUrl) {
        bgStyle.backgroundImage = `url(${encodeURI(bgImageUrl)})`;
    }

    return (
        <div
            style={bgStyle}
            className={cx('ak-find-your-plan', { 'ak-find-your-plan--has-background': bgImageUrl })}
        >
            <BaseTitle
                className="ak-find-your-plan__title"
                text={title}
                headingLevel={headingLevel}
                overrideStyle="h3"
            />
            <BaseText
                className="ak-find-your-plan__description"
                isRichText
                text={description}
                level="3"
            />
            <ZipCodeForm
                noEDCLink={noEDCLink}
                url={url}
                search={search}
                ctaText={ctaText}
                modalDescription={modalDescription}
                modalTitle={modalTitle}
            />
        </div>
    );
};

FindYourPlan.displayName = 'FindYourPlan';

export default FindYourPlan;
