import React, { useEffect } from 'react';
import { BaseIcon, BaseText, BaseTitle } from '../../../base';
import { t } from '@konrad/reweb-aem';
import PropTypes from 'prop-types';
import { OFFER_TYPE_LABEL } from '../../../../lib/constants';

const Details = (props) => {
    const {
        planName,
        planLength,
        detailsPriceData,
        titleState,
        changedStep,
        setChangedStep,
        handleDrawerOpen,
        isBundle,
    } = props;

    //If the user changed of step in the form, the scroll should go to the top
    if (changedStep) {
        const element = document.getElementById(titleState);
        element && element.scrollIntoView(true);
        setChangedStep(false);
    }

    useEffect(() => {
        if (changedStep) {
            const element = document.getElementById(titleState);
            element && element.scrollIntoView(true);
            setChangedStep(false);
        }
    }, [changedStep, titleState, setChangedStep]);

    return (
        <div className="ak-enrollment-frame__details-header">
            <div className="ak-enrollment-frame__details-container">
                <div className="ak-enrollment-frame__left-content">
                    <div className="ak-enrollment-frame__col">
                        <BaseTitle
                            id={titleState}
                            className="ak-enrollment-frame__title"
                            overrideStyle={'h3'}
                            headingLevel={'h1'}
                        >
                            {planName}
                        </BaseTitle>
                        <button
                            aria-label={t('Open the more details drawer')}
                            onClick={handleDrawerOpen}
                            className="ak-enrollment-frame__details"
                        >
                            <BaseText
                                className="ak-enrollment-frame__details-text"
                                level="3"
                                text={t('View Details')}
                                wrapper="span"
                            />
                            <BaseIcon name="info-grey" className="ak-enrollment-frame__info-icon" />
                        </button>
                    </div>
                </div>
                <div className="ak-enrollment-frame__right-content">
                    <div className={'ak-enrollment-frame__col'}>
                        <BaseTitle
                            className="ak-enrollment-frame__field-value"
                            overrideStyle={'h4'}
                            headingLevel={'div'}
                        >
                            {planLength}
                        </BaseTitle>
                        <BaseText className="ak-enrollment-frame__field-title" level={'4'}>
                            {t('Term')}
                        </BaseText>
                    </div>
                    {Object.keys(detailsPriceData).map((key, index) => (
                        <div key={index} className={'ak-enrollment-frame__col'}>
                            <BaseTitle
                                className="ak-enrollment-frame__field-value"
                                overrideStyle={'h4'}
                                headingLevel={'div'}
                            >
                                {detailsPriceData[key].planAmount}
                            </BaseTitle>
                            <BaseText className="ak-enrollment-frame__field-title" level={'4'}>
                                {isBundle
                                    ? OFFER_TYPE_LABEL[key]
                                    : detailsPriceData[key].planAmountTerm}
                            </BaseText>
                        </div>
                    ))}
                </div>
            </div>
        </div>
    );
};

Details.displayName = 'DetailsHeader';

Details.propTypes = {
    planName: PropTypes.string,
    planLength: PropTypes.string,
    planAmount: PropTypes.string,
    planAmountTerm: PropTypes.string,
    titleState: PropTypes.string,
    changedStep: PropTypes.bool,
    setChangedStep: PropTypes.func,
    handleDrawerOpen: PropTypes.func,
};

export default Details;
