import React from 'react';
import { BaseInstructions } from '../base';
import { Form } from './elements';
import { t } from '@konrad/reweb-aem';
import PropTypes from 'prop-types';
import { HEADING_LEVELS } from '../../lib/constants';

const ContactUs = (props) => {
    const {
        headingLevel,
        firstNameLabel,
        firstNamePlaceholder,
        lastNameLabel,
        lastNamePlaceholder,
        phoneNumberLabel,
        phoneNumberPlaceholder,
        emailAddressLabel,
        emailAddressPlaceholder,
        companyLabel,
        companyPlaceholder,
        questionLabel,
        questionPlaceholder,
        multifieldQuestionConcerningOptions,
        customerTypeLabel,
        customerTypeBusiness,
        customerTypeResidential,
        questionsOrCommentsLabel,
        questionsOrCommentsPlaceholder,
        ctaLabel,
        fileReference,
        explanatoryIconAlternativeText,
        explanatoryTitle,
        description,
        ctaLink,
        ctaLinkTarget,
        captchaToken,
    } = props;

    return (
        <div className={'ak-contact-us__container'}>
            <Form
                headingLevel={headingLevel}
                firstNameLabel={firstNameLabel}
                firstNamePlaceholder={firstNamePlaceholder}
                lastNameLabel={lastNameLabel}
                lastNamePlaceholder={lastNamePlaceholder}
                phoneNumberLabel={phoneNumberLabel}
                phoneNumberPlaceholder={phoneNumberPlaceholder}
                emailAddressLabel={emailAddressLabel}
                emailAddressPlaceholder={emailAddressPlaceholder}
                companyLabel={companyLabel}
                companyPlaceholder={companyPlaceholder}
                questionLabel={questionLabel}
                questionPlaceholder={questionPlaceholder}
                multifieldQuestionConcerningOptions={multifieldQuestionConcerningOptions}
                customerTypeLabel={customerTypeLabel}
                customerTypeBusiness={customerTypeBusiness}
                customerTypeResidential={customerTypeResidential}
                questionsOrCommentsLabel={questionsOrCommentsLabel}
                questionsOrCommentsPlaceholder={questionsOrCommentsPlaceholder}
                ctaLabel={ctaLabel}
                ctaLink={ctaLink}
                ctaLinkTarget={ctaLinkTarget}
                captchaToken={captchaToken}
            />
            <div className="ak-contact-us__right-panel">
                <BaseInstructions
                    fileReference={fileReference}
                    explanatoryIconAlternativeText={explanatoryIconAlternativeText}
                    explanatoryTitle={explanatoryTitle}
                    description={description}
                />
            </div>
        </div>
    );
};

ContactUs.displayName = 'ContactUs';

ContactUs.defaultProps = {
    headingLevel: 'h2',
    firstNameLabel: t('First Name'),
    firstNamePlaceholder: t('Enter first name'),
    lastNameLabel: t('Last Name'),
    lastNamePlaceholder: t('Enter last name'),
    phoneNumberLabel: t('Phone Number'),
    phoneNumberPlaceholder: t('Enter phone number'),
    emailAddressLabel: t('Email address'),
    emailAddressPlaceholder: t('Enter email address'),
    companyLabel: t('Company (optional)'),
    companyPlaceholder: t('Enter company name (optional)'),
    questionLabel: t('My Question is Concerning'),
    questionPlaceholder: t('Select an option'),
    customerTypeLabel: t('Customer Type'),
    customerTypeBusiness: t('Business'),
    customerTypeResidential: t('Residential'),
    questionsOrCommentsLabel: t('Questions or Comments'),
    questionsOrCommentsPlaceholder: t('Enter your questions or comments'),
    ctaLabel: t('Submit'),
};

ContactUs.propTypes = {
    headingLevel: PropTypes.oneOf(HEADING_LEVELS),
    firstNameLabel: PropTypes.string,
    firstNamePlaceholder: PropTypes.string,
    lastNameLabel: PropTypes.string,
    lastNamePlaceholder: PropTypes.string,
    phoneNumberLabel: PropTypes.string,
    phoneNumberPlaceholder: PropTypes.string,
    emailAddressLabel: PropTypes.string,
    emailAddressPlaceholder: PropTypes.string,
    companyLabel: PropTypes.string,
    companyPlaceholder: PropTypes.string,
    questionLabel: PropTypes.string,
    questionPlaceholder: PropTypes.string,
    multifieldQuestionConcerningOptions: PropTypes.array,
    customerTypeLabel: PropTypes.string,
    customerTypeBusiness: PropTypes.string,
    customerTypeResidential: PropTypes.string,
    questionsOrCommentsLabel: PropTypes.string,
    questionsOrCommentsPlaceholder: PropTypes.string,
    ctaLabel: PropTypes.string,
    ctaLink: PropTypes.string,
    ctaLinkTarget: PropTypes.string,
};

export default ContactUs;
