import React from 'react';
import withBreakpoint from '../../lib/utils/withBreakpoint';
import { Box, BaseTitle, BaseText, BaseImage } from '../base';

const BiographyFrame = (props) => {
    const { job, title, bioImage, isMobile, bioImageAltText, children } = props;

    return (
        <div className="ak-biography-frame">
            <Box direction={isMobile ? 'col' : 'row'} className="ak-biography-frame__container">
                <div className="ak-biography-frame__image-container">
                    <BaseImage
                        className="ak-biography-frame__image"
                        url={bioImage}
                        altText={bioImageAltText}
                        size="contain"
                    />
                </div>
                <div className="ak-biography-frame__content">
                    <div className="ak-biography-frame__details">
                        <BaseTitle text={title} overrideStyle="h2" />
                        <BaseText
                            text={job}
                            level="2"
                            className="ak-biography-frame__job-position"
                        />
                    </div>
                    {children}
                </div>
            </Box>
        </div>
    );
};

BiographyFrame.displayName = 'BiographyFrame';

export default withBreakpoint(BiographyFrame);
