import React from 'react';
import cx from 'classnames';
import PropTypes from 'prop-types';
import { Box, BaseImage, BaseButton, BaseTitle, BaseText, BaseLink } from '../base';
import { linkService } from '@konrad/reweb-aem';

const IconCard = (props) => {
    const {
        title,
        description,
        icon,
        headingLevel,
        ctaText,
        ctaUrl,
        ctaLinkTarget,
        alignItems,
        ctaType,
        iconAltText,
    } = props;
    const TagElement = headingLevel;
    const isAligned = alignItems === 'left';
    const isLink = ctaType === 'link';
    const hasCta = ctaType !== 'none';

    return (
        <Box
            direction="col"
            justify="left"
            alignment="center"
            className={cx('ak-icon-card__container', {
                'ak-icon-card__container--left-aligned': isAligned,
                'ak-icon-card__container--left-aligned--no-cta': isAligned && !hasCta,
            })}
        >
            <BaseImage
                url={icon}
                altText={iconAltText}
                className={cx('ak-icon-card__icon', {
                    'ak-icon-card__icon--left-aligned': isAligned,
                    'ak-icon-card__icon--top-padding': !isAligned,
                })}
            />
            <BaseTitle
                text={title}
                overrideStyle="h4"
                headingLevel={TagElement}
                className={cx('ak-icon-card__title', {
                    'ak-icon-card__title--left-aligned': isAligned,
                })}
            />
            <BaseText
                wrapper="div"
                level="4"
                className={cx('ak-icon-card__description', {
                    'ak-icon-card__description--left-aligned': isAligned,
                    'ak-icon-card__description--left-aligned--no-cta': isAligned && !hasCta,
                })}
                text={description}
                isRichText
            />
            {hasCta &&
                (isLink ? (
                    <BaseLink
                        url={linkService.get(ctaUrl)}
                        altText={ctaText}
                        target={ctaLinkTarget === 'external'}
                        className={cx('ak-icon-card__cta', 'ak-icon-card__cta--link')}
                    >
                        <BaseText
                            className={'ak-icon-card__cta-text'}
                            level="3"
                            text={ctaText}
                            wrapper="span"
                        />
                    </BaseLink>
                ) : (
                    <BaseButton
                        url={linkService.get(ctaUrl)}
                        fluid
                        type="tertiary"
                        external={ctaLinkTarget === 'external'}
                        children={ctaText}
                        className={cx('ak-icon-card__cta', 'ak-icon-card__cta--button')}
                    />
                ))}
        </Box>
    );
};

IconCard.displayName = 'IconCard';

IconCard.propTypes = {
    title: PropTypes.string,
    description: PropTypes.string,
    icon: PropTypes.string,
    headingLevel: PropTypes.string,
    ctaText: PropTypes.string,
    ctaUrl: PropTypes.string,
    ctaLinkTarget: PropTypes.string,
    alignItems: PropTypes.string,
    ctaType: PropTypes.string,
};

export default IconCard;
