import React from 'react';
import { BaseInstructions, BaseImage, BaseText } from '../base';
import { Form } from './elements';
import { t } from '@konrad/reweb-aem';

const RequestAQuote = (props) => {
    const {
        disclaimerIconReference,
        disclaimerIconAltText,
        disclaimer,

        //STEP ONE
        stepOneLabel,
        stepOneTitle,
        firstNameLabel,
        firstNamePlaceholder,
        lastNameLabel,
        lastNamePlaceholder,
        emailAddressLabel,
        emailAddressPlaceholder,
        phoneNumberLabel,
        phoneNumberPlaceholder,
        jobTitleLabel,
        jobTitlePlaceholder,
        companyLabel,
        companyPlaceholder,
        mailingAddressLabel,
        mailingAddressPlaceholder,
        cityLabel,
        cityPlaceholder,
        stateLabel,
        statePlaceholder,
        zipCodeLabel,
        zipCodePlaceholder,
        preferredContactLabel,
        preferredContactPlaceholder,

        //STEP TWO
        stepLabelTwo,
        stepTitleTwo,
        utilityLabel,
        utilityPlaceholder,
        approximateMonthlyBillLabel,
        approximateMonthlyBillPlaceholder,
        facilityTypeLabel,
        facilityTypePlaceholder,
        businessTypeLabel,
        businessTypePlaceholder,
        decisionTimeframeLabel,
        decisionTimeframePlaceholder,
        uploadRecentBillLabel,

        //STEP THREE
        stepThreeLabel,
        stepThreeTitle,
        howDidYouHearLabel,
        howDidYouHearPlaceholder,
        commentsLabel,
        commentsPlaceholder,
        ctaLabel,

        //EXPLANATORY COPY
        explanatoryIconReference,
        explanatoryIconAltText,
        explanatoryTitle,
        explanatoryDescription,

        //SUCCESS
        ctaLink,
        ctaLinkTarget,

        captchaToken,
    } = props;

    return (
        <>
            <div className={'ak-request-a-quote__disclaimer'}>
                <BaseImage
                    className="ak-request-a-quote__disclaimer--image"
                    url={disclaimerIconReference}
                    altText={disclaimerIconAltText}
                />
                <BaseText
                    className="ak-request-a-quote__disclaimer--text"
                    text={disclaimer}
                    level={'4'}
                />
            </div>
            <div className={'ak-request-a-quote__container'}>
                <Form
                    stepOneLabel={stepOneLabel}
                    stepOneTitle={stepOneTitle}
                    firstNameLabel={firstNameLabel}
                    firstNamePlaceholder={firstNamePlaceholder}
                    lastNameLabel={lastNameLabel}
                    lastNamePlaceholder={lastNamePlaceholder}
                    emailAddressLabel={emailAddressLabel}
                    emailAddressPlaceholder={emailAddressPlaceholder}
                    phoneNumberLabel={phoneNumberLabel}
                    phoneNumberPlaceholder={phoneNumberPlaceholder}
                    jobTitleLabel={jobTitleLabel}
                    jobTitlePlaceholder={jobTitlePlaceholder}
                    companyLabel={companyLabel}
                    companyPlaceholder={companyPlaceholder}
                    mailingAddressLabel={mailingAddressLabel}
                    mailingAddressPlaceholder={mailingAddressPlaceholder}
                    cityLabel={cityLabel}
                    cityPlaceholder={cityPlaceholder}
                    stateLabel={stateLabel}
                    statePlaceholder={statePlaceholder}
                    zipCodeLabel={zipCodeLabel}
                    zipCodePlaceholder={zipCodePlaceholder}
                    preferredContactLabel={preferredContactLabel}
                    preferredContactPlaceholder={preferredContactPlaceholder}
                    stepTwoLabel={stepLabelTwo}
                    stepTwoTitle={stepTitleTwo}
                    utilityLabel={utilityLabel}
                    utilityPlaceholder={utilityPlaceholder}
                    approximateMonthlyBillLabel={approximateMonthlyBillLabel}
                    approximateMonthlyBillPlaceholder={approximateMonthlyBillPlaceholder}
                    facilityTypeLabel={facilityTypeLabel}
                    facilityTypePlaceholder={facilityTypePlaceholder}
                    businessTypeLabel={businessTypeLabel}
                    businessTypePlaceholder={businessTypePlaceholder}
                    decisionTimeframeLabel={decisionTimeframeLabel}
                    decisionTimeframePlaceholder={decisionTimeframePlaceholder}
                    uploadRecentBillLabel={uploadRecentBillLabel}
                    stepThreeLabel={stepThreeLabel}
                    stepThreeTitle={stepThreeTitle}
                    howDidYouHearLabel={howDidYouHearLabel}
                    howDidYouHearPlaceholder={howDidYouHearPlaceholder}
                    commentsLabel={commentsLabel}
                    commentsPlaceholder={commentsPlaceholder}
                    ctaLabel={ctaLabel}
                    ctaLink={ctaLink}
                    ctaLinkTarget={ctaLinkTarget}
                    captchaToken={captchaToken}
                />
                <div className="ak-request-a-quote__right-panel">
                    <BaseInstructions
                        fileReference={explanatoryIconReference}
                        explanatoryIconAlternativeText={explanatoryIconAltText}
                        explanatoryTitle={explanatoryTitle}
                        description={explanatoryDescription}
                    />
                </div>
            </div>
        </>
    );
};

RequestAQuote.displayName = 'RequestAQuote';

RequestAQuote.defaultProps = {
    stepOneLabel: t('Step 1'),
    stepOneTitle: t('Contact Information'),
    stepLabelTwo: t('Step 2'),
    stepTitleTwo: t('Account Information'),
    stepThreeLabel: t('Step 3'),
    stepThreeTitle: t('Additional Information'),
    firstNameLabel: t('First Name'),
    firstNamePlaceholder: t('Enter first name'),
    lastNameLabel: t('Last Name'),
    lastNamePlaceholder: t('Enter last name'),
    phoneNumberLabel: t('Phone Number'),
    phoneNumberPlaceholder: t('Enter phone number'),
    emailAddressLabel: t('Email address'),
    emailAddressPlaceholder: t('Enter email address'),
    jobTitleLabel: t('Job Title'),
    jobTitlePlaceholder: t('Enter job title'),
    companyLabel: t('Company'),
    companyPlaceholder: t('Enter company name'),
    mailingAddressLabel: t('Mailing Address (optional)'),
    mailingAddressPlaceholder: t('Enter your address'),
    cityLabel: t('City (optional)'),
    cityPlaceholder: t('Enter city'),
    stateLabel: t('State (optional)'),
    statePlaceholder: t('Choose an option'),
    zipCodeLabel: t('Zip Code'),
    zipCodePlaceholder: t('Enter zip code'),
    preferredContactLabel: t('Preferred Method of Contact (optional)'),
    preferredContactPlaceholder: t('Choose an option'),
    utilityLabel: t('Utility'),
    utilityPlaceholder: t('Choose an option'),
    approximateMonthlyBillLabel: t('Approximate Monthly Bill'),
    approximateMonthlyBillPlaceholder: t('Choose an option'),
    facilityTypeLabel: t('Facility Type	(optional)'),
    facilityTypePlaceholder: t('Choose an option'),
    businessTypeLabel: t('Business Type (optional)'),
    businessTypePlaceholder: t('Choose an option'),
    decisionTimeframeLabel: t('Decision Timeframe (optional)'),
    decisionTimeframePlaceholder: t('Choose an option'),
    uploadRecentBillLabel: t(
        'Please upload a recent bill or invoice so that we can tailor a quote for your specific needs. (optional)'
    ),
    commentsLabel: t('Questions or Comments (optional)'),
    commentsPlaceholder: t('Enter Questions or Comments'),
    ctaLabel: t('Submit'),
    howDidYouHearLabel: t('How did you hear about us? (optional)'),
    howDidYouHearPlaceholder: t('Choose an option'),
};

export default RequestAQuote;
