import React, { Fragment, useState } from 'react';
import {
    BaseText,
    BaseTitle,
    BaseInput,
    BaseCheckbox,
    BaseButton,
    BaseIcon,
    BaseSelect,
    BaseInstructions,
    BaseModal,
    BaseImage,
} from '../../../base';
import uuidv4 from 'uuid/v4';
import { t } from '@konrad/reweb-aem';
import { STATES, YES_NO, HEAR_ABOUT_US, OFFER_TYPE_LABEL } from '../../../../lib/constants';
import {
    isRequired,
    isValidZipCode,
    isValidCustomerNumber,
} from '../../../../lib/fieldValidations';
import PropTypes from 'prop-types';

const Three = (props) => {
    const {
        title,

        company,
        setCompany,
        companyLabel,
        utilityModalLabel,

        customerNumber,
        setCustomerNumber,
        customerNumberPlaceholder,
        customerNameModalLabel,

        residentialPurpose,
        setResidentialPurpose,
        residentialPurposeLabel,
        residentialPurposePlaceholder,
        residentialPurposeModalLabel,

        hearAboutUs,
        setHearAboutUs,
        hearAboutUsLabel,
        hearAboutUsPlaceholder,

        backButtonLabel,
        CTALabelOne,
        stepThreeInstrImage,
        stepThreeInstrTitle,
        stepThreeInstrDescription,
        setChangedStep,
        step,
        amountOfSteps,
        setStep,
        goToFinalStep,
        validate,
        formType,

        edcData,
        isBundle,
        offerDataKeys,
    } = props;

    //Ids and references to the inputs
    const companyField = {};
    const customerNumberField = {};
    const residentialPurposeField = {};
    const hearAboutUsField = {};

    offerDataKeys.forEach((item) => {
        companyField[item] = {
            id: uuidv4(),
            ref: React.createRef(),
        };

        customerNumberField[item] = {
            id: uuidv4(),
            ref: React.createRef(),
        };

        residentialPurposeField[item] = {
            id: uuidv4(),
            ref: React.createRef(),
        };

        hearAboutUsField[item] = {
            id: uuidv4(),
            ref: React.createRef(),
        };
    });

    //States for the error messages on the inputs
    const [companyErrorMessage, setCompanyErrorMessage] = useState(
        offerDataKeys.reduce((acc, cur) => {
            acc[cur] = '';
            return acc;
        }, {})
    );
    const [customerNumberErrorMessage, setCustomerNumberErrorMessage] = useState(
        offerDataKeys.reduce((acc, cur) => {
            acc[cur] = '';
            return acc;
        }, {})
    );
    const [residentialPurposeErrorMessage, setResidentialPurposeErrorMessage] = useState(
        offerDataKeys.reduce((acc, cur) => {
            acc[cur] = '';
            return acc;
        }, {})
    );
    const [hearAboutUsErrorMessage, setHearAboutUsErrorErrorMessage] = useState(
        offerDataKeys.reduce((acc, cur) => {
            acc[cur] = '';
            return acc;
        }, {})
    );

    const handleSetCompanyErrorMessage = (type, value) => {
        let temp = Object.assign({}, companyErrorMessage);
        temp[type] = value;
        setCompanyErrorMessage(temp);
    };

    const handleSetCustomerNumberErrorMessage = (type, value) => {
        let temp = Object.assign({}, customerNumberErrorMessage);
        temp[type] = value;
        setCustomerNumberErrorMessage(temp);
    };

    const handleSetResidentialPurposeErrorMessage = (type, value) => {
        let temp = Object.assign({}, residentialPurposeErrorMessage);
        temp[type] = value;
        setResidentialPurposeErrorMessage(temp);
    };

    const handleSetHearAboutUsErrorErrorMessage = (type, value) => {
        let temp = Object.assign({}, hearAboutUsErrorMessage);
        temp[type] = value;
        setHearAboutUsErrorErrorMessage(temp);
    };

    //States for the modal visibility
    const [utilityModalShow, setUtilityModalShow] = useState(
        offerDataKeys.reduce((acc, cur) => {
            acc[cur] = false;
            return acc;
        }, {})
    );
    const [customerNameModalShow, setCustomerNameModalShow] = useState(
        offerDataKeys.reduce((acc, cur) => {
            acc[cur] = false;
            return acc;
        }, {})
    );
    const [residentialPurposeModalShow, setResidentialPurposeModalShow] = useState(
        offerDataKeys.reduce((acc, cur) => {
            acc[cur] = false;
            return acc;
        }, {})
    );

    const handleSetUtilityModalShow = (type, value) => {
        let temp = Object.assign({}, utilityModalShow);
        temp[type] = value;
        setUtilityModalShow(temp);
    };

    const handleSetCustomerNameModalShow = (type, value) => {
        let temp = Object.assign({}, customerNameModalShow);
        temp[type] = value;
        setCustomerNameModalShow(temp);
    };

    const handleSetResidentialPurposeModalShow = (type, value) => {
        let temp = Object.assign({}, residentialPurposeModalShow);
        temp[type] = value;
        setResidentialPurposeModalShow(temp);
    };

    //Validations for required inputs
    const requiredFields = [isRequired()];

    const customerNumberValid = {};

    offerDataKeys.forEach((key) => {
        let customerNumberErrorMessageValue;

        if (edcData[key].cusNumChars !== '' && edcData[key].cusNumLength !== '') {
            customerNumberErrorMessageValue = `${t('Invalid ')} ${edcData[key].cusNumLabel}. ${t(
                'It must start with '
            )} ${edcData[key].cusNumChars} ${t(' and it should be ')} ${
                edcData[key].cusNumLength
            } ${t(' characters length.')}`;
        } else if (edcData[key].cusNumLength !== '') {
            customerNumberErrorMessageValue = `${t('Invalid ')} ${edcData[key].cusNumLabel}. ${t(
                'It should be '
            )} ${edcData[key].cusNumLength} ${t(' characters length.')}`;
        } else if (edcData[key].cusNumChars !== '') {
            customerNumberErrorMessageValue = `${t('Invalid ')} ${edcData[key].cusNumLabel}. ${t(
                'It must start with '
            )} ${edcData[key].cusNumChars}`;
        } else {
            customerNumberErrorMessageValue = `${t('Invalid ')} ${edcData[key].cusNumLabel}.`;
        }

        customerNumberValid[key] = [
            isRequired(),
            isValidCustomerNumber(
                edcData[key].cusNumLength,
                edcData[key].cusNumChars,
                edcData[key].cusNumLabel,
                customerNumberErrorMessageValue
            ),
        ];
    });

    //Boolean variable true if the form type is residential, to show the yes or no select
    const displayAndValidateResidentialPurpose = formType
        ? formType === 'residential' || formType === 'bundle'
        : false;

    /**
     * Goes through all the required fields validating the inputs
     * @returns {boolean}
     */
    const validateInputs = () => {
        let valid = true;
        offerDataKeys.forEach((key) => {
            if (!validate(requiredFields, company[key], handleSetCompanyErrorMessage, key)) {
                if (valid) {
                    companyField[key].ref && companyField[key].ref.current.focus();
                }
                valid = false;
            }

            if (
                !validate(
                    customerNumberValid[key],
                    customerNumber[key],
                    handleSetCustomerNumberErrorMessage,
                    key
                )
            ) {
                if (valid) {
                    customerNumberField[key].ref && customerNumberField[key].ref.current.focus();
                }
                valid = false;
            }

            if (
                displayAndValidateResidentialPurpose &&
                !validate(
                    requiredFields,
                    residentialPurpose[key],
                    handleSetResidentialPurposeErrorMessage,
                    key
                )
            ) {
                if (valid) {
                    residentialPurposeField[key] &&
                        residentialPurposeField[key].ref.current.focus();
                }
                valid = false;
            }

            if (
                !validate(
                    requiredFields,
                    hearAboutUs[key],
                    handleSetHearAboutUsErrorErrorMessage,
                    key
                )
            ) {
                if (valid) {
                    hearAboutUsField[key] && hearAboutUsField[key].current.focus();
                }
                valid = false;
            }
        });

        return valid;
    };

    /**
     * Validates required inputs and changes the step
     * @param e
     */
    const handleNextClick = (e) => {
        e.stopPropagation();
        e.preventDefault();

        if (validateInputs()) {
            setChangedStep && setChangedStep(true);

            if (goToFinalStep) {
                setStep(4);
            } else {
                setStep(step + 1);
            }
        }
    };

    const handleBackClick = () => {
        setChangedStep && setChangedStep(true);
        setStep(step - 1);
    };

    const openModalUtility = (e, type) => {
        e.stopPropagation();
        e.preventDefault();
        handleSetUtilityModalShow(type, true);
    };

    const openModalCustomerName = (e, type) => {
        e.stopPropagation();
        e.preventDefault();
        handleSetCustomerNameModalShow(type, true);
    };

    const openModalResidentialPurposes = (e, type) => {
        e.stopPropagation();
        e.preventDefault();
        handleSetResidentialPurposeModalShow(type, true);
    };

    const handleModalClose = () => {
        const tempVal = {};
        offerDataKeys.forEach((key) => {
            tempVal[key] = false;
        });
        setUtilityModalShow(tempVal);
        setCustomerNameModalShow(tempVal);
        setResidentialPurposeModalShow(tempVal);
    };

    const handleSetCompany = (type, val) => {
        let tempCompany = Object.assign({}, company);
        tempCompany[type] = val;
        setCompany(tempCompany);
    };

    const handleSetCustomerNumber = (type, val) => {
        let tempCustomerNumber = Object.assign({}, customerNumber);
        tempCustomerNumber[type] = val;
        setCustomerNumber(tempCustomerNumber);
    };

    const handleSetResidentialPurpose = (type, val) => {
        let tempResPurpose = Object.assign({}, residentialPurpose);
        tempResPurpose[type] = val;
        setResidentialPurpose(tempResPurpose);
    };

    const handleSetHearAboutUs = (type, val) => {
        let tempHearAboutUs = Object.assign({}, hearAboutUs);
        tempHearAboutUs[type] = val;
        setHearAboutUs(tempHearAboutUs);
    };

    return (
        <>
            {step === 3 && (
                <div className="ak-enrollment-frame__mailing">
                    <div className="ak-enrollment-frame__container">
                        <BaseText
                            className={'ak-enrollment-frame__label'}
                            level={'4'}
                            text={`${t('Step')} ${step} ${t('of')} ${amountOfSteps}`}
                        />
                        <BaseTitle headingLevel={'h2'} text={title} overrideStyle={'h4'} />
                    </div>
                    <div className="ak-enrollment-frame__container ak-enrollment-frame__container--flex">
                        <div className="ak-enrollment-frame__panel-left">
                            {offerDataKeys.map((key, index) => {
                                const companyId = companyField[key].id;
                                const companyRef = companyField[key].ref;
                                const customerNumberId = customerNumberField[key].id;
                                const customerNumberRef = customerNumberField[key].ref;
                                const residentialPurposeId = residentialPurposeField[key].id;
                                const residentialPurposeRef = residentialPurposeField[key].ref;
                                const hearAboutUsId = hearAboutUsField[key].id;
                                const hearAboutUsRef = hearAboutUsField[key].ref;
                                return (
                                    <div
                                        key={index}
                                        className={`ak-enrollment-frame__offer-section-wrapper ${
                                            isBundle
                                                ? 'ak-enrollment-frame__offer-section-wrapper--bundle'
                                                : ''
                                        }`}
                                    >
                                        {isBundle && (
                                            <div className="ak-enrollment-frame__fields-row">
                                                <div className="ak-enrollment-frame__offer-section-title">
                                                    {OFFER_TYPE_LABEL[key]}
                                                </div>
                                            </div>
                                        )}

                                        <div className={'ak-enrollment-frame__fields-row'}>
                                            <div className={'ak-enrollment-frame__input'}>
                                                <BaseInput
                                                    required
                                                    disabled
                                                    type={'text'}
                                                    id={companyId}
                                                    ref={companyRef}
                                                    value={company[key]}
                                                    label={companyLabel}
                                                    error={companyErrorMessage[key]}
                                                    onChange={(val) => handleSetCompany(key, val)}
                                                />
                                                <button
                                                    type="button"
                                                    className="base-link ak-enrollment-frame__modal-button"
                                                    onClick={(e) => openModalUtility(e, key)}
                                                >
                                                    <BaseText
                                                        wrapper="span"
                                                        level="4"
                                                        text={utilityModalLabel}
                                                    />
                                                </button>
                                                <div className="ak-enrollment-frame__error-container">
                                                    {companyErrorMessage[key] && (
                                                        <BaseText
                                                            id={`${companyId}-error`}
                                                            className="ak-enrollment-frame__error-message"
                                                            text={companyErrorMessage[key]}
                                                            level={'3'}
                                                        />
                                                    )}
                                                </div>
                                            </div>
                                            <div className={'ak-enrollment-frame__input'}>
                                                <BaseInput
                                                    required
                                                    type={'text'}
                                                    id={customerNumberId}
                                                    ref={customerNumberRef}
                                                    value={customerNumber[key]}
                                                    label={edcData[key].cusNumLabel}
                                                    error={customerNumberErrorMessage[key]}
                                                    placeholder={customerNumberPlaceholder}
                                                    className={
                                                        customerNumberErrorMessage[key].length > 30
                                                            ? 'large'
                                                            : customerNumberErrorMessage[key]
                                                                  .length > 0
                                                            ? 'small'
                                                            : ''
                                                    }
                                                    onChange={(val) =>
                                                        handleSetCustomerNumber(key, val)
                                                    }
                                                />
                                                <button
                                                    type="button"
                                                    className="base-link ak-enrollment-frame__modal-button ak-enrollment-frame__modal-button--larger"
                                                    onClick={(e) => openModalCustomerName(e, key)}
                                                >
                                                    <BaseText
                                                        wrapper="span"
                                                        level="4"
                                                        text={customerNameModalLabel}
                                                    />
                                                </button>
                                                <div className="ak-enrollment-frame__error-container">
                                                    {customerNumberErrorMessage[key] && (
                                                        <BaseText
                                                            id={`${customerNumberId}-error`}
                                                            className="ak-enrollment-frame__error-message ak-enrollment-frame__error-message--larger"
                                                            text={customerNumberErrorMessage[key]}
                                                            level={'3'}
                                                        />
                                                    )}
                                                </div>
                                            </div>
                                        </div>

                                        <div className={'ak-enrollment-frame__fields-row'}>
                                            {displayAndValidateResidentialPurpose && (
                                                <div className={'ak-enrollment-frame__input'}>
                                                    <BaseSelect
                                                        required
                                                        id={residentialPurposeId}
                                                        ref={residentialPurposeRef}
                                                        value={residentialPurpose[key]}
                                                        label={residentialPurposeLabel}
                                                        error={residentialPurposeErrorMessage[key]}
                                                        ariaLabel={`${residentialPurposeId}-label`}
                                                        placeholder={residentialPurposePlaceholder}
                                                        onChange={(val) =>
                                                            handleSetResidentialPurpose(key, val)
                                                        }
                                                        className={'ak-enrollment-frame__select'}
                                                        ariaLabelList={`${residentialPurposeId}-label`}
                                                        options={YES_NO}
                                                    />
                                                    <button
                                                        type="button"
                                                        className="base-link ak-enrollment-frame__modal-button"
                                                        onClick={(e) =>
                                                            openModalResidentialPurposes(e, key)
                                                        }
                                                    >
                                                        <BaseText
                                                            wrapper="span"
                                                            level="4"
                                                            text={residentialPurposeModalLabel}
                                                        />
                                                    </button>
                                                    <div className="ak-enrollment-frame__error-container">
                                                        {residentialPurposeErrorMessage[key] && (
                                                            <BaseText
                                                                id={`${residentialPurposeId}-error`}
                                                                className="ak-enrollment-frame__error-message"
                                                                text={
                                                                    residentialPurposeErrorMessage[
                                                                        key
                                                                    ]
                                                                }
                                                                level={'3'}
                                                            />
                                                        )}
                                                    </div>
                                                </div>
                                            )}
                                            <div className={'ak-enrollment-frame__input'}>
                                                <BaseSelect
                                                    required
                                                    id={hearAboutUsId}
                                                    ref={hearAboutUsRef}
                                                    value={hearAboutUs[key]}
                                                    label={hearAboutUsLabel}
                                                    error={hearAboutUsErrorMessage[key]}
                                                    ariaLabel={`${hearAboutUsId}-label`}
                                                    placeholder={hearAboutUsPlaceholder}
                                                    onChange={(val) =>
                                                        handleSetHearAboutUs(key, val)
                                                    }
                                                    className={'ak-enrollment-frame__select'}
                                                    ariaLabelList={`${hearAboutUsId}-label`}
                                                    options={HEAR_ABOUT_US}
                                                />
                                                <div className="ak-enrollment-frame__error-container">
                                                    {hearAboutUsErrorMessage[key] && (
                                                        <BaseText
                                                            id={`${hearAboutUsId}-error`}
                                                            className="ak-enrollment-frame__error-message"
                                                            text={hearAboutUsErrorMessage[key]}
                                                            level={'3'}
                                                        />
                                                    )}
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                );
                            })}

                            <div className={'ak-enrollment-frame__btns-row'}>
                                <div className={'ak-enrollment-frame__element'}>
                                    {!goToFinalStep ? (
                                        <button
                                            type="button"
                                            className={'ak-enrollment-frame__back-btn'}
                                            onClick={() => handleBackClick()}
                                        >
                                            <BaseIcon
                                                className={'ak-enrollment-frame__chevron'}
                                                name={'chevron-down'}
                                            />
                                            {backButtonLabel}
                                        </button>
                                    ) : (
                                        <></>
                                    )}
                                </div>

                                <div
                                    className={
                                        'ak-enrollment-frame__element ak-enrollment-frame__input--justified-right'
                                    }
                                >
                                    <BaseButton
                                        className="ak-enrollment-frame__btn"
                                        onClick={(e) => handleNextClick(e)}
                                        altText={t('Next')}
                                        type="primary"
                                    >
                                        {CTALabelOne}
                                    </BaseButton>
                                </div>
                            </div>
                        </div>
                        <div
                            className={`ak-enrollment-frame__panel-right ${
                                isBundle ? 'ak-enrollment-frame__panel-right--bundle' : ''
                            }`}
                        >
                            <BaseInstructions
                                fileReference={stepThreeInstrImage}
                                explanatoryTitle={stepThreeInstrTitle}
                                description={stepThreeInstrDescription}
                            />
                        </div>
                    </div>
                    {offerDataKeys.map((key, index) => (
                        <Fragment key={index}>
                            <BaseModal
                                isContentModal
                                modalState={utilityModalShow[key]}
                                handleClose={handleModalClose}
                                id="ak-enrollment-frame__utility"
                                title={utilityModalLabel}
                            >
                                <div className="ak-enrollment-frame__content">
                                    <BaseText
                                        level={'4'}
                                        isRichText
                                        text={edcData[key].wrongUtilityModal}
                                    />
                                </div>
                            </BaseModal>

                            <BaseModal
                                isContentModal
                                modalState={customerNameModalShow[key]}
                                handleClose={handleModalClose}
                                id="ak-enrollment-frame__customer-number"
                                title={customerNameModalLabel}
                            >
                                <div className="ak-enrollment-frame__content">
                                    <BaseImage
                                        className="ak-enrollment-frame__customer-number-image"
                                        url={edcData[key].fileNameHelpFindModal}
                                    />
                                </div>
                            </BaseModal>

                            <BaseModal
                                isContentModal
                                modalState={residentialPurposeModalShow[key]}
                                handleClose={handleModalClose}
                                id="ak-enrollment-frame__residential-purpose"
                                title={residentialPurposeModalLabel}
                            >
                                <div className="ak-enrollment-frame__modal-content">
                                    <BaseText
                                        level={'4'}
                                        isRichText
                                        text={edcData[key].whatThisModal}
                                    />
                                </div>
                            </BaseModal>
                        </Fragment>
                    ))}
                </div>
            )}
        </>
    );
};

Three.displayName = 'Three';

Three.propTypes = {
    title: PropTypes.string,

    company: PropTypes.object,
    setCompany: PropTypes.func,
    companyLabel: PropTypes.string,

    customerNumber: PropTypes.object,
    setCustomerNumber: PropTypes.func,
    customerNumberLabel: PropTypes.string,
    customerNumberPlaceholder: PropTypes.string,

    residentialPurpose: PropTypes.object,
    setResidentialPurpose: PropTypes.func,
    residentialPurposeLabel: PropTypes.string,
    residentialPurposePlaceholder: PropTypes.string,

    hearAboutUs: PropTypes.object,
    setHearAboutUs: PropTypes.func,
    hearAboutUsLabel: PropTypes.string,
    hearAboutUsPlaceholder: PropTypes.string,

    backButtonLabel: PropTypes.string,
    CTALabelOne: PropTypes.string,
    stepThreeInstrImage: PropTypes.string,
    stepThreeInstrTitle: PropTypes.string,
    stepThreeInstrDescription: PropTypes.string,

    step: PropTypes.number,
    amountOfSteps: PropTypes.number,
    setStep: PropTypes.func,
    goToFinalStep: PropTypes.bool,
    validate: PropTypes.func,
};

export default Three;
