import React from 'react';
import cx from 'classnames';
import parse from 'html-react-parser';
import PropTypes from 'prop-types';
import {
    ALIGNMENT_OPTIONS,
    BODY_TEXT_WRAPPERS,
    BODY_TEXT_EXTRA_TYPES,
    BODY_TEXT_LEVELS,
} from '../../../lib/constants';

const BaseText = (props) => {
    const { id, text, type, level, wrapper, alignment, className, isRichText, children } = props;

    const TagElement = isRichText ? 'div' : wrapper;
    const content = text || children;
    return (
        <TagElement
            id={id}
            className={cx(
                'base-text',
                `base-text--alignment-${alignment}`,
                { [`base-text--level-${level}`]: !type },
                { [`base-text--type-${type}`]: type },
                className
            )}
        >
            {isRichText && content ? parse(content) : content}
        </TagElement>
    );
};

BaseText.displayName = 'BaseText';

BaseText.defaultProps = {
    level: '1',
    wrapper: 'p',
    alignment: 'left',
};

BaseText.propTypes = {
    id: PropTypes.string,
    isRichText: PropTypes.bool,
    className: PropTypes.string,
    text: PropTypes.string,
    level: PropTypes.oneOf(BODY_TEXT_LEVELS),
    wrapper: PropTypes.oneOf(BODY_TEXT_WRAPPERS),
    type: PropTypes.oneOf(BODY_TEXT_EXTRA_TYPES),
    alignment: PropTypes.oneOf(ALIGNMENT_OPTIONS),
};

export default BaseText;
