import React, { useState, useRef, useEffect } from 'react';
import ReCAPTCHA from 'react-google-recaptcha';
import {
    BaseButton,
    BaseCheckbox,
    BaseIcon,
    BaseInstructions,
    BaseText,
    BaseTitle,
} from '../../../base';
import { Card } from '../index';
import { t } from '@konrad/reweb-aem';
import uuidv4 from 'uuid/v4';
import PropTypes from 'prop-types';

const Four = (props) => {
    const {
        sectionFourTitle,
        stepOneTitle,
        stepTwoTitle,
        stepThreeTitle,
        firstName,
        lastName,
        phoneNumber,
        emailAddress,
        companyBusiness,
        streetAddress,
        city,
        state,
        zipCode,
        company,
        customerNumber,
        residentialPurpose,
        hearAboutUs,
        differentServiceAddress,
        serviceAddress,
        cityDiff,
        stateDiff,
        zipCodeDiff,
        disclaimerCheckbox,
        setDisclaimerCheckbox,
        alertCheckbox,
        setAlertCheckbox,
        textAlertCheckbox,
        disclaimer,
        fourthBackButton,
        fourthCTALabel,
        stepFourInstrImage,
        stepFourInstrTitle,
        stepFourInstrDescription,
        setChangedStep,
        step,
        amountOfSteps,
        setGoToFinalStep,
        setStep,
        pdfLink,
        completeEnrollment,
        submitErrorMessage,
        submitting,
        captcha,
        setCaptcha,
        captchaToken,
        offerDataKeys,
        isBundle,
    } = props;

    //Ids and references to the disclaimer
    const disclaimerId = uuidv4();
    const disclaimerRef = React.createRef();
    const recaptchaRef = useRef(null);
    const checkboxId = uuidv4();

    //State for the error messages on the disclaimer
    const [disclaimerErrorMessage, setDisclaimerErrorMessage] = useState('');
    const [captchaErrorMessage, setCaptchaErrorMessage] = useState('');

    //Creates the disclaimer text with the pdf link
    let disclaimerText = '<p>' + disclaimer + '</p>';
    let termsAndConditionsLinks = {};
    offerDataKeys.forEach((item) => {
        termsAndConditionsLinks[item] =
            '<a target="_blank" href="' + pdfLink[item] + '">' + t('Terms and Conditions') + '</a>';
    });

    Object.keys(termsAndConditionsLinks).forEach((item) => {
        let offerTypeParam = '';
        if (item === 'GAS') {
            offerTypeParam = '${g}';
        } else {
            offerTypeParam = '${e}';
        }
        disclaimerText = disclaimerText.replace(offerTypeParam, termsAndConditionsLinks[item]);
    });

    useEffect(() => {
        if (submitErrorMessage) {
            recaptchaRef && recaptchaRef.current && recaptchaRef.current.reset();
        }
    }, [submitErrorMessage]);

    /**
     * Checks if the disclaimer is checked and if there is the completeEnrollment function it calls it
     * If no checkbox checked, error message shows
     * @param e
     */
    const handleNextClick = (e) => {
        e.preventDefault();

        if (disclaimerCheckbox && captcha && completeEnrollment) {
            completeEnrollment();
            setDisclaimerErrorMessage(t(''));
            setCaptchaErrorMessage('');
        } else {
            if (!disclaimerCheckbox) {
                disclaimerRef && disclaimerRef.current.focus();
                setDisclaimerErrorMessage(t('You must accept the Terms and Conditions'));
            } else {
                setDisclaimerErrorMessage(t(''));
            }

            if (!captcha) {
                setCaptchaErrorMessage(t('Please verify that you are a human'));
            } else {
                setCaptchaErrorMessage('');
            }
        }
    };

    const handleBackClick = () => {
        setChangedStep && setChangedStep(true);
        setDisclaimerErrorMessage('');
        setCaptchaErrorMessage('');
        setCaptcha('');
        setGoToFinalStep(false);
        setStep(step - 1);
    };

    const generateCardData = ({ infoOne, infoTwo, infoThree, infoFour, infoFive, step }) => {
        if (step !== 3) {
            return [
                {
                    infoOne,
                    infoTwo,
                    infoThree,
                    infoFour,
                    infoFive,
                },
            ];
        } else {
            return offerDataKeys.map((key) => {
                return {
                    infoOne: infoOne[key],
                    infoTwo: infoTwo[key],
                    infoThree: infoThree[key],
                    infoFour: infoFour[key],
                    offerType: key,
                };
            });
        }
    };

    return (
        <>
            {step === 4 && (
                <div className="ak-enrollment-frame__review">
                    <div className="ak-enrollment-frame__container">
                        <BaseText
                            className={'ak-enrollment-frame__label'}
                            level={'4'}
                            text={`${t('Step')} ${step} ${t('of')} ${amountOfSteps}`}
                        />
                        <BaseTitle
                            headingLevel={'h2'}
                            text={sectionFourTitle}
                            overrideStyle={'h4'}
                        />
                    </div>
                    <div className="ak-enrollment-frame__container ak-enrollment-frame__container--flex">
                        <div className="ak-enrollment-frame__panel-left">
                            <div className={'ak-enrollment-frame__cards-container'}>
                                <Card
                                    cardData={generateCardData({
                                        infoOne: firstName,
                                        infoTwo: lastName,
                                        infoThree: phoneNumber,
                                        infoFour: emailAddress,
                                        infoFive: companyBusiness,
                                        step: 1,
                                    })}
                                    title={stepOneTitle}
                                    labelOne={'First Name'}
                                    labelTwo={'Last Name'}
                                    labelThree={'Phone Number'}
                                    labelFour={'Email Address'}
                                    labelFive={'Company'}
                                    icon={'edit'}
                                    setStep={setStep}
                                    setGoToFinalStep={setGoToFinalStep}
                                    stepToGo={1}
                                    setChangedStep={setChangedStep}
                                    isBundle={isBundle}
                                />
                                <Card
                                    cardData={generateCardData({
                                        infoOne: streetAddress,
                                        infoTwo: city,
                                        infoThree: state,
                                        infoFour: zipCode,
                                        step: 2,
                                    })}
                                    title={stepTwoTitle}
                                    labelOne={'Street Address'}
                                    labelTwo={'City'}
                                    labelThree={'State'}
                                    labelFour={'Zip Code'}
                                    displayExtraInfo={differentServiceAddress}
                                    extraLabelOne={'Service Address'}
                                    extraInfoOne={serviceAddress}
                                    extraLabelTwo={'Service City'}
                                    extraInfoTwo={cityDiff}
                                    extraLabelThree={'Service State'}
                                    extraInfoThree={stateDiff}
                                    extraLabelFour={'Service Zip Code'}
                                    extraInfoFour={zipCodeDiff}
                                    icon={'edit'}
                                    setStep={setStep}
                                    setGoToFinalStep={setGoToFinalStep}
                                    stepToGo={2}
                                    setChangedStep={setChangedStep}
                                    isBundle={isBundle}
                                />
                                <Card
                                    cardData={generateCardData({
                                        infoOne: company,
                                        infoTwo: customerNumber,
                                        infoThree: residentialPurpose,
                                        infoFour: hearAboutUs,
                                        step: 3,
                                    })}
                                    title={stepThreeTitle}
                                    labelOne={'Current Utility Company'}
                                    labelTwo={'Customer Number'}
                                    labelThree={
                                        'Is this used solely for your residential purposes?'
                                    }
                                    labelFour={'How did you hear about us?'}
                                    icon={'edit'}
                                    setStep={setStep}
                                    setGoToFinalStep={setGoToFinalStep}
                                    stepToGo={3}
                                    setChangedStep={setChangedStep}
                                    isBundle={isBundle}
                                />

                                <div className={'ak-enrollment-frame__disclaimer'}>
                                    <BaseCheckbox
                                        background
                                        isRichText
                                        id={disclaimerId}
                                        text={disclaimerText}
                                        ref={disclaimerRef}
                                        value={'disclaimer'}
                                        checked={disclaimerCheckbox}
                                        onChange={(val) => setDisclaimerCheckbox(val)}
                                    />
                                    {disclaimerErrorMessage && (
                                        <BaseText
                                            id={`${disclaimerId}-error`}
                                            className="ak-enrollment-frame__error-message"
                                            text={disclaimerErrorMessage}
                                            level={'3'}
                                        />
                                    )}
                                </div>

                                <div className={'ak-enrollment-frame__disclaimer'}>
                                    <BaseCheckbox
                                        background
                                        id={checkboxId}
                                        text={textAlertCheckbox}
                                        value={'alert'}
                                        checked={alertCheckbox}
                                        onChange={(val) => setAlertCheckbox(val)}
                                        className={'ak-enrollment-frame__alert'}
                                    />
                                </div>

                                <div className={'ak-enrollment-frame__captcha'}>
                                    <ReCAPTCHA
                                        ref={recaptchaRef}
                                        sitekey={captchaToken}
                                        onChange={(val) => setCaptcha(val)}
                                    />
                                    {captchaErrorMessage && (
                                        <BaseText
                                            id="captcha-error"
                                            className="ak-enrollment-frame__error-message"
                                            text={captchaErrorMessage}
                                            level={'3'}
                                        />
                                    )}
                                </div>

                                <div
                                    className={
                                        'ak-enrollment-frame__btns-row ak-enrollment-frame__btns-row--col'
                                    }
                                >
                                    <div className={'ak-enrollment-frame__element'}>
                                        <button
                                            disabled={submitting}
                                            className={'ak-enrollment-frame__back-btn'}
                                            onClick={() => handleBackClick()}
                                        >
                                            <BaseIcon
                                                className={'ak-enrollment-frame__chevron'}
                                                name={'chevron-down'}
                                            />
                                            {fourthBackButton}
                                        </button>
                                    </div>

                                    <div
                                        className={
                                            'ak-enrollment-frame__element ak-enrollment-frame__input--justified-right'
                                        }
                                    >
                                        <BaseButton
                                            loading={submitting}
                                            className="ak-enrollment-frame__btn"
                                            onClick={(e) => handleNextClick(e)}
                                            altText={t('Next')}
                                            type="primary"
                                        >
                                            {fourthCTALabel}
                                        </BaseButton>
                                        {submitErrorMessage && (
                                            <BaseText
                                                id={`submit-error`}
                                                className="ak-enrollment-frame__error-message ak-enrollment-frame__error-message--complete"
                                                text={submitErrorMessage}
                                                level={'3'}
                                            />
                                        )}
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="ak-enrollment-frame__panel-right">
                            <BaseInstructions
                                fileReference={stepFourInstrImage}
                                explanatoryTitle={stepFourInstrTitle}
                                description={stepFourInstrDescription}
                            />
                        </div>
                    </div>
                </div>
            )}
        </>
    );
};

Four.displayName = 'Four';

Four.propTypes = {
    sectionFourTitle: PropTypes.string,
    stepOneTitle: PropTypes.string,
    stepTwoTitle: PropTypes.string,
    stepThreeTitle: PropTypes.string,
    firstName: PropTypes.string,
    lastName: PropTypes.string,
    phoneNumber: PropTypes.string,
    emailAddress: PropTypes.string,
    companyBusiness: PropTypes.string,
    streetAddress: PropTypes.string,
    city: PropTypes.string,
    state: PropTypes.string,
    zipCode: PropTypes.string,
    company: PropTypes.object,
    customerNumber: PropTypes.object,
    residentialPurpose: PropTypes.object,
    hearAboutUs: PropTypes.object,
    differentServiceAddress: PropTypes.bool,
    electricService: PropTypes.string,
    cityDiff: PropTypes.string,
    stateDiff: PropTypes.oneOfType([PropTypes.string, PropTypes.object]),
    zipCodeDiff: PropTypes.string,
    disclaimerCheckbox: PropTypes.bool,
    setDisclaimerCheckbox: PropTypes.func,
    specialOffersCheckboxText: PropTypes.string,
    specialOffersCheckbox: PropTypes.bool,
    setSpecialOffersCheckbox: PropTypes.func,
    disclaimer: PropTypes.string,
    fourthBackButton: PropTypes.string,
    fourthCTALabel: PropTypes.string,
    stepFourInstrImage: PropTypes.string,
    stepFourInstrTitle: PropTypes.string,
    stepFourInstrDescription: PropTypes.string,
    setChangedStep: PropTypes.func,
    step: PropTypes.number,
    amountOfSteps: PropTypes.number,
    setGoToFinalStep: PropTypes.func,
    setStep: PropTypes.func,
    pdfLink: PropTypes.oneOfType([PropTypes.string, PropTypes.array]),
    completeEnrollment: PropTypes.func,
    submitErrorMessage: PropTypes.string,
};

export default Four;
