import React from 'react';
import { BaseText, BaseTitle, BaseIcon } from '../../../base';
import PropTypes from 'prop-types';
import { t } from '@konrad/reweb-aem';
import { OFFER_TYPE_LABEL } from '../../../../lib/constants';

const Card = (props) => {
    const {
        title,
        labelOne,
        infoOne,
        labelTwo,
        infoTwo,
        labelThree,
        infoThree,
        labelFour,
        infoFour,
        labelFive,
        infoFive,
        displayExtraInfo,
        extraLabelOne,
        extraInfoOne,
        extraLabelTwo,
        extraInfoTwo,
        extraLabelThree,
        extraInfoThree,
        extraLabelFour,
        extraInfoFour,
        icon,
        setStep,
        stepToGo,
        setGoToFinalStep,
        setChangedStep,
        cardData,
        isBundle,
    } = props;

    const handleEditClick = (newStep) => {
        setGoToFinalStep && setGoToFinalStep(true);
        setChangedStep && setChangedStep(true);
        setStep && setStep(newStep);
    };

    return (
        <div className={'ak-enrollment-frame__card'}>
            <BaseTitle text={title} headingLevel={'h3'} overrideStyle={'h4'} />
            <button
                aria-label={`${t('Edit step')} ${stepToGo}`}
                className={'ak-enrollment-frame__action-icon'}
                onClick={() => handleEditClick(stepToGo)}
            >
                <BaseIcon name={icon} />
            </button>
            {cardData.map((data, index) => (
                <div key={index} className={'ak-enrollment-frame__card-section-wrapper'}>
                    {isBundle && (
                        <div className="ak-enrollment-frame__offer-section-title ak-enrollment-frame__offer-section-title--card">
                            {OFFER_TYPE_LABEL[data.offerType]}
                        </div>
                    )}
                    <div className={'ak-enrollment-frame__row'}>
                        {labelOne && data.infoOne && (
                            <div className={'ak-enrollment-frame__detail-container'}>
                                <BaseText
                                    className={'ak-enrollment-frame__label'}
                                    text={labelOne}
                                    level={'4'}
                                />
                                <BaseText text={data.infoOne} level={'3'} />
                            </div>
                        )}
                        {labelTwo && data.infoTwo && (
                            <div className={'ak-enrollment-frame__detail-container'}>
                                <BaseText
                                    className={'ak-enrollment-frame__label'}
                                    text={labelTwo}
                                    level={'4'}
                                />
                                <BaseText text={data.infoTwo} level={'3'} />
                            </div>
                        )}
                    </div>
                    <div className={'ak-enrollment-frame__row'}>
                        {labelThree && data.infoThree && (
                            <div className={'ak-enrollment-frame__detail-container'}>
                                <BaseText
                                    className={'ak-enrollment-frame__label'}
                                    text={labelThree}
                                    level={'4'}
                                />
                                <BaseText text={data.infoThree} level={'3'} />
                            </div>
                        )}
                        {labelFour && data.infoFour && (
                            <div className={'ak-enrollment-frame__detail-container'}>
                                <BaseText
                                    className={'ak-enrollment-frame__label'}
                                    text={labelFour}
                                    level={'4'}
                                />
                                <BaseText text={data.infoFour} level={'3'} />
                            </div>
                        )}
                    </div>
                    {labelFive && data.infoFive && (
                        <div className={'ak-enrollment-frame__row'}>
                            <div className={'ak-enrollment-frame__detail-container'}>
                                <BaseText
                                    className={'ak-enrollment-frame__label'}
                                    text={labelFive}
                                    level={'4'}
                                />
                                <BaseText text={data.infoFive} level={'3'} />
                            </div>
                        </div>
                    )}
                </div>
            ))}
            {displayExtraInfo && (
                <>
                    <div className={'ak-enrollment-frame__line'} />
                    <div className={'ak-enrollment-frame__row'}>
                        {extraLabelOne && extraInfoOne && (
                            <div className={'ak-enrollment-frame__detail-container'}>
                                <BaseText
                                    className={'ak-enrollment-frame__label'}
                                    text={extraLabelOne}
                                    level={'4'}
                                />
                                <BaseText text={extraInfoOne} level={'3'} />
                            </div>
                        )}
                        {extraLabelTwo && extraInfoTwo && (
                            <div className={'ak-enrollment-frame__detail-container'}>
                                <BaseText
                                    className={'ak-enrollment-frame__label'}
                                    text={extraLabelTwo}
                                    level={'4'}
                                />
                                <BaseText text={extraInfoTwo} level={'3'} />
                            </div>
                        )}
                    </div>
                    <div className={'ak-enrollment-frame__row'}>
                        {extraLabelThree && extraInfoThree && (
                            <div className={'ak-enrollment-frame__detail-container'}>
                                <BaseText
                                    className={'ak-enrollment-frame__label'}
                                    text={extraLabelThree}
                                    level={'4'}
                                />
                                <BaseText text={extraInfoThree} level={'3'} />
                            </div>
                        )}
                        {extraLabelFour && extraInfoFour && (
                            <div className={'ak-enrollment-frame__detail-container'}>
                                <BaseText
                                    className={'ak-enrollment-frame__label'}
                                    text={extraLabelFour}
                                    level={'4'}
                                />
                                <BaseText text={extraInfoFour} level={'3'} />
                            </div>
                        )}
                    </div>
                </>
            )}
        </div>
    );
};

Card.displayName = 'Card';

Card.propTypes = {
    title: PropTypes.string,
    labelOne: PropTypes.string,
    infoOne: PropTypes.oneOfType([PropTypes.string, PropTypes.object]),
    labelTwo: PropTypes.string,
    infoTwo: PropTypes.oneOfType([PropTypes.string, PropTypes.object]),
    labelThree: PropTypes.string,
    infoThree: PropTypes.oneOfType([PropTypes.string, PropTypes.object]),
    labelFour: PropTypes.string,
    infoFour: PropTypes.oneOfType([PropTypes.string, PropTypes.object]),
    displayExtraInfo: PropTypes.bool,
    extraLabelOne: PropTypes.string,
    extraInfoOne: PropTypes.oneOfType([PropTypes.string, PropTypes.object]),
    extraLabelTwo: PropTypes.string,
    extraInfoTwo: PropTypes.oneOfType([PropTypes.string, PropTypes.object]),
    extraLabelThree: PropTypes.string,
    extraInfoThree: PropTypes.oneOfType([PropTypes.string, PropTypes.object]),
    extraLabelFour: PropTypes.string,
    extraInfoFour: PropTypes.oneOfType([PropTypes.string, PropTypes.object]),
    icon: PropTypes.string,
    setStep: PropTypes.func,
    stepToGo: PropTypes.number,
    setGoToFinalStep: PropTypes.func,
};

export default Card;
