import React from 'react';
import PropTypes from 'prop-types';
import { BaseIcon, BaseButton } from '../base';
import cx from 'classnames';
import withBreakpoint from '../../lib/utils/withBreakpoint';
import { t } from '@konrad/reweb-aem';

const Pagination = (props) => {
    const { currentPage, totalPages, onPageChange, sectionName, isDesktop } = props;
    const firstPage = 1;
    const lastPage = totalPages;
    const totalVisiblePages = 3;
    const currentPageLabel = t('current page');
    const showPrev = currentPage !== firstPage;
    const showNext = currentPage !== totalPages;
    const visiblePages = [];
    let startPage =
        currentPage + totalVisiblePages >= totalPages
            ? totalPages - totalVisiblePages
            : currentPage;
    if (startPage < firstPage) {
        startPage = firstPage;
    }

    let endPage = totalPages < totalVisiblePages ? totalPages - 1 : currentPage + totalVisiblePages;
    if (endPage >= totalPages) {
        endPage = totalPages - 1;
    }
    for (let page = startPage; page <= endPage; page++) {
        visiblePages.push(page);
    }

    const onNext = () => {
        let newPage = currentPage + 1;
        if (newPage > totalPages) {
            newPage = totalPages;
        }
        onPageChange(newPage);
    };

    const onPrev = () => {
        let newPage = currentPage - 1;
        if (newPage < firstPage) {
            newPage = firstPage;
        }
        onPageChange(newPage);
    };

    const hasEllipsis = currentPage < totalPages - totalVisiblePages;
    const renderPages = (page, index) =>
        isDesktop || currentPage === page ? (
            <li
                className={`ak-pagination__nav-item ${currentPage === page ? 'current' : ''}`}
                key={index}
            >
                <BaseButton
                    className="ak-pagination__page-number"
                    onClick={() => onPageChange(page)}
                    altText={`${t('Page')} ${page} ${currentPage === page ? currentPageLabel : ''}`}
                    type="pagination"
                >
                    <span aria-hidden="true">{page}</span>
                </BaseButton>
            </li>
        ) : null;

    return (
        <div className="ak-pagination">
            <nav className="ak-pagination__nav" aria-label={`${sectionName} pagination`}>
                <ul className="ak-pagination__nav-list">
                    {showPrev && (
                        <li className="ak-pagination__nav-item ak-pagination__prev">
                            <BaseButton
                                className="ak-pagination__arrow-btn"
                                onClick={onPrev}
                                altText={t('Previous page')}
                                type="tertiary"
                            >
                                <BaseIcon name="arrow-left" />
                            </BaseButton>
                        </li>
                    )}
                    {visiblePages.map(renderPages)}
                    {(isDesktop || currentPage === lastPage) && (
                        <li
                            className={cx('ak-pagination__nav-item', {
                                current: currentPage === lastPage,
                                ellipsis: hasEllipsis,
                            })}
                        >
                            <BaseButton
                                className="ak-pagination__page-number"
                                onClick={() => onPageChange(lastPage)}
                                altText={`${t('Page')} ${lastPage} ${
                                    currentPage === lastPage ? currentPageLabel : ''
                                }`}
                                type="pagination"
                            >
                                <span aria-hidden="true">{lastPage}</span>
                            </BaseButton>
                        </li>
                    )}
                    {showNext && (
                        <li className="ak-pagination__nav-item ak-pagination__next">
                            <BaseButton
                                className="ak-pagination__arrow-btn"
                                onClick={onNext}
                                altText={t('Next page')}
                                type="tertiary"
                            >
                                <BaseIcon name="arrow-right" />
                            </BaseButton>
                        </li>
                    )}
                </ul>
            </nav>
        </div>
    );
};

Pagination.displayName = 'Pagination';

Pagination.propTypes = {
    currentPage: PropTypes.number.isRequired,
    totalPages: PropTypes.number.isRequired,
    onPageChange: PropTypes.func,
    sectionName: PropTypes.string,
};

export default withBreakpoint(Pagination);
