import React, { useState, useCallback } from 'react';
import debounce from 'lodash/debounce';
import withBreakpoint from '../../lib/utils/withBreakpoint';
import { STATES } from '../../lib/constants';
import { BaseTitle, BaseText, BaseImage, BaseButton, BaseInput, BaseIcon } from '../base';
import CommunityTable from './CommunityTable';
import { t } from '@konrad/reweb-aem';

const CommunityProgramsFrame = (props) => {
    const [filter, setFilter] = useState('');
    const debounceOnChange = useCallback(debounce(setFilter, 25), []);

    const {
        title,
        subtitle,
        phoneTitle,
        phoneNumber,
        phoneIcon,
        filterPlaceholder,
        column1Title,
        column2Title,
        column3Title,
        column4Title,
        children,
        communities,
    } = props;

    const handleFormSubmit = (e) => {
        e.preventDefault();
        return false;
    };

    const filterLower = filter.toLocaleLowerCase();
    const parsedCommunities = filter
        ? communities.filter((program) => {
              const state = STATES.find((state) => state.value === program.state);
              return (
                  program.communityName.toLocaleLowerCase().includes(filterLower) ||
                  program.county.toLocaleLowerCase().includes(filterLower) ||
                  program.utility.toLocaleLowerCase().includes(filterLower) ||
                  program.state.toLocaleLowerCase().includes(filterLower) ||
                  (state && state.option.toLocaleLowerCase().includes(filterLower))
              );
          })
        : communities;

    return (
        <div className="ak-community-programs-frame">
            <div className="ak-community-programs-frame__header">
                <div className="ak-community-programs-frame__header-container">
                    <div className="ak-community-programs-frame__header-container__left">
                        <BaseTitle
                            className="ak-community-programs-frame__title"
                            text={title}
                            headingLevel="h1"
                        />
                        <BaseText
                            className="ak-community-programs-frame__subtitle"
                            text={subtitle}
                            level="1"
                        />
                    </div>
                    <div className="ak-community-programs-frame__header-container__right">
                        <BaseText
                            className="ak-community-programs-frame__phone-title"
                            text={phoneTitle}
                            level="3"
                        />
                        <BaseButton
                            className="ak-community-programs-frame__phone-link"
                            url={`tel:${phoneNumber}`}
                            type="tertiary"
                            altText={`${phoneTitle}  ${phoneNumber}`}
                        >
                            <BaseImage
                                className="ak-community-programs-frame__phone-link-icon"
                                url={phoneIcon}
                            />
                            <span className="text">{phoneNumber}</span>
                        </BaseButton>
                    </div>
                </div>
                {children && (
                    <div className="ak-community-programs-frame__header-components">{children}</div>
                )}
            </div>
            <div className="ak-community-programs-frame__filter-container">
                <form
                    className="ak-community-programs-frame__filter-container__form"
                    onSubmit={handleFormSubmit}
                    noValidate
                >
                    <BaseInput
                        className="ak-community-programs-frame__filter-container__input"
                        value={filter}
                        onChange={debounceOnChange}
                        ariaLabel={filterPlaceholder}
                        placeholder={filterPlaceholder}
                    />
                    <BaseButton
                        isSubmit
                        className="ak-community-programs-frame__filter-container__btn"
                        altText={t('Search Icon')}
                    >
                        <BaseIcon name="search-icon" />
                    </BaseButton>
                </form>
            </div>
            <div className="ak-community-programs-frame__table-container">
                <CommunityTable
                    column1Title={column1Title}
                    column2Title={column2Title}
                    column3Title={column3Title}
                    column4Title={column4Title}
                    programs={parsedCommunities}
                />
            </div>
        </div>
    );
};

CommunityProgramsFrame.displayName = 'CommunityProgramsFrame';

export default withBreakpoint(CommunityProgramsFrame);
