import React, { useEffect } from 'react';
import { BaseText, BaseTitle, BaseDrawer, BaseButton, BaseImage } from '../base';
import PropTypes from 'prop-types';
import { t } from '@konrad/reweb-aem';
import cx from 'classnames';
import { OFFER_TYPE_LABEL, TERMS_AND_CONDITIONS_FOLDER } from '../../lib/constants';

const OfferDetails = (props) => {
    const {
        time,
        title,
        amount,
        showCTA,
        edcLogo,
        edcName,
        timeTerm,
        greenOfferImage,
        greenOfferText,
        amountTerm,
        drawerShow,
        description,
        planDescription,
        handleDrawerClose,
        priceBreakdownArray,
        enrollmentUrl,
        isBundle,
        gasOffer,
        electricityOffer,
        edcList,
        priorityCode,
        // fixedRatePlanDescription
    } = props;

    let pdfLink = null;

    if (!isBundle) {
        pdfLink = TERMS_AND_CONDITIONS_FOLDER + '/' + priorityCode + '.pdf';
    } else {
        let pdfLinks = [];
        [gasOffer, electricityOffer].forEach((item) => {
            if (item) {
                pdfLinks.push({
                    offerType: item.offerType,
                    link:
                        item && item.priorityCode
                            ? TERMS_AND_CONDITIONS_FOLDER + '/' + item.priorityCode + '.pdf'
                            : null,
                });
            }
        });

        pdfLink = pdfLinks;
    }

    const generatePdfText = (link, offerType) => {
        if (!link) {
            return null;
        }

        let termsAndConditionsAnchor =
            '<a target="_blank" href="' + link + '">' + t('Terms and Conditions') + '</a>';
        return isBundle
            ? '<p>' + termsAndConditionsAnchor + t(' for ') + OFFER_TYPE_LABEL[offerType] + '</p>'
            : '<p>' + t('View') + ' ' + termsAndConditionsAnchor + '</p>';
    };

    return (
        <BaseDrawer
            drawerState={drawerShow}
            handleClose={handleDrawerClose}
            className={'offer-details'}
        >
            <div className={'offer-details__content'}>
                <div className={'offer-details__information'}>
                    <BaseText
                        className={'offer-details__subtitle'}
                        level="3"
                        text={t('Viewing details for our plan')}
                    />
                    <BaseTitle overrideStyle="h2" headingLevel="h1" text={title} />
                    {description && (
                        <BaseText
                            isRichText
                            className={'offer-details__description'}
                            level="2"
                            text={description}
                        />
                    )}

                    {planDescription && (
                        <div className={'offer-details__container'}>
                            <BaseText
                                className={'offer-details__title'}
                                level="4"
                                text={t('Who is this plan best for?')}
                            />
                            <BaseText
                                isRichText
                                className={'offer-details__text'}
                                level="3"
                                text={planDescription}
                            />
                        </div>
                    )}

                    {((edcList && edcList.length > 0) || edcLogo) && (
                        <div className="offer-details__container offer-details__edc-logo-container">
                            <BaseText
                                className="offer-details__title"
                                level="4"
                                text={t('Utility')}
                            />
                            {isBundle ? (
                                <div className="offer-details__edc-images-block">
                                    {edcList.map((item, index) => (
                                        <div
                                            key={index}
                                            className={`offer-details__edc-image-container ${
                                                edcList.length > 1
                                                    ? 'offer-details__edc-image-container--multiple'
                                                    : ''
                                            }`}
                                        >
                                            <BaseText
                                                className="offer-details__edc-image-title"
                                                level="4"
                                                text={OFFER_TYPE_LABEL[item.offerType]}
                                            />
                                            <BaseImage
                                                className="offer-details__image offer-details__edc-logo"
                                                url={item.edcLogo}
                                                altText={item.edcName}
                                            />
                                        </div>
                                    ))}
                                </div>
                            ) : (
                                <div className="offer-details__edc-images-block">
                                    <BaseImage
                                        className="offer-details__image offer-details__edc-logo"
                                        url={edcLogo}
                                        altText={edcName}
                                    />
                                </div>
                            )}
                        </div>
                    )}

                    {priceBreakdownArray && (
                        <div className={'offer-details__container'}>
                            <BaseText
                                className={'offer-details__title'}
                                level="4"
                                text={t('Price Breakdown')}
                            />
                            <ul className={'offer-details__price-breakdown-container'}>
                                {priceBreakdownArray.map((option, i) => {
                                    return (
                                        <li key={i} className={'offer-details__price-breakdown'}>
                                            <BaseText
                                                className={'offer-details__price-name'}
                                                level="4"
                                                text={option.priceLabel}
                                            />
                                            <BaseText
                                                className={'offer-details__amount'}
                                                level="4"
                                                text={option.priceValue}
                                            />
                                        </li>
                                    );
                                })}
                            </ul>
                        </div>
                    )}
                    {/*<div className={'offer-details__container'}>*/}
                    {/*    <BaseText className={'offer-details__title'} level='4' text={t('Fixed Rate Plan')}/>*/}
                    {/*    <BaseText className={'offer-details__text'} level='3' text={fixedRatePlanDescription}/>*/}
                    {/*</div>*/}

                    {greenOfferImage && greenOfferText && (
                        <div className={'offer-details__container'}>
                            <div className={'offer-details__image-container'}>
                                <BaseImage
                                    className="offer-details__image"
                                    url={greenOfferImage}
                                    altText={t('Green offer image')}
                                />
                            </div>
                            <BaseText
                                className={'offer-details__green-text'}
                                level="4"
                                text={greenOfferText}
                                isRichText={true}
                            />
                        </div>
                    )}

                    {pdfLink && (
                        <div className="offer-details__container offer-details__edc-logo-container">
                            <BaseText
                                className="offer-details__title"
                                level="4"
                                text={t('Contract Documents for this Plan')}
                            />
                            <div className="offer-details__terms-conditions-block">
                                {isBundle ? (
                                    pdfLink.map((item, index) => (
                                        <BaseText
                                            key={index}
                                            className={
                                                'offer-details__text offer-details__text--is-bundle'
                                            }
                                            level="4"
                                            text={generatePdfText(item.link, item.offerType)}
                                            isRichText={true}
                                        />
                                    ))
                                ) : (
                                    <BaseText
                                        className={'offer-details__text'}
                                        level="4"
                                        text={generatePdfText(pdfLink)}
                                        isRichText={true}
                                    />
                                )}
                            </div>
                        </div>
                    )}
                </div>
                <div
                    className={cx('offer-details__final-information', {
                        'offer-details__final-information--no-button': !showCTA,
                    })}
                >
                    <div
                        className={cx('offer-details__final-container', {
                            'offer-details__final-container--no-button': !showCTA,
                        })}
                    >
                        {isBundle ? (
                            [gasOffer, electricityOffer].map(
                                (offerItem, index, array) =>
                                    offerItem.title && (
                                        <React.Fragment key={index}>
                                            <div
                                                className={'offer-details__final-details-container'}
                                            >
                                                <BaseText level="2" text={offerItem.price} />
                                                <BaseText
                                                    className={'offer-details__subtitle'}
                                                    level="4"
                                                    text={offerItem.priceLabel}
                                                />
                                            </div>
                                            {index < array.length - 1 && (
                                                <div className={'offer-details__line'} />
                                            )}
                                        </React.Fragment>
                                    )
                            )
                        ) : (
                            <div className={'offer-details__final-details-container'}>
                                <BaseText level="2" text={amount} />
                                <BaseText
                                    className={'offer-details__subtitle'}
                                    level="4"
                                    text={amountTerm}
                                />
                            </div>
                        )}
                        <div className={'offer-details__line'} />
                        <div className={'offer-details__final-details-container'}>
                            <BaseText level="2" text={time} />
                            <BaseText
                                className={'offer-details__subtitle'}
                                level="4"
                                text={timeTerm}
                            />
                        </div>
                    </div>
                    {showCTA && (
                        <BaseButton type="primary" url={enrollmentUrl}>
                            {t('Enroll Now')}
                        </BaseButton>
                    )}
                </div>
            </div>
        </BaseDrawer>
    );
};

OfferDetails.displayName = 'OfferDetails';

OfferDetails.propTypes = {
    button: PropTypes.bool,
    time: PropTypes.string,
    title: PropTypes.string,
    amount: PropTypes.string,
    edcLogo: PropTypes.string,
    edcName: PropTypes.string,
    timeTerm: PropTypes.string,
    drawerShow: PropTypes.bool,
    amountTerm: PropTypes.string,
    description: PropTypes.string,
    enrollmentUrl: PropTypes.string,
    handleDrawerClose: PropTypes.func,
    planDescription: PropTypes.string,
    priceBreakdownArray: PropTypes.array,
    fixedRatePlanDescription: PropTypes.string,
};

export default OfferDetails;
