import axios from 'axios';
import { CSRF_TOKEN_PATH } from '../lib/constants';

export const get = (url, options = { headers: {} }) => {
    return new Promise((resolve, reject) => {
        let params = {
            method: 'get',
            url: encodeURI(url),
            headers: options.headers,
        };

        axios(params)
            .then((response) => {
                resolve(response.data);
            })
            .catch((error) => {
                reject(error);
            });
    });
};

export const post = (url, data) => {
    return requestWithData(url, data, 'post');
};

const requestWithData = (url, data, method) => {
    return new Promise((resolve, reject) => {
        get(CSRF_TOKEN_PATH)
            .then((response) => {
                let params = {
                    method: method,
                    url: encodeURI(url),
                    data: data,
                    responseType: 'json',
                    headers: {
                        'CSRF-Token': response.token,
                    },
                };

                axios(params)
                    .then((response) => {
                        resolve(response.data);
                    })
                    .catch((error) => {
                        reject(error);
                    });
            })
            .catch((error) => {
                reject(error);
            });
    });
};
