import React from 'react';
import cx from 'classnames';
import { ALIGNMENT_OPTIONS, DIRECTION_OPTIONS } from '../../../lib/constants';
import PropTypes from 'prop-types';

const BaseBox = React.forwardRef(function Box(props, ref) {
    const { id, justify, children, className, direction, alignment } = props;

    return (
        <div
            ref={ref}
            id={id}
            className={cx(
                'base-box',
                `base-box--direction-${direction}`,
                {
                    [`base-box--justify-${justify}`]: justify,
                    [`base-box--align-${alignment}`]: alignment,
                },
                className
            )}
        >
            {children}
        </div>
    );
});

BaseBox.defaultProps = {
    direction: 'row',
};

BaseBox.propTypes = {
    justify: PropTypes.oneOf(ALIGNMENT_OPTIONS),
    alignment: PropTypes.oneOf(ALIGNMENT_OPTIONS),
    direction: PropTypes.oneOf(DIRECTION_OPTIONS),
};

export default BaseBox;
