import React from 'react';
import cx from 'classnames';
import { t } from '@konrad/reweb-aem';
import PropTypes from 'prop-types';
import { BUTTON_TYPES } from '../../../lib/constants';

const BaseButton = React.forwardRef((props, ref) => {
    const {
        id,
        url,
        type,
        fluid,
        loading,
        isSubmit,
        external,
        onClick,
        altText,
        disabled,
        tabIndex,
        children,
        className,
        onMouseEnter,
        onMouseLeave,
        hasIconOnly,
        ariaControls,
        ariaExpanded,
    } = props;

    const TagElement = url ? 'a' : 'button';

    return (
        <TagElement
            id={id}
            ref={ref}
            href={url}
            onClick={onClick}
            disabled={disabled || loading}
            tabIndex={tabIndex}
            aria-label={loading ? t('Loading') : altText}
            onMouseEnter={onMouseEnter}
            onMouseLeave={onMouseLeave}
            aria-controls={ariaControls}
            aria-expanded={ariaExpanded}
            type={isSubmit ? 'submit' : undefined}
            target={external ? '_blank' : undefined}
            className={cx(
                'base-button',
                `base-button--${type}`,
                {
                    'base-button--fluid': fluid,
                    'base-button--disabled': disabled,
                    'base-button--icon': hasIconOnly,
                },
                className
            )}
        >
            {loading ? <span className="loading"></span> : children}
        </TagElement>
    );
});

BaseButton.displayName = 'BaseButton';

BaseButton.defaultProps = {
    type: 'primary',
};

BaseButton.propTypes = {
    id: PropTypes.string,
    url: PropTypes.string,
    onClick: PropTypes.func,
    external: PropTypes.bool,
    disabled: PropTypes.bool,
    hasIconOnly: PropTypes.bool,
    altText: PropTypes.string,
    ariaControls: PropTypes.string,
    ariaExpanded: PropTypes.string,
    tabIndex: PropTypes.number,
    className: PropTypes.string,
    onMouseEnter: PropTypes.func,
    onMouseLeave: PropTypes.func,
    type: PropTypes.oneOf(BUTTON_TYPES),
};

export default BaseButton;
