import React from 'react';
import { ImageCard } from '../index';
import PropTypes from 'prop-types';

const BiographyCard = (props) => {
    const { ctaText, content } = props;

    return (
        <div className="ak-biography-card">
            <ImageCard
                headingLevel="h2"
                imageSize="contain"
                alignment="center"
                ctaText={ctaText}
                title={content.title}
                description={content.job}
                ctaLink={content.bioPath}
                mainImageReference={content.bioImage}
                mainImageAltText={content.bioImageAltText}
            />
        </div>
    );
};

BiographyCard.displayName = 'BiographyCard';

BiographyCard.propTypes = {
    ctaText: PropTypes.string,
    content: PropTypes.shape({
        job: PropTypes.string,
        title: PropTypes.string,
        bioPath: PropTypes.string,
        bioImage: PropTypes.string,
        bioImageAltText: PropTypes.string,
    }),
};

export default BiographyCard;
