import React from 'react';
import { BaseImage, BaseButton, BaseText, BaseTitle } from '../base';
import { linkService } from '@konrad/reweb-aem';
import PropTypes from 'prop-types';
import { IMAGE_WIDTHS } from '../../lib/constants';

const ImageCard = (props) => {
    const {
        alignment,
        mainImageReference,
        mainImageAltText,
        title,
        headingLevel,
        description,
        // thumbnailReference,
        // thumbnailText,
        // thumbnailAltText,
        ctaLink,
        ctaText,
        ctaTarget,
        imageSize,
    } = props;

    const headingOverride = alignment === 'left' ? 'h4' : 'h3';
    return (
        <div className="ak-image-card">
            <div className={`ak-image-card__container ${alignment}`}>
                {mainImageReference && (
                    <BaseImage
                        className="ak-image-card__image"
                        url={mainImageReference}
                        altText={mainImageAltText}
                        size={imageSize ? imageSize : 'cover'}
                    />
                )}
                <div className="ak-image-card__content">
                    {title && (
                        <BaseTitle
                            className="ak-image-card__title"
                            text={title}
                            headingLevel={headingLevel}
                            overrideStyle={headingOverride}
                        />
                    )}
                    {description && (
                        <BaseText
                            className="ak-image-card__description"
                            text={description}
                            level="3"
                        />
                    )}
                    {/* {thumbnailReference && thumbnailText &&
                <div className='ak-image-card__profile'>
                    <BaseImage className='ak-image-card__profile-thumbnail' url={thumbnailReference}
                               altText={thumbnailAltText}/>
                    <BaseText className='ak-image-card__profile-name' text={thumbnailText}
                              level='4'/>
                </div>} */}
                </div>
                {ctaLink && (
                    <BaseButton
                        className="ak-image-card__cta"
                        url={linkService.get(ctaLink)}
                        external={ctaTarget === 'external'}
                        type="tertiary"
                    >
                        <BaseText text={ctaText} type="cta" />
                    </BaseButton>
                )}
            </div>
        </div>
    );
};

ImageCard.displayName = 'ImageCard';

ImageCard.propTypes = {
    title: PropTypes.string,
    ctaLink: PropTypes.string,
    ctaText: PropTypes.string,
    alignment: PropTypes.string,
    ctaTarget: PropTypes.string,
    description: PropTypes.string,
    headingLevel: PropTypes.string,
    thumbnailText: PropTypes.string,
    mainImageAltText: PropTypes.string,
    thumbnailAltText: PropTypes.string,
    mainImageReference: PropTypes.string,
    thumbnailReference: PropTypes.string,
    imageSize: PropTypes.oneOf(IMAGE_WIDTHS),
};

export default ImageCard;
