import React from 'react';
import { BaseModal, BaseText, BaseButton } from '../base';
import PropTypes from 'prop-types';
import { linkService, t } from '@konrad/reweb-aem';

const PlanModal = (props) => {
    const {
        url,
        zipCode,
        modalShow,
        modalTitle,
        utilities,
        handleModalClose,
        modalDescription,
    } = props;

    const parsedUrl = linkService.get(url);

    return (
        <BaseModal
            modalState={modalShow}
            handleClose={handleModalClose}
            id="ak-plan-modal__wrapper"
            isContentModal
            title={modalTitle}
        >
            <div className="ak-plan-modal__modal-content">
                <BaseText
                    className="ak-plan-modal__description"
                    text={modalDescription}
                    level="4"
                    alignment="left"
                />
                {utilities && (
                    <ul className="ak-plan-modal__utilities-wrapper">
                        {utilities.map((utility, index) => (
                            <li key={index}>
                                <BaseButton
                                    fluid
                                    url={`${window.location.origin}${parsedUrl}?zip=${zipCode}&edc=${utility.code}`}
                                    type="primary"
                                    className="ak-plan-modal__utility"
                                    altText={t(utility.name)}
                                    children={utility.name}
                                />
                            </li>
                        ))}
                    </ul>
                )}
            </div>
        </BaseModal>
    );
};

PlanModal.displayName = 'PlanModal';

PlanModal.propTypes = {
    modalShow: PropTypes.bool,
    handleModalClose: PropTypes.func,
    modalDescription: PropTypes.string,
    modalTitle: PropTypes.string,
    utilities: PropTypes.array,
};

export default PlanModal;
