import React from 'react';
import PropTypes from 'prop-types';
import { BaseIcon, BaseLink, BaseText } from '../base';
import { t, linkService } from '@konrad/reweb-aem';
import withBreakpoint from '../../lib/utils/withBreakpoint';

const CommunityTable = (props) => {
    const { programs, column1Title, column2Title, column3Title, column4Title, isDesktop } = props;

    const renderRows = (row, index) => {
        return (
            <tr className="ak-community-table__row" key={index} role="row">
                <td className="ak-community-table__cell" role="cell">
                    <BaseText level="2" wrapper="span">
                        {row.communityName}
                    </BaseText>
                    {!isDesktop && (
                        <BaseText
                            className="ak-community-table__cell--gray"
                            level="2"
                            wrapper="span"
                        >
                            {row.county}
                        </BaseText>
                    )}
                </td>
                {isDesktop && (
                    <td className="ak-community-table__cell" role="cell">
                        <BaseText level="2" wrapper="span">
                            {row.county}
                        </BaseText>
                    </td>
                )}
                {isDesktop && (
                    <td className="ak-community-table__cell" role="cell">
                        <BaseText level="2" wrapper="span">
                            {row.utility}
                        </BaseText>
                    </td>
                )}
                {isDesktop && (
                    <td className="ak-community-table__cell" role="cell">
                        <BaseText level="2" wrapper="span">
                            {row.state}
                        </BaseText>
                    </td>
                )}
                <td className="ak-community-table__cell" role="cell">
                    <BaseLink
                        id={`row-link-${index}`}
                        className="ak-community-table__row-link"
                        url={linkService.get(row.url)}
                        altText={`${t('View more about ')} ${row.communityName}`}
                    >
                        <BaseIcon name="chevron-right" />
                    </BaseLink>
                </td>
            </tr>
        );
    };

    return (
        <table className="ak-community-table" role="table">
            <caption className="sr-only">{t('Community Programs')}</caption>
            <tbody>
                <tr
                    className="ak-community-table__header ak-community-table__row ak-community-table__row--header"
                    role="row"
                >
                    <th
                        className="ak-community-table__cell ak-community-table__cell--header"
                        role="columnheader"
                        scope="col"
                    >
                        <BaseText level="3" wrapper="span">
                            {column1Title}
                        </BaseText>
                    </th>
                    {isDesktop && (
                        <th
                            className="ak-community-table__cell ak-community-table__cell--header"
                            role="columnheader"
                            scope="col"
                        >
                            <BaseText level="3" wrapper="span">
                                {column2Title}
                            </BaseText>
                        </th>
                    )}
                    {isDesktop && (
                        <th
                            className="ak-community-table__cell ak-community-table__cell--header"
                            role="columnheader"
                            scope="col"
                        >
                            <BaseText level="3" wrapper="span">
                                {column3Title}
                            </BaseText>
                        </th>
                    )}
                    {isDesktop && (
                        <th
                            className="ak-community-table__cell ak-community-table__cell--header"
                            role="columnheader"
                            scope="col"
                        >
                            <BaseText level="3" wrapper="span">
                                {column4Title}
                            </BaseText>
                        </th>
                    )}
                    <th
                        className="ak-community-table__cell ak-community-table__cell--header"
                        role="columnheader"
                        scope="col"
                    >
                        <span className="sr-only">{t('links')}</span>
                    </th>
                </tr>
                {programs.map(renderRows)}
            </tbody>
        </table>
    );
};

CommunityTable.displayName = 'CommunityTable';

CommunityTable.propTypes = {
    column1Title: PropTypes.string.isRequired,
    column2Title: PropTypes.string.isRequired,
    column3Title: PropTypes.string.isRequired,
    programs: PropTypes.array,
};

export default withBreakpoint(CommunityTable);
