import React, { useState } from 'react';
import cx from 'classnames';
import PropTypes from 'prop-types';
import { BaseIcon } from '../';

const BaseSelect = React.forwardRef((props, ref) => {
    const {
        id,
        value,
        error,
        label,
        onBlur,
        onFocus,
        options,
        onChange,
        disabled,
        required,
        className,
        placeholder,
        onMouseEnter,
        onMouseleave,
    } = props;

    let [open, setOpened] = useState(false);
    let [justChanged, setJustChanged] = useState(false);

    const preOnChange = (e) => {
        let elem = document.getElementById(id);
        elem.classList.add('selected');
        setJustChanged(true);
        setOpened(false);
        if (onChange) onChange(e.target.value, e);
    };

    const preOnBlur = (e) => {
        if (open) {
            setOpened(false);
        }
        if (onBlur) onBlur(e.target.value, e);
    };

    const preOnFocus = (e) => {
        if (onFocus) onFocus(e.target.value, e);
    };

    const preOnMouseEnter = (e) => {
        if (onMouseEnter) onMouseEnter(e.target.value, e);
    };

    const preOnMouseLeave = (e) => {
        if (onMouseleave) onMouseleave(e.target.value, e);
    };

    const handleClick = (e) => {
        if (!justChanged) {
            setOpened(!open);
        }
        setJustChanged(false);
    };

    const onKeyDown = (e) => {
        if (e.keyCode === 13 || e.keyCode === 32) {
            setOpened(true);
        }
    };

    return (
        <div className={cx('base-select', className)}>
            {label ? (
                <label className={'base-input__label'} htmlFor={id}>
                    {label}
                </label>
            ) : (
                <></>
            )}
            <div className={cx('base-select__container', { open: open })}>
                <select
                    id={id}
                    name={id}
                    ref={ref}
                    value={value}
                    onBlur={preOnBlur}
                    required={required}
                    disabled={disabled}
                    onFocus={preOnFocus}
                    onClick={handleClick}
                    onKeyDown={onKeyDown}
                    onChange={preOnChange}
                    onMouseEnter={preOnMouseEnter}
                    onMouseLeave={preOnMouseLeave}
                    placeholder={placeholder}
                    aria-invalid={!!error}
                    aria-describedby={error ? `${id}-error` : undefined}
                    className={cx(
                        'base-select__select',
                        { 'base-select__select--invalid': error },
                        { selected: value }
                    )}
                >
                    <option
                        value={placeholder}
                        defaultValue={true}
                        className={cx('base-select__option', 'base-select__default')}
                    >
                        {placeholder}
                    </option>
                    {options &&
                        options.map((option, i) => {
                            return (
                                <option
                                    key={i}
                                    value={option.value ? option.value : option.option}
                                    className={cx('base-select__option')}
                                >
                                    {option.option}
                                </option>
                            );
                        })}
                </select>
                <span className={cx('base-select__icons')}>
                    <BaseIcon
                        className={'base-select__icon base-select__icon--open'}
                        name={'chevron-up'}
                    />
                    <BaseIcon
                        className={'base-select__icon base-select__icon--closed'}
                        name={'chevron-down'}
                    />
                </span>
            </div>
        </div>
    );
});

BaseSelect.displayName = 'BaseSelect';

BaseSelect.propTypes = {
    id: PropTypes.string,
    onBlur: PropTypes.func,
    label: PropTypes.string,
    value: PropTypes.string,
    onFocus: PropTypes.func,
    disabled: PropTypes.bool,
    onChange: PropTypes.func,
    required: PropTypes.bool,
    options: PropTypes.array,
    className: PropTypes.string,
    onMouseEnter: PropTypes.func,
    onMouseleave: PropTypes.func,
    placeholder: PropTypes.string,
    ariaLabelList: PropTypes.string,
    questionPlaceholder: PropTypes.string,
    error: PropTypes.oneOfType([PropTypes.string, PropTypes.bool]),
};

export default BaseSelect;
