import React from 'react';
import { BaseTitle } from '../base';

const FaqFrame = (props) => {
    const { faqTitle, children } = props;

    return (
        <div className="ak-faq-frame">
            <div className="ak-faq-frame__container">
                <div className="ak-faq-frame__content">
                    <BaseTitle className="ak-faq-frame__title" text={faqTitle} overrideStyle="h3" />
                    {children}
                </div>
            </div>
        </div>
    );
};

FaqFrame.displayName = 'FaqFrame';

export default FaqFrame;
