import React from 'react';
import cx from 'classnames';
import uuidv4 from 'uuid/v4';
import { t, linkService } from '@konrad/reweb-aem';
import withBreakpoint from '../../lib/utils/withBreakpoint';
import { BaseLink, BaseText, BaseImage, Box } from '../base';

const renderFooterCol = (column) => {
    return (
        <div key={uuidv4()} className="ak-footer__column-wrapper">
            <BaseText type="cta" text={column.category} className="ak-footer__column-title" />
            <ul className="ak-footer__column-elements">
                {column.navigation.map((link) => (
                    <li className="ak-footer__column-link" key={link.text}>
                        <BaseLink external={link.target === 'external'} url={link.url}>
                            <BaseText level="4" text={link.text} wrapper="span" />
                        </BaseLink>
                    </li>
                ))}
            </ul>
        </div>
    );
};

const renderPrivacyLinks = (privacyLink) => {
    return (
        <div key={uuidv4()} className="ak-footer__privacy-link">
            <BaseLink external={privacyLink.target === 'external'} url={privacyLink.url}>
                <BaseText level="4" text={privacyLink.text} wrapper="span" />
            </BaseLink>
        </div>
    );
};

const renderSocialMediaLinks = (socialMediaLink) => {
    return (
        <div key={uuidv4()}>
            <BaseLink
                className="ak-footer__social-link"
                external={socialMediaLink.target === 'external'}
                url={socialMediaLink.url}
            >
                <BaseImage
                    url={socialMediaLink.icon}
                    altText={socialMediaLink.altText}
                    size="full"
                />
            </BaseLink>
        </div>
    );
};
const Footer = (props) => {
    const {
        isDesktop,
        logoUrl,
        logoIcon,
        logoAltText,
        copyright,
        contactIcon,
        contactInfo,
        contactPhone,
        optionalContactInfo,
        optionalContactIcon,
        optionalContactPhone,
        multifieldPrivacy,
        multifieldSocial,
        multifieldCategories,
        hoursOfOperation,
    } = props;

    const hasOptionalContact = optionalContactIcon && optionalContactInfo && optionalContactPhone;

    const mainNavigation =
        multifieldCategories &&
        multifieldCategories.map((category) => {
            if (category.multifieldNavigationItems) {
                category.navigation = category.multifieldNavigationItems.map((navigation) => {
                    navigation.url = linkService.get(navigation.url);
                    return navigation;
                });
            }
            return category;
        });

    const privacyNavigation =
        multifieldPrivacy &&
        multifieldPrivacy.map((privacy) => {
            privacy.url = linkService.get(privacy.url);
            return privacy;
        });

    const socialNavigation =
        multifieldSocial &&
        multifieldSocial.map((social) => {
            social.url = linkService.get(social.url);
            return social;
        });

    const footerLogo = () => {
        return (
            <BaseLink url={linkService.get(logoUrl)}>
                <span className="sr-only">{logoAltText ? logoAltText : t('Logo')}</span>
                <BaseImage className="ak-footer__logo" url={logoIcon} />
            </BaseLink>
        );
    };

    return (
        <footer className="ak-footer">
            <div className="ak-footer__navigation-wrapper">
                <div className="ak-footer__curve"></div>
                <div className="ak-footer__navigation">
                    {isDesktop ? (
                        <Box className="ak-footer__columns">
                            {mainNavigation &&
                                mainNavigation.map((column) => renderFooterCol(column))}
                        </Box>
                    ) : (
                        footerLogo()
                    )}
                    <hr className="ak-footer__separator" />
                    <Box
                        direction={isDesktop ? 'row' : 'col'}
                        alignment={isDesktop ? 'center' : 'left'}
                        className="ak-footer__information"
                    >
                        {isDesktop && footerLogo()}
                        <Box direction={isDesktop ? 'row' : 'col'} className="ak-footer__contacts">
                            <div className="ak-footer__contact">
                                <BaseImage url={contactIcon} className="ak-footer__contact-image" />
                                <BaseText
                                    type="cta"
                                    text={contactInfo}
                                    className="ak-footer__contact-info"
                                />
                                <BaseLink
                                    url={`tel:+${contactPhone && contactPhone.replace(/-/g, '')}`}
                                >
                                    <BaseText level="4" text={contactPhone} wrapper="span" />
                                </BaseLink>
                            </div>
                            {hasOptionalContact && (
                                <div
                                    className={cx('ak-footer__contact', {
                                        'ak-footer__contact--extra': isDesktop,
                                    })}
                                >
                                    <BaseImage
                                        url={optionalContactIcon}
                                        className="ak-footer__contact-image"
                                    />
                                    <BaseText
                                        type="cta"
                                        text={optionalContactInfo}
                                        className="ak-footer__contact-info"
                                    />
                                    <BaseLink
                                        url={`tel:+${
                                            optionalContactPhone &&
                                            optionalContactPhone.replace(/-/g, '')
                                        }`}
                                    >
                                        <BaseText
                                            level="4"
                                            text={optionalContactPhone}
                                            wrapper="span"
                                        />
                                    </BaseLink>
                                </div>
                            )}
                            {!!hoursOfOperation && (
                                <BaseText
                                    level="4"
                                    alignment={isDesktop ? 'right' : 'left'}
                                    text={hoursOfOperation}
                                    className="ak-footer__hours-of-operation"
                                />
                            )}
                        </Box>
                    </Box>
                    {!isDesktop && (
                        <hr className="ak-footer__separator ak-footer__separator--no-margin" />
                    )}
                </div>
            </div>
            <div className="ak-footer__disclaimer-wrapper">
                <Box
                    direction={isDesktop ? 'row' : 'col'}
                    alignment="center"
                    className="ak-footer__disclaimer"
                >
                    <div className={'ak-footer__disclaimer-left'}>
                        <div className="ak-footer__copyright">
                            <BaseText level="4" text={copyright} wrapper="span" />
                        </div>
                        <Box>
                            {privacyNavigation &&
                                privacyNavigation.map((privacyLink) =>
                                    renderPrivacyLinks(privacyLink)
                                )}
                        </Box>
                    </div>
                    <Box className={'ak-footer__disclaimer-right'}>
                        {socialNavigation &&
                            socialNavigation.map((socialLink) =>
                                renderSocialMediaLinks(socialLink)
                            )}
                    </Box>
                </Box>
            </div>
        </footer>
    );
};

Footer.displayName = 'Footer';

export default withBreakpoint(Footer);
