import React, { useEffect, useState } from 'react';
import { linkService } from '@konrad/reweb-aem';
import Cookies from 'universal-cookie';
import { HEADER_BANNER_COOKIES_EXPIRE_TIME } from '../../lib/constants.js';
import BaseTitle from '../base/base-title/base-title';
import BaseText from '../base/base-text/base-text';
import BaseLink from '../base/base-link/base-link';
import BaseIcon from '../base/base-icon/base-icon';

const HeaderBanner = ({
    displayBanner,
    bannerTitle,
    titleHeadingLevel,
    bannerDescription,
    bannerCtaLabel,
    bannerCtaLink,
    bannerCtaLinkTarget,
    isEdit,
    headerHeight,
    handleResize,
}) => {
    const cookies = new Cookies();
    const [showBanner, setShowBanner] = useState(displayBanner);

    const dismissBanner = () => {
        setShowBanner(false);
        cookies.set('isHeaderBannerDisabled', true, { maxAge: HEADER_BANNER_COOKIES_EXPIRE_TIME });
    };

    useEffect(() => {
        handleResize();

        const isHeaderBannerDisabled = cookies.get('isHeaderBannerDisabled');
        setShowBanner(showBanner && !isHeaderBannerDisabled);

        window.addEventListener('resize', handleResize);

        return () => {
            window.removeEventListener('resize', handleResize);
        };
    }, []);

    useEffect(() => {
        if (!isEdit) {
            document.getElementById('mainContent').style.paddingTop = headerHeight + 'px';
        }
    }, [headerHeight]);

    useEffect(() => {
        handleResize();
    }, [showBanner]);

    return showBanner ? (
        <div className="ak-header__header-banner">
            <div className="ak-header__header-banner-container">
                <button
                    aria-label="Dismiss header banner"
                    className="ak-header__dismiss-banner"
                    onClick={dismissBanner}
                >
                    <BaseIcon name="close-sign" />
                </button>
                <div className="ak-header__banner-title">
                    <BaseTitle
                        headingLevel={titleHeadingLevel}
                        text={bannerTitle}
                        overrideStyle="cta"
                    />
                </div>
                <div className="ak-header__banner-description">
                    <BaseText wrapper={'p'} text={bannerDescription} level="4" isRichText />
                </div>
                <div className="ak-header__banner-cta">
                    <BaseLink
                        className="ak-header__banner-cta-link"
                        url={linkService.get(bannerCtaLink)}
                        external={bannerCtaLinkTarget === 'external'}
                    >
                        <BaseText text={bannerCtaLabel} type="cta" wrapper="span" />
                    </BaseLink>
                </div>
            </div>
        </div>
    ) : null;
};

export default HeaderBanner;
