import React from 'react';
import PropTypes from 'prop-types';

const StatusBar = (props) => {
    const { current } = props;

    return (
        <div className={'ak-enrollment-frame__status-bar'}>
            <div
                className={`ak-enrollment-frame__filled ak-enrollment-frame__filled-${current}`}
            ></div>
        </div>
    );
};

StatusBar.displayName = 'StatusBar';

StatusBar.propTypes = {
    current: PropTypes.number,
};

export default StatusBar;
