import React from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';
import InputMask from 'react-input-mask';

const BaseInput = React.forwardRef((props, ref) => {
    const {
        id,
        type,
        mask,
        value,
        error,
        label,
        onBlur,
        onFocus,
        onChange,
        disabled,
        required,
        maskChar,
        className,
        placeholder,
        onMouseEnter,
        onMouseleave,
        ariaLabel,
    } = props;

    let TagName = mask ? InputMask : 'input';
    let maskProps = {};

    if (mask) {
        maskProps.mask = mask;
        maskProps.maskChar = maskChar;
    }

    const preOnChange = (e) => {
        if (onChange) onChange(e.target.value, e);
    };

    const preOnBlur = (e) => {
        if (onBlur) onBlur(e.target.value, e);
    };

    const preOnFocus = (e) => {
        if (onFocus) onFocus(e.target.value, e);
    };

    const preOnMouseEnter = (e) => {
        if (onMouseEnter) onMouseEnter(e.target.value, e);
    };

    const preOnMouseLeave = (e) => {
        if (onMouseleave) onMouseleave(e.target.value, e);
    };

    const input = (
        <TagName
            id={id}
            name={id}
            ref={!mask ? ref : undefined}
            type={type}
            value={value}
            onBlur={preOnBlur}
            required={required}
            disabled={disabled}
            onFocus={preOnFocus}
            onChange={preOnChange}
            onMouseEnter={preOnMouseEnter}
            onMouseLeave={preOnMouseLeave}
            placeholder={placeholder}
            aria-invalid={!!error}
            aria-label={ariaLabel}
            aria-describedby={error ? `${id}-error` : undefined}
            {...maskProps}
            className={cx('base-input__input', { 'base-input__input--invalid': error }, className)}
        >
            {mask && ((inputProps) => <input ref={ref} {...inputProps} />)}
        </TagName>
    );

    return label ? (
        <div className="base-input">
            <label className={'base-input__label'} htmlFor={id}>
                {label}
            </label>
            {input}
        </div>
    ) : (
        input
    );
});

BaseInput.defaultProps = {
    type: 'text',
    placeholder: '',
};

BaseInput.propTypes = {
    id: PropTypes.string,
    type: PropTypes.string,
    mask: PropTypes.string,
    onBlur: PropTypes.func,
    onFocus: PropTypes.func,
    label: PropTypes.string,
    onChange: PropTypes.func,
    disabled: PropTypes.bool,
    required: PropTypes.bool,
    maskChar: PropTypes.string,
    className: PropTypes.string,
    onMouseEnter: PropTypes.func,
    onMouseleave: PropTypes.func,
    placeholder: PropTypes.string,
    ariaLabel: PropTypes.string,
    error: PropTypes.oneOfType([PropTypes.string, PropTypes.bool]),
    value: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
};

export default BaseInput;
