import React, { useState, useRef } from 'react';
import ReCAPTCHA from 'react-google-recaptcha';
import {
    BaseText,
    BaseTitle,
    BaseRadioButton,
    BaseInput,
    BaseButton,
    BaseTextArea,
    BaseSelect,
} from '../../../base';
import { linkService, t } from '@konrad/reweb-aem';
import uuidv4 from 'uuid/v4';
import { isRequired, emailIsValid, phoneNumberValid } from '../../../../lib/fieldValidations';
import PropTypes from 'prop-types';
import { HEADING_LEVELS, CAPTCHA_VALUE } from '../../../../lib/constants';
import akronAPI from '../../../../api';

const Form = (props) => {
    const {
        headingLevel,
        firstNameLabel,
        firstNamePlaceholder,
        lastNameLabel,
        lastNamePlaceholder,
        phoneNumberLabel,
        phoneNumberPlaceholder,
        emailAddressLabel,
        emailAddressPlaceholder,
        companyLabel,
        companyPlaceholder,
        questionLabel,
        questionPlaceholder,
        multifieldQuestionConcerningOptions,
        customerTypeLabel,
        customerTypeBusiness,
        customerTypeResidential,
        questionsOrCommentsLabel,
        questionsOrCommentsPlaceholder,
        ctaLabel,
        ctaLink,
        ctaLinkTarget,
        captchaToken,
    } = props;

    const formId = uuidv4();
    const firstNameId = 'firstName-' + formId;
    const lastNameId = 'lastName-' + formId;
    const phoneNumberId = 'phoneNumber-' + formId;
    const emailId = 'emailAddress-' + formId;
    const companyId = 'company-' + formId;
    const questionId = 'question-' + formId;
    const customerTypeId = 'customerType-' + formId;
    const questionOrCommentsId = 'questionOrComments-' + formId;

    const businessValue = 'business';
    const residentialValue = 'residential';

    //Error message states for the required inputs
    const [firstNameErrorMessage, setFirstNameErrorMessage] = useState('');
    const [lastNameErrorMessage, setLastNameErrorMessage] = useState('');
    const [phoneNumberErrorMessage, setPhoneNumberErrorMessage] = useState('');
    const [emailAddressErrorMessage, setEmailAddressErrorMessage] = useState('');
    const [questionErrorMessage, setQuestionErrorMessage] = useState('');
    const [questionsOrCommentsErrorMessage, setQuestionsOrCommentsErrorMessage] = useState('');
    const [sentFormErrorMessage, setSentFormErrorMessage] = useState('');
    const [captchaErrorMessage, setCaptchaErrorMessage] = useState('');
    const [loading, setLoading] = useState(false);

    const [firstName, setFirstName] = useState('');
    const [lastName, setLastName] = useState('');
    const [phoneNumber, setPhoneNumber] = useState('');
    const [emailAddress, setEmail] = useState('');
    const [company, setCompany] = useState('');
    const [question, setQuestion] = useState('');
    const [customerType, setCustomerType] = useState(businessValue);
    const [questionsOrComments, setQuestionsOrComments] = useState('');
    const [captcha, setCaptcha] = useState('');
    const recaptchaRef = useRef(null);

    //Validations for required inputs
    const requiredFields = [isRequired()];
    const emailValid = [isRequired(), emailIsValid()];
    const phoneValid = [isRequired(), phoneNumberValid()];

    /**
     *  Returns if the input is valid
     * @param value to validate
     * @param validationArray According to the input type it'll validate
     * @returns {*}
     */
    const validateInput = (value, validationArray) => {
        let validationMessage;

        for (let validator in validationArray) {
            validationMessage = validationArray[validator](value);

            if (validationMessage) {
                break;
            }
        }
        return validationMessage;
    };

    /**
     * Function which sets the error message state if needed
     * @param {*} validateArray Array of functions needed to validate input
     * @param {*} value Value to be validated
     * @param {*} setErrorMessageFunction Function which will set the error message
     */
    const validate = (validateArray, value, setErrorMessageFunction) => {
        let validationMessage = validateInput(value, validateArray);

        if (validationMessage) {
            setErrorMessageFunction(validationMessage);
            return false;
        } else {
            setErrorMessageFunction('');
            return true;
        }
    };

    //Validates the required form inputs
    const validateInputs = () => {
        let form = document.forms[formId];
        let elements = form.querySelectorAll('[required]');
        let valid = true;
        elements.forEach((element) => {
            let id = element.id;

            switch (id) {
                case firstNameId:
                    if (!validate(requiredFields, firstName, setFirstNameErrorMessage)) {
                        if (valid) {
                            element.focus();
                        }
                        valid = false;
                    }
                    break;
                case lastNameId:
                    if (!validate(requiredFields, lastName, setLastNameErrorMessage)) {
                        if (valid) {
                            element.focus();
                        }
                        valid = false;
                    }
                    break;
                case phoneNumberId:
                    if (!validate(phoneValid, phoneNumber, setPhoneNumberErrorMessage)) {
                        if (valid) {
                            element.focus();
                        }
                        valid = false;
                    }
                    break;
                case emailId:
                    if (!validate(emailValid, emailAddress, setEmailAddressErrorMessage)) {
                        if (valid) {
                            element.focus();
                        }
                        valid = false;
                    }
                    break;
                case questionId:
                    if (!validate(requiredFields, question, setQuestionErrorMessage)) {
                        if (valid) {
                            element.focus();
                        }
                        valid = false;
                    }
                    break;
                case questionOrCommentsId:
                    if (
                        !validate(
                            requiredFields,
                            questionsOrComments,
                            setQuestionsOrCommentsErrorMessage
                        )
                    ) {
                        if (valid) {
                            element.focus();
                        }
                        valid = false;
                    }
                    break;
                default:
                    break;
            }
        });

        if (captcha && captcha !== '') {
            setCaptchaErrorMessage('');
        } else {
            valid = false;
            setCaptchaErrorMessage(t('Please verify that you are a human'));
        }

        return valid;
    };

    //Submit function
    const handleFormSubmit = (e) => {
        e.preventDefault();
        let contactForm = {};
        setSentFormErrorMessage('');

        if (validateInputs()) {
            contactForm = {
                first_name: firstName,
                last_name: lastName,
                phone_number: phoneNumber,
                email_address: emailAddress,
                company: company,
                question_concerning: question,
                customerType: customerType,
                question_or_comment: questionsOrComments,
                [CAPTCHA_VALUE]: captcha,
            };

            setLoading(true);
            akronAPI.contactForm(contactForm).then(
                (result) => redirect(result),
                (error) => showErrorMessage(error)
            );
        }
    };

    const redirect = (result) => {
        setLoading(false);
        if (ctaLinkTarget === 'internal') {
            window.location.href = linkService.get(ctaLink);
        } else {
            window.open(linkService.get(ctaLink), '_blank');
        }
    };

    const showErrorMessage = (error) => {
        setLoading(false);
        setCaptcha('');
        recaptchaRef && recaptchaRef.current && recaptchaRef.current.reset();
        setSentFormErrorMessage(t('Email could not be sent'));
    };

    return (
        <form
            noValidate
            id={formId}
            name={formId}
            onSubmit={handleFormSubmit}
            className={'ak-contact-us__form'}
        >
            <fieldset className={'ak-contact-us__fieldset'}>
                <div className={'ak-contact-us__input ak-contact-us__input--full'}>
                    <BaseText
                        className={'ak-contact-us__subtitle'}
                        level={'4'}
                        wrapper={'div'}
                        text={t('Online form')}
                    />
                </div>
                <div className={'ak-contact-us__input ak-contact-us__input--full'}>
                    <BaseTitle
                        headingLevel={headingLevel}
                        overrideStyle={'h4'}
                        text={t('Send us a message')}
                    />
                </div>
                <div className={'ak-contact-us__row'}>
                    <div className={'ak-contact-us__input'}>
                        <BaseInput
                            required
                            type={'text'}
                            id={firstNameId}
                            value={firstName}
                            label={firstNameLabel}
                            error={firstNameErrorMessage}
                            placeholder={firstNamePlaceholder}
                            onChange={(val) => setFirstName(val)}
                        />
                        {firstNameErrorMessage && (
                            <BaseText
                                id={`${firstNameId}-error`}
                                className="ak-contact-us__error-message"
                                text={firstNameErrorMessage}
                                level={'3'}
                            />
                        )}
                    </div>
                    <div className={'ak-contact-us__input'}>
                        <BaseInput
                            required
                            type={'text'}
                            id={lastNameId}
                            value={lastName}
                            label={lastNameLabel}
                            error={lastNameErrorMessage}
                            placeholder={lastNamePlaceholder}
                            onChange={(val) => setLastName(val)}
                        />
                        {lastNameErrorMessage && (
                            <BaseText
                                id={`${lastNameId}-error`}
                                className="ak-contact-us__error-message"
                                text={lastNameErrorMessage}
                                level={'3'}
                            />
                        )}
                    </div>
                </div>
                <div className={'ak-contact-us__row'}>
                    <div className={'ak-contact-us__input'}>
                        <BaseInput
                            required
                            maskChar={''}
                            id={phoneNumberId}
                            value={phoneNumber}
                            mask={'(999) 999-9999'}
                            label={phoneNumberLabel}
                            placeholder={phoneNumberPlaceholder}
                            onChange={(val) => setPhoneNumber(val)}
                            error={phoneNumberErrorMessage ? phoneNumberErrorMessage : ''}
                        />
                        {phoneNumberErrorMessage && (
                            <BaseText
                                id={`${phoneNumberId}-error`}
                                className="ak-contact-us__error-message"
                                text={phoneNumberErrorMessage}
                                level={'3'}
                            />
                        )}
                    </div>
                    <div className={'ak-contact-us__input'}>
                        <BaseInput
                            required
                            id={emailId}
                            type={'email'}
                            value={emailAddress}
                            label={emailAddressLabel}
                            onChange={(val) => setEmail(val)}
                            error={emailAddressErrorMessage}
                            placeholder={emailAddressPlaceholder}
                        />
                        {emailAddressErrorMessage && (
                            <BaseText
                                id={`${emailId}-error`}
                                className="ak-contact-us__error-message"
                                text={emailAddressErrorMessage}
                                level={'3'}
                            />
                        )}
                    </div>
                </div>
                <div className={'ak-contact-us__row'}>
                    <div className={'ak-contact-us__input'}>
                        <BaseInput
                            type={'text'}
                            id={companyId}
                            value={company}
                            label={companyLabel}
                            placeholder={companyPlaceholder}
                            onChange={(val) => setCompany(val)}
                        />
                    </div>
                    <div className={'ak-contact-us__input'}>
                        <BaseSelect
                            required
                            id={questionId}
                            value={question}
                            label={questionLabel}
                            error={questionErrorMessage}
                            ariaLabel={`${questionId}-label`}
                            placeholder={questionPlaceholder}
                            onChange={(val) => setQuestion(val)}
                            className={'ak-contact-us__select'}
                            ariaLabelList={`${questionId}-label`}
                            options={multifieldQuestionConcerningOptions}
                        />
                        {questionErrorMessage && (
                            <BaseText
                                id={`${questionId}-error`}
                                className="ak-contact-us__error-message"
                                text={questionErrorMessage}
                                level={'3'}
                            />
                        )}
                    </div>
                </div>
                <div className={'ak-contact-us__row'}>
                    <div className={'ak-contact-us__input'}>
                        <BaseRadioButton
                            direction={'row'}
                            id={customerTypeId}
                            name={customerTypeId}
                            value={customerType}
                            label={customerTypeLabel}
                            onChange={(val) => setCustomerType(val)}
                            className={'ak-contact-us__radio-button'}
                            values={[
                                { value: businessValue, name: customerTypeBusiness },
                                { value: residentialValue, name: customerTypeResidential },
                            ]}
                        />
                    </div>
                </div>
                <div className={'ak-contact-us__row'}>
                    <div className={'ak-contact-us__input ak-contact-us__input--full'}>
                        <BaseTextArea
                            required
                            id={questionOrCommentsId}
                            value={questionsOrComments}
                            label={questionsOrCommentsLabel}
                            error={questionsOrCommentsErrorMessage}
                            onChange={(val) => setQuestionsOrComments(val)}
                            placeholder={questionsOrCommentsPlaceholder}
                        />
                        {questionsOrCommentsErrorMessage && (
                            <BaseText
                                id={`${questionOrCommentsId}-error`}
                                className="ak-contact-us__error-message"
                                text={questionsOrCommentsErrorMessage}
                                level={'3'}
                            />
                        )}
                    </div>
                </div>
                <div className={'ak-contact-us__row'}>
                    <div
                        className={
                            'ak-contact-us__input ak-contact-us__input--full ak-contact-us__captcha'
                        }
                    >
                        <ReCAPTCHA
                            ref={recaptchaRef}
                            sitekey={captchaToken}
                            onChange={(val) => setCaptcha(val)}
                        />
                        {captchaErrorMessage && (
                            <BaseText
                                id="captcha-error"
                                className="ak-contact-us__error-message"
                                text={captchaErrorMessage}
                                level={'3'}
                            />
                        )}
                    </div>
                </div>
                <div className={'ak-contact-us__row ak-contact-us__row--aligned-right'}>
                    <BaseButton
                        className={'ak-contact-us__submit'}
                        loading={loading}
                        altText={t('Submit Contact us Form')}
                        required
                    >
                        {ctaLabel}
                    </BaseButton>
                    {sentFormErrorMessage && (
                        <BaseText
                            id={`submit-error`}
                            className="ak-contact-us__error-message ak-contact-us__error-message--position-end"
                            text={sentFormErrorMessage}
                            level={'3'}
                        />
                    )}
                </div>
            </fieldset>
        </form>
    );
};

Form.displayName = 'Form';

Form.propTypes = {
    headingLevel: PropTypes.oneOf(HEADING_LEVELS),
    firstNameLabel: PropTypes.string,
    firstNamePlaceholder: PropTypes.string,
    lastNameLabel: PropTypes.string,
    lastNamePlaceholder: PropTypes.string,
    phoneNumberLabel: PropTypes.string,
    phoneNumberPlaceholder: PropTypes.string,
    emailAddressLabel: PropTypes.string,
    emailAddressPlaceholder: PropTypes.string,
    companyLabel: PropTypes.string,
    companyPlaceholder: PropTypes.string,
    questionLabel: PropTypes.string,
    questionPlaceholder: PropTypes.string,
    multifieldQuestionConcerningOptions: PropTypes.array,
    customerTypeLabel: PropTypes.string,
    customerTypeBusiness: PropTypes.string,
    customerTypeResidential: PropTypes.string,
    questionsOrCommentsLabel: PropTypes.string,
    questionsOrCommentsPlaceholder: PropTypes.string,
    ctaLabel: PropTypes.string,
    ctaLink: PropTypes.string,
    ctaLinkTarget: PropTypes.string,
};

export default Form;
