import React from 'react';
import cx from 'classnames';
import PropTypes from 'prop-types';

const BaseTextArea = (props) => {
    const {
        id,
        label,
        value,
        error,
        onBlur,
        onFocus,
        onChange,
        disabled,
        required,
        className,
        placeholder,
        onMouseEnter,
        onMouseleave,
    } = props;

    const preOnChange = (e) => {
        if (onChange) onChange(e.target.value, e);
    };

    const preOnBlur = (e) => {
        if (onBlur) onBlur(e.target.value, e);
    };

    const preOnFocus = (e) => {
        if (onFocus) onFocus(e.target.value, e);
    };

    const preOnMouseEnter = (e) => {
        if (onMouseEnter) onMouseEnter(e.target.value, e);
    };

    const preOnMouseLeave = (e) => {
        if (onMouseleave) onMouseleave(e.target.value, e);
    };

    return (
        <div className={cx('base-text-area', className)}>
            {label ? (
                <label className={'base-text-area__label'} htmlFor={id}>
                    {label}
                </label>
            ) : (
                <></>
            )}
            <textarea
                id={id}
                name={id}
                value={value}
                onBlur={preOnBlur}
                required={required}
                disabled={disabled}
                onFocus={preOnFocus}
                onChange={preOnChange}
                onMouseEnter={preOnMouseEnter}
                onMouseLeave={preOnMouseLeave}
                placeholder={placeholder}
                aria-invalid={!!error}
                aria-describedby={error ? `${id}-error` : undefined}
                className={cx('base-text-area__input', { 'base-text-area__input--invalid': error })}
            />
        </div>
    );
};

BaseTextArea.displayName = 'BaseTextArea';

BaseTextArea.defaultProps = {
    placeholder: '',
};

BaseTextArea.propTypes = {
    id: PropTypes.string,
    onBlur: PropTypes.func,
    label: PropTypes.string,
    onFocus: PropTypes.func,
    onChange: PropTypes.func,
    disabled: PropTypes.bool,
    required: PropTypes.bool,
    className: PropTypes.string,
    onMouseEnter: PropTypes.func,
    onMouseleave: PropTypes.func,
    placeholder: PropTypes.string,
    error: PropTypes.oneOfType([PropTypes.string, PropTypes.bool]),
    value: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
};

export default BaseTextArea;
