import React from 'react';
import { BaseButton, BaseTitle, BaseText, BaseImage } from '../base';
import { t } from '@konrad/reweb-aem';
import PropTypes from 'prop-types';

const CommunityFrame = (props) => {
    const { name, subtitle, phoneNumberLabel, phoneNumber, phoneIcon } = props;

    return (
        <div className="ak-community-frame">
            <div className="ak-community-frame__header">
                <div className="ak-community-frame__header-container">
                    <div className="ak-community-frame__header-container__left">
                        <BaseTitle
                            className="ak-community-frame__title"
                            text={name}
                            headingLevel="h1"
                        />
                        <BaseText
                            className="ak-community-frame__subtitle"
                            text={subtitle}
                            level="1"
                        />
                    </div>
                    {phoneNumber && (
                        <div className="ak-community-frame__header-container__right">
                            <BaseText
                                className="ak-community-frame__phone-title"
                                text={phoneNumberLabel}
                                level="3"
                            />
                            <BaseButton
                                className="ak-community-frame__phone-link"
                                url={`tel:${phoneNumber}`}
                                type="tertiary"
                            >
                                <BaseImage
                                    className="ak-community-frame__phone-link-icon"
                                    url={phoneIcon}
                                />
                                <span className="text">{phoneNumber}</span>
                            </BaseButton>
                        </div>
                    )}
                </div>
            </div>
        </div>
    );
};

CommunityFrame.displayName = 'CommunityFrame';

CommunityFrame.defaultProps = {
    firstNameLabel: t('Electric Aggregation Program'),
};

CommunityFrame.propTypes = {
    name: PropTypes.string,
    subtitle: PropTypes.string,
    phoneNumberLabel: PropTypes.string,
    phoneNumber: PropTypes.string,
    phoneIcon: PropTypes.string,
    communityListPhone: PropTypes.string,
};

export default CommunityFrame;
