import React from 'react';
import { TestimonialCarousel, TestimonialCarouselItem } from './elements';
import PropTypes from 'prop-types';

const Testimonial = (props) => {
    const { multifieldItems } = props;

    let render = () => {
        return (
            <>
                {multifieldItems &&
                    multifieldItems.map((a, i) => (
                        <TestimonialCarouselItem key={i} id={i} item={a}></TestimonialCarouselItem>
                    ))}
            </>
        );
    };

    return (
        <TestimonialCarousel render={render} showNavigationCarousel={multifieldItems.length > 1} />
    );
};

Testimonial.displayName = 'Testimonial';

Testimonial.propTypes = {
    multifieldItems: PropTypes.array,
};

export default Testimonial;
