import React from 'react';
import cx from 'classnames';
import { BaseTitle, BaseText, BaseLink, BaseIcon, Box } from '../base';
import withBreakpoint from '../../lib/utils/withBreakpoint';
import { linkService, t } from '@konrad/reweb-aem';

const FaqCategory = (props) => {
    const { title, faqLinks, children, isMobile, faqCategories } = props;

    const renderFaqLink = (link, id) => {
        return (
            <div key={id} className="ak-faq-category__link-wrapper">
                <BaseLink className="ak-faq-category__link" url={linkService.get(link.path)}>
                    <BaseText text={link.title} type="cta" wrapper="span" />
                    <BaseIcon className="ak-faq-category__link-icon" name="chevron-right" />
                </BaseLink>
            </div>
        );
    };

    const renderNavigation = () => {
        return (
            <Box direction={'col'}>
                <ul>
                    {faqCategories.map((category, index) => {
                        return (
                            <li
                                key={index}
                                className={cx('ak-faq-category__navigation-item', {
                                    'ak-faq-category__navigation-item--active': category.active,
                                })}
                            >
                                <BaseLink url={linkService.get(category.path)}>
                                    <BaseText text={category.title} type="cta" wrapper="span" />
                                </BaseLink>
                            </li>
                        );
                    })}
                </ul>
            </Box>
        );
    };

    return (
        <div className="ak-faq-category">
            <Box className="ak-faq-category__container" direction={isMobile ? 'col' : 'row'}>
                {!isMobile && (
                    <nav aria-label={t('Categories Menu')} className="ak-faq-category__navigation">
                        {renderNavigation()}
                    </nav>
                )}
                <div className="ak-faq-category__content">
                    <BaseTitle text={title} overrideStyle="h3" />
                    <div className="ak-faq-category__description">{children}</div>
                    <div className="ak-faq-category__links">{faqLinks.map(renderFaqLink)}</div>
                </div>
            </Box>
        </div>
    );
};

FaqCategory.displayName = 'FaqCategory';

export default withBreakpoint(FaqCategory);
