import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';
import { Box, BaseTitle, BaseText, BaseLink } from '../base';
import parseQueryString from '../../lib/utils/parseQueryString';
import { Pagination, SearchBar } from '../index';
import { t } from '@konrad/reweb-aem';

const SearchPage = (props) => {
    const { title, subtitle, headingLevel, currentSearchTerm, searchResults } = props;
    const params = parseQueryString();
    const [isEmptyState, setIsEmptyState] = useState(true);
    const [currentPage, setCurrentPage] = useState(1);
    const contentRef = React.createRef();

    const pageSize = 6;
    const hasPagination = searchResults.length > pageSize;
    const totalPages = Math.ceil(searchResults.length / pageSize);
    let results = [];

    if (hasPagination) {
        const pageFirstElement = (currentPage - 1) * pageSize;
        const pageLastElement = pageFirstElement + pageSize;
        results = searchResults.slice(pageFirstElement, pageLastElement);
    } else {
        results = searchResults;
    }

    useEffect(() => {
        if (params['q']) {
            setIsEmptyState(params['q'].length <= 0 && true);
        }
    }, [params]);

    const onPageChange = (page) => {
        const articlesContainer = contentRef.current;
        articlesContainer.scrollIntoView(true);
        setCurrentPage(page);
    };

    const shrinkText = (str) => {
        if (str.length > 160) str = `${str.substring(0, 160)}...`;
        return str;
    };

    return (
        <>
            <SearchBar
                placeholder="Search"
                searchResultsPath={window.location.pathname}
                isSearchOpen={true}
            />
            <Box
                direction="col"
                className={cx('ak-search-page__container', {
                    'ak-search-page__container--empty': isEmptyState,
                })}
                ref={contentRef}
            >
                {isEmptyState ? (
                    <Box direction="col" alignment="center" className="ak-search-page__top">
                        <BaseText
                            type="sub"
                            text={subtitle}
                            className="ak-search-page__top--subtitle"
                        />
                        <BaseTitle
                            headingLevel={headingLevel}
                            text={title}
                            overrideStyle="h2"
                            className="ak-search-page__top--title"
                        />
                    </Box>
                ) : (
                    <Box direction="col" alignment="center" className="ak-search-page__top">
                        <BaseText
                            type="sub"
                            text={
                                searchResults.length !== 1
                                    ? `${searchResults.length} Results For`
                                    : `${searchResults.length} Result For`
                            }
                            className="ak-search-page__top--subtitle"
                        />
                        <BaseTitle
                            headingLevel={headingLevel}
                            text={isEmptyState ? title : `“${currentSearchTerm}”`}
                            overrideStyle="h2"
                            className="ak-search-page__top--title"
                        />
                    </Box>
                )}
            </Box>
            <Box direction="col" alignment="left" className="ak-search-page__articles-wrapper">
                {!isEmptyState && (
                    <>
                        <ul>
                            {results.map((article) => (
                                <li className="ak-search-page__article-wrapper">
                                    <BaseLink
                                        key={article.id}
                                        className="ak-search-page__article"
                                        url={article.url}
                                    >
                                        <BaseTitle
                                            text={article.title}
                                            headingLevel="h2"
                                            overrideStyle="h4"
                                            className="ak-search-page__article--title"
                                        />
                                    </BaseLink>
                                    {article.description && (
                                        <BaseText
                                            className="ak-search-page__excerpt"
                                            level="3"
                                            text={shrinkText(article.description)}
                                        />
                                    )}
                                </li>
                            ))}
                        </ul>
                        {hasPagination && (
                            <Pagination
                                sectionName={t('Articles')}
                                currentPage={currentPage}
                                totalPages={totalPages}
                                onPageChange={onPageChange}
                            />
                        )}
                    </>
                )}
            </Box>
        </>
    );
};

SearchPage.displayName = 'SearchPage';

SearchPage.protoTypes = {
    title: PropTypes.string.isRequired,
    subtitle: PropTypes.string,
    headingLevel: PropTypes.string,
};

export default SearchPage;
