import React from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';
import { DIRECTION_OPTIONS } from '../../../lib/constants';

import { BaseIcon } from '../';

const BaseRadioButton = React.forwardRef((props, ref) => {
    const {
        id,
        label,
        value,
        values,
        onBlur,
        onFocus,
        onChange,
        disabled,
        className,
        direction,
        onMouseEnter,
        onMouseleave,
    } = props;

    const preOnChange = (e) => {
        if (onChange) onChange(e.target.value, e);
    };

    const preOnBlur = (e) => {
        if (onBlur) onBlur(e.target.value, e);
    };

    const preOnFocus = (e) => {
        if (onFocus) onFocus(e.target.value, e);
    };

    const preOnMouseEnter = (e) => {
        if (onMouseEnter) onMouseEnter(e.target.value, e);
    };

    const preOnMouseLeave = (e) => {
        if (onMouseleave) onMouseleave(e.target.value, e);
    };

    return (
        <fieldset className={cx('base-radio-button', className)}>
            <legend className={'base-radio-button__legend'}>{label}</legend>
            <div
                className={cx(
                    'base-radio-button__container',
                    `base-radio-button__container--direction-${direction}`
                )}
            >
                {values &&
                    values.map((option, i) => {
                        return (
                            <div key={i} className={cx('base-radio-button__button')}>
                                <input
                                    id={i + id}
                                    name={id}
                                    type={'radio'}
                                    onBlur={preOnBlur}
                                    disabled={disabled}
                                    onFocus={preOnFocus}
                                    value={option.value}
                                    onChange={preOnChange}
                                    onMouseEnter={preOnMouseEnter}
                                    onMouseLeave={preOnMouseLeave}
                                    ref={i === 0 ? ref : undefined}
                                    defaultChecked={option.value === value}
                                    className={cx('base-radio-button__input')}
                                />
                                <label className={'base-radio-button__label'} htmlFor={i + id}>
                                    <BaseIcon
                                        name="radio-button"
                                        className="base-radio-button__icon unselected"
                                    />
                                    <BaseIcon
                                        name="radio-button-selected"
                                        className="base-radio-button__icon selected"
                                    />
                                    <span className="base-radio-button__text">{option.name}</span>
                                </label>
                            </div>
                        );
                    })}
            </div>
        </fieldset>
    );
});

BaseRadioButton.displayName = 'BaseRadioButton';

BaseRadioButton.propTypes = {
    id: PropTypes.string,
    value: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
    text: PropTypes.string,
    name: PropTypes.string,
    disabled: PropTypes.bool,
    required: PropTypes.bool,
    checked: PropTypes.bool,
    className: PropTypes.string,
    direction: PropTypes.oneOf(DIRECTION_OPTIONS),
};

export default BaseRadioButton;
