import React, { Fragment, useState } from 'react';
import { BaseText } from '../../../base';
import { TabsHeader } from '../';

const TabsContent = (props) => {
    const { className, multifieldItems, currentIndex, wcSlots, isEdit, changeTabHandler } = props;

    const [activeIndex, setActiveIndex] = useState(currentIndex);

    const handleClick = (event, index) => {
        event.preventDefault();
        setActiveIndex(index);
    };

    const renderTabContent = (tab, i) => {
        if (tab.component) {
            return tab.component;
        } else {
            return <div key={i} ref={tab.appendRef} />;
        }
    };

    return (
        <>
            <TabsHeader
                className={className}
                multifieldItems={multifieldItems}
                activeIndex={activeIndex}
                changeIndex={handleClick}
                changeTabHandler={changeTabHandler}
            />
            <div className={`${className}__items`}>
                {wcSlots.tabList &&
                    wcSlots.tabList.map((tab, i) => (
                        <Fragment key={i}>
                            {isEdit ? (
                                <>
                                    <div className={`${className}__section-label`}>
                                        <BaseText level={'4'} text={multifieldItems[i].title} />
                                    </div>
                                    {renderTabContent(tab, i)}
                                    {/* <div key={i} ref={tab.appendRef} /> */}
                                </>
                            ) : (
                                <div
                                    className={`${className}__panel`}
                                    style={{ display: activeIndex !== i ? 'none' : undefined }}
                                    ref={tab.ref}
                                    role={'tabpanel'}
                                    tabIndex={'-1'}
                                >
                                    {renderTabContent(tab, i)}
                                    {/* <div key={i} ref={tab.appendRef} /> */}
                                </div>
                            )}
                        </Fragment>
                    ))}
            </div>
        </>
    );
};

TabsContent.displayName = 'TabsContent';

export default TabsContent;
