import React, { useState } from 'react';
import {
    BaseText,
    BaseTitle,
    BaseInput,
    BaseButton,
    BaseIcon,
    BaseInstructions,
} from '../../../base';
import uuidv4 from 'uuid/v4';
import { t } from '@konrad/reweb-aem';
import { emailIsValid, isRequired, phoneNumberValid } from '../../../../lib/fieldValidations';
import PropTypes from 'prop-types';

const One = (props) => {
    const {
        title,

        firstName,
        setFirstName,
        firstNameLabel,
        firstNamePlaceholder,

        lastName,
        setLastName,
        lastNameLabel,
        lastNamePlaceholder,

        phoneNumber,
        setPhoneNumber,
        phoneNumberLabel,
        phoneNumberPlaceholder,

        emailAddress,
        setEmail,
        emailAddressLabel,
        emailAddressPlaceholder,

        companyBusiness,
        setCompanyBusiness,
        companyBusinessLabel,
        companyBusinessPlaceholder,

        backButtonLabel,
        CTALabelOne,
        stepOneInstrImage,
        stepOneInstrTitle,
        stepOneInstrDescription,
        setChangedStep,
        step,
        amountOfSteps,
        setStep,
        goToFinalStep,
        validate,
        formType,
    } = props;

    //Ids and references to the inputs
    const firstNameId = uuidv4();
    const firstNameRef = React.createRef();
    const lastNameId = uuidv4();
    const lastNameRef = React.createRef();
    const phoneNumberId = uuidv4();
    const phoneNumberRef = React.createRef();
    const emailId = uuidv4();
    const emailRef = React.createRef();
    const companyBusinessId = uuidv4();
    const companyBusinessRef = React.createRef();

    //States for the error messages on the inputs
    const [firstNameErrorMessage, setFirstNameErrorMessage] = useState('');
    const [lastNameErrorMessage, setLastNameErrorMessage] = useState('');
    const [phoneNumberErrorMessage, setPhoneNumberErrorMessage] = useState('');
    const [emailAddressErrorMessage, setEmailAddressErrorMessage] = useState('');
    const [companyBusinessErrorMessage, setCompanyBusinessErrorMessage] = useState('');

    //Validations for required inputs
    const requiredFields = [isRequired()];
    const emailValid = [isRequired(), emailIsValid()];
    const phoneValid = [isRequired(), phoneNumberValid()];

    //Boolean variable true if the form type is business, to show the company input
    const displayAndValidateCompanyName = formType ? formType === 'business' : false;

    /**
     * Goes through all the required fields validating the inputs
     * @returns {boolean}
     */
    const validateInputs = () => {
        let valid = true;
        if (!validate(requiredFields, firstName, setFirstNameErrorMessage)) {
            if (valid) {
                firstNameRef && firstNameRef.current.focus();
            }
            valid = false;
        }

        if (!validate(requiredFields, lastName, setLastNameErrorMessage)) {
            if (valid) {
                lastNameRef && lastNameRef.current.focus();
            }
            valid = false;
        }

        if (!validate(phoneValid, phoneNumber, setPhoneNumberErrorMessage)) {
            if (valid) {
                phoneNumberRef && phoneNumberRef.current.focus();
            }
            valid = false;
        }

        if (!validate(emailValid, emailAddress, setEmailAddressErrorMessage)) {
            if (valid) {
                emailRef && emailRef.current.focus();
            }
            valid = false;
        }

        if (
            displayAndValidateCompanyName &&
            !validate(requiredFields, companyBusiness, setCompanyBusinessErrorMessage)
        ) {
            if (valid) {
                companyBusinessRef && companyBusinessRef.current.focus();
            }
            valid = false;
        }
        return valid;
    };

    /**
     * Validates required inputs and changes the step
     * @param e
     */
    const handleNextClick = (e) => {
        e.stopPropagation();
        e.preventDefault();

        if (validateInputs()) {
            setChangedStep && setChangedStep(true);

            if (goToFinalStep) {
                setStep(4);
            } else {
                setStep(step + 1);
            }
        }
    };

    const handleBackClick = (e) => {
        e.stopPropagation();
        e.preventDefault();

        setChangedStep && setChangedStep(true);

        window.history.back();
    };

    return (
        <>
            {step === 1 && (
                <div className="ak-enrollment-frame__contact">
                    <div className="ak-enrollment-frame__container">
                        <BaseText
                            className={'ak-enrollment-frame__label'}
                            level={'4'}
                            text={`${t('Step')} ${step} ${t('of')} ${amountOfSteps}`}
                        />
                        <BaseTitle headingLevel={'h2'} text={title} overrideStyle={'h4'} />
                    </div>

                    <div className="ak-enrollment-frame__container ak-enrollment-frame__container--flex">
                        <div className="ak-enrollment-frame__panel-left">
                            <div className={'ak-enrollment-frame__fields-row'}>
                                <div className={'ak-enrollment-frame__input'}>
                                    <BaseInput
                                        required
                                        type={'text'}
                                        id={firstNameId}
                                        ref={firstNameRef}
                                        value={firstName}
                                        label={firstNameLabel}
                                        error={firstNameErrorMessage}
                                        placeholder={firstNamePlaceholder}
                                        onChange={(val) => setFirstName(val)}
                                    />
                                    <div className="ak-enrollment-frame__error-container">
                                        {firstNameErrorMessage && (
                                            <BaseText
                                                id={`${firstNameId}-error`}
                                                className="ak-enrollment-frame__error-message"
                                                text={firstNameErrorMessage}
                                                level={'3'}
                                            />
                                        )}
                                    </div>
                                </div>
                                <div className={'ak-enrollment-frame__input'}>
                                    <BaseInput
                                        required
                                        type={'text'}
                                        id={lastNameId}
                                        ref={lastNameRef}
                                        value={lastName}
                                        label={lastNameLabel}
                                        error={lastNameErrorMessage}
                                        placeholder={lastNamePlaceholder}
                                        onChange={(val) => setLastName(val)}
                                    />
                                    <div className="ak-enrollment-frame__error-container">
                                        {lastNameErrorMessage && (
                                            <BaseText
                                                id={`${lastNameId}-error`}
                                                className="ak-enrollment-frame__error-message"
                                                text={lastNameErrorMessage}
                                                level={'3'}
                                            />
                                        )}
                                    </div>
                                </div>
                            </div>

                            <div className={'ak-enrollment-frame__fields-row'}>
                                <div className={'ak-enrollment-frame__input'}>
                                    <BaseInput
                                        required
                                        maskChar={''}
                                        type={'text'}
                                        id={phoneNumberId}
                                        ref={phoneNumberRef}
                                        value={phoneNumber}
                                        mask={'(999) 999-9999'}
                                        label={phoneNumberLabel}
                                        error={phoneNumberErrorMessage}
                                        placeholder={phoneNumberPlaceholder}
                                        onChange={(val) => setPhoneNumber(val)}
                                    />
                                    <div className="ak-enrollment-frame__error-container">
                                        {phoneNumberErrorMessage && (
                                            <BaseText
                                                id={`${phoneNumberId}-error`}
                                                className="ak-enrollment-frame__error-message"
                                                text={phoneNumberErrorMessage}
                                                level={'3'}
                                            />
                                        )}
                                    </div>
                                </div>
                                <div className={'ak-enrollment-frame__input'}>
                                    <BaseInput
                                        required
                                        type={'text'}
                                        id={emailId}
                                        ref={emailRef}
                                        value={emailAddress}
                                        label={emailAddressLabel}
                                        error={emailAddressErrorMessage}
                                        placeholder={emailAddressPlaceholder}
                                        onChange={(val) => setEmail(val)}
                                    />
                                    <div className="ak-enrollment-frame__error-container">
                                        {emailAddressErrorMessage && (
                                            <BaseText
                                                id={`${emailId}-error`}
                                                className="ak-enrollment-frame__error-message"
                                                text={emailAddressErrorMessage}
                                                level={'3'}
                                            />
                                        )}
                                    </div>
                                </div>
                            </div>

                            {displayAndValidateCompanyName && (
                                <div className={'ak-enrollment-frame__fields-row'}>
                                    <div className={'ak-enrollment-frame__input'}>
                                        <BaseInput
                                            required
                                            type={'text'}
                                            id={companyBusinessId}
                                            ref={companyBusinessRef}
                                            value={companyBusiness}
                                            label={companyBusinessLabel}
                                            error={companyBusinessErrorMessage}
                                            placeholder={companyBusinessPlaceholder}
                                            onChange={(val) => setCompanyBusiness(val)}
                                        />
                                        <div className="ak-enrollment-frame__error-container">
                                            {companyBusinessErrorMessage && (
                                                <BaseText
                                                    id={`${companyBusinessId}-error`}
                                                    className="ak-enrollment-frame__error-message"
                                                    text={companyBusinessErrorMessage}
                                                    level={'3'}
                                                />
                                            )}
                                        </div>
                                    </div>
                                </div>
                            )}

                            <div className={'ak-enrollment-frame__btns-row'}>
                                <div className={'ak-enrollment-frame__element'}>
                                    {!goToFinalStep ? (
                                        <button
                                            type="button"
                                            className={'ak-enrollment-frame__back-btn'}
                                            onClick={(e) => handleBackClick(e)}
                                        >
                                            <BaseIcon
                                                className={'ak-enrollment-frame__chevron'}
                                                name={'chevron-down'}
                                            />
                                            {backButtonLabel}
                                        </button>
                                    ) : (
                                        <></>
                                    )}
                                </div>

                                <div
                                    className={
                                        'ak-enrollment-frame__element ak-enrollment-frame__input--justified-right'
                                    }
                                >
                                    <BaseButton
                                        className="ak-enrollment-frame__btn"
                                        onClick={(e) => handleNextClick(e)}
                                        altText={t('Next')}
                                        type="primary"
                                    >
                                        {CTALabelOne}
                                    </BaseButton>
                                </div>
                            </div>
                        </div>
                        <div className="ak-enrollment-frame__panel-right">
                            <BaseInstructions
                                fileReference={stepOneInstrImage}
                                explanatoryTitle={stepOneInstrTitle}
                                description={stepOneInstrDescription}
                            />
                        </div>
                    </div>
                </div>
            )}
        </>
    );
};

One.displayName = 'One';

One.propTypes = {
    title: PropTypes.string,

    firstName: PropTypes.string,
    setFirstName: PropTypes.func,
    firstNameLabel: PropTypes.string,
    firstNamePlaceholder: PropTypes.string,

    lastName: PropTypes.string,
    setLastName: PropTypes.func,
    lastNameLabel: PropTypes.string,
    lastNamePlaceholder: PropTypes.string,

    phoneNumber: PropTypes.string,
    setPhoneNumber: PropTypes.func,
    phoneNumberLabel: PropTypes.string,
    phoneNumberPlaceholder: PropTypes.string,

    emailAddress: PropTypes.string,
    setEmail: PropTypes.func,
    emailAddressLabel: PropTypes.string,
    emailAddressPlaceholder: PropTypes.string,

    companyBusiness: PropTypes.string,
    setCompanyBusiness: PropTypes.func,
    companyBusinessLabel: PropTypes.string,
    companyBusinessPlaceholder: PropTypes.string,

    alertCheckbox: PropTypes.bool,
    setAlertCheckbox: PropTypes.func,
    textAlertCheckbox: PropTypes.string,

    backButtonLabel: PropTypes.string,
    CTALabelOne: PropTypes.string,
    stepOneInstrImage: PropTypes.string,
    stepOneInstrTitle: PropTypes.string,
    stepOneInstrDescription: PropTypes.string,
    setChangedStep: PropTypes.func,

    step: PropTypes.number,
    amountOfSteps: PropTypes.number,
    setStep: PropTypes.func,
    goToFinalStep: PropTypes.bool,
    validate: PropTypes.func,

    formType: PropTypes.string,
};

export default One;
