import React, { useEffect, useState, useRef } from 'react';
import cx from 'classnames';
import BaseButton from '../base-button/base-button';
import withBreakpoint from '../../../lib/utils/withBreakpoint';
import { t } from '@konrad/reweb-aem';
import BaseIcon from '../base-icon/base-icon';
import ScrollLock from '../../../lib/utils/scrollLock';
import FocusLock from 'react-focus-lock';
import PropTypes from 'prop-types';

const BaseDrawer = (props) => {
    const { id, children, isMobile, className, drawerState, handleClose } = props;

    const bodyRef = React.createRef();
    const contentRef = React.createRef();
    const [close, setClose] = useState(false);

    useEffect(() => {
        if (drawerState) {
            ScrollLock.lockBody();
            // const currentNode = contentRef.current;
            // const firstFocusableElement = currentNode.querySelectorAll('button, [href], input, select, textarea, [tabindex]:not([tabindex="-1"])')[0];
            // firstFocusableElement && firstFocusableElement.focus();
        }
    }, [drawerState]);

    useEffect(() => {
        if (close) {
            if (!isMobile) {
                const timer = setTimeout(() => {
                    handleClose();
                    ScrollLock.unlockBody();
                    setClose(false);
                }, 400);
                return () => clearTimeout(timer);
            } else {
                handleClose();
                ScrollLock.unlockBody();
                setClose(false);
            }
        }
    }, [close, isMobile, handleClose]);

    const handleClickOutside = ({ target }) => {
        const isClickInside = bodyRef && bodyRef.current.contains(target);
        if (!isClickInside) {
            setClose(true);
        }
    };

    return (
        <>
            {drawerState && (
                <FocusLock>
                    <div className={cx('base-drawer', className)} onClick={handleClickOutside}>
                        <div
                            tabIndex={-1}
                            className={cx({ 'base-drawer__overlay': drawerState })}
                        />
                        <div
                            className={cx('base-drawer__container', {
                                'base-drawer__container--close': close,
                            })}
                        >
                            {!isMobile && (
                                <div className="base-drawer__container-button" ref={bodyRef}>
                                    <BaseButton
                                        ariaExpanded="true"
                                        ariaControls={id}
                                        hasIconOnly
                                        type="tertiary"
                                        onClick={() => setClose(true)}
                                        className="base-drawer__btn-close"
                                        altText={t('Close')}
                                    >
                                        <BaseIcon
                                            name="close-sign"
                                            className="base-drawer__icon-close"
                                        />
                                    </BaseButton>
                                </div>
                            )}
                            <div className="base-drawer__container-content" ref={bodyRef}>
                                {isMobile && (
                                    <BaseButton
                                        ariaExpanded="true"
                                        ariaControls={id}
                                        hasIconOnly
                                        type="tertiary"
                                        onClick={() => setClose(true)}
                                        className="base-drawer__btn-close"
                                        altText={t('Close')}
                                    >
                                        <BaseIcon
                                            name="close-sign"
                                            className="base-drawer__icon-close"
                                        />
                                    </BaseButton>
                                )}
                                <div className="base-drawer__content" ref={contentRef}>
                                    {children}
                                </div>
                            </div>
                        </div>
                    </div>
                </FocusLock>
            )}
        </>
    );
};

BaseDrawer.displayName = 'BaseDrawer';

BaseDrawer.propTypes = {
    id: PropTypes.string,
    className: PropTypes.string,
    children: PropTypes.element.isRequired,
    drawerState: PropTypes.bool.isRequired,
    handleClose: PropTypes.func.isRequired,
};

export default withBreakpoint(BaseDrawer);
