import React from 'react';
import { BaseText, BaseTitle } from '../base';

const SmallHero = (props) => {
    const { subtitle, title, headingLevel, description, backgroundColor } = props;
    const subHeadingLevel = 'h' + (parseInt(headingLevel.replace('h', '')) + 1);
    return (
        <div className={`ak-small-hero ${backgroundColor || 'blue'}`}>
            <div className="ak-small-hero__container">
                {subtitle && (
                    <BaseTitle
                        className="ak-small-hero__subtitle"
                        text={subtitle}
                        headingLevel={subHeadingLevel}
                    />
                )}
                <BaseTitle className="ak-small-hero__title" headingLevel={headingLevel}>
                    {title}
                </BaseTitle>
                <BaseText className="ak-small-hero__description" text={description} level="1" />
            </div>
        </div>
    );
};

SmallHero.displayName = 'SmallHero';

export default SmallHero;
