import React, { useEffect, useRef } from 'react';
import cx from 'classnames';
import PropTypes from 'prop-types';
import { t } from '@konrad/reweb-aem';
import BaseButton from '../base-button/base-button';
import BaseIcon from '../base-icon/base-icon';
import ScrollLock from '../../../lib/utils/scrollLock';
import FocusLock from 'react-focus-lock';
import { BaseTitle } from '../index';

const BaseModal = (props) => {
    const { id, children, className, modalState, handleClose, title, isContentModal } = props;

    const contentRef = useRef(null);

    useEffect(() => {
        if (modalState) {
            ScrollLock.lockBody();
            if (isContentModal) {
                const currentNode = contentRef.current;
                const firstFocusableElement = currentNode.querySelectorAll(
                    'button, [href], input, select, textarea, [tabindex]:not([tabindex="-1"])'
                )[0];
                firstFocusableElement && firstFocusableElement.focus();
            }
        } else {
            ScrollLock.unlockBody();
        }
    }, [modalState, isContentModal]);

    const handleClickOutside = ({ target }) => {
        const body = document.querySelector(
            isContentModal ? '.base-modal__modal-body' : '.base-modal__content'
        );
        const header = document.querySelector('.base-modal__header');
        const isClickInside = (header && header.contains(target)) || body.contains(target);
        if (!isClickInside) {
            handleClose();
        }
    };

    return (
        <>
            {modalState && (
                <FocusLock>
                    <div
                        aria-modal
                        tabIndex={-1}
                        role="dialog"
                        onClick={handleClickOutside}
                        className={cx(
                            'base-modal',
                            { 'base-modal__overlay': modalState },
                            className
                        )}
                    >
                        {isContentModal ? (
                            <div className="base-modal__modal-body" role="document">
                                <div className="base-modal__modal-header">
                                    <BaseTitle
                                        className="base-modal__modal-title"
                                        headingLevel="h2"
                                        overrideStyle="h4"
                                    >
                                        {title}
                                    </BaseTitle>
                                    <BaseButton
                                        ariaExpanded="true"
                                        ariaControls={id}
                                        hasIconOnly
                                        onClick={handleClose}
                                        type="tertiary"
                                        className="base-modal__btn-close"
                                        altText={t('Close')}
                                    >
                                        <BaseIcon
                                            name="close-sign"
                                            className="base-modal__icon-close"
                                        />
                                    </BaseButton>
                                </div>
                                <div className="base-modal__content" ref={contentRef}>
                                    {children}
                                </div>
                            </div>
                        ) : (
                            [
                                <div className="base-modal__header">
                                    <BaseButton
                                        ariaExpanded="true"
                                        ariaControls={id}
                                        hasIconOnly
                                        onClick={handleClose}
                                        type="tertiary"
                                        className="base-modal__btn-close rounded"
                                        altText={t('Close')}
                                    >
                                        <BaseIcon
                                            name="close-sign"
                                            className="base-modal__icon-close"
                                        />
                                    </BaseButton>
                                </div>,
                                <div className="base-modal__content">{children}</div>,
                            ]
                        )}
                    </div>
                </FocusLock>
            )}
        </>
    );
};

BaseModal.propTypes = {
    children: PropTypes.element.isRequired,
    modalState: PropTypes.bool.isRequired,
    className: PropTypes.string,
    id: PropTypes.string,
    handleClose: PropTypes.func.isRequired,
};

export default BaseModal;
