import React from 'react';
import { GridRowContent, GridItem } from './elements';
import uuidv4 from 'uuid/v4';

const GridRow = (props) => {
    const { numCols, distributeThreeColumns, alignItems, gutters, wcChildren } = props;

    return (
        <div key={uuidv4()}>
            <GridRowContent
                numCols={numCols}
                distribution={distributeThreeColumns}
                alignItems={alignItems}
                gutters={gutters}
            >
                <GridItem ref={wcChildren.appendRef} />
            </GridRowContent>
        </div>
    );
};

GridRow.displayName = 'GridRow';

export default GridRow;
