import React from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';
import { BaseIcon, BaseText } from '../';

const BaseCheckbox = React.forwardRef((props, ref) => {
    const {
        id,
        text,
        value,
        error,
        onBlur,
        checked,
        onFocus,
        onChange,
        disabled,
        required,
        className,
        isRichText,
        background,
        onMouseEnter,
        onMouseleave,
    } = props;

    const preOnChange = (e) => {
        if (onChange) onChange(e.target.checked, e);
    };

    const preOnBlur = (e) => {
        if (onBlur) onBlur(e.target.value, e);
    };

    const preOnFocus = (e) => {
        if (onFocus) onFocus(e.target.value, e);
    };

    const preOnMouseEnter = (e) => {
        if (onMouseEnter) onMouseEnter(e.target.value, e);
    };

    const preOnMouseLeave = (e) => {
        if (onMouseleave) onMouseleave(e.target.value, e);
    };

    return (
        <label
            htmlFor={id}
            className={cx(
                'base-checkbox',
                { 'base-checkbox__invalid': error },
                { 'base-checkbox__disabled': disabled },
                className
            )}
        >
            <input
                id={id}
                ref={ref}
                value={value}
                type={'checkbox'}
                onBlur={preOnBlur}
                required={required}
                disabled={disabled}
                onFocus={preOnFocus}
                onChange={preOnChange}
                defaultChecked={checked}
                onMouseEnter={preOnMouseEnter}
                onMouseLeave={preOnMouseLeave}
                className={'base-checkbox__input'}
            />
            <div
                className={cx('base-checkbox__icon-container', {
                    'base-checkbox__icon-container--darker': background,
                })}
            >
                <BaseIcon
                    name={'checkbox-selected'}
                    className={'base-checkbox__icon base-checkbox__selected'}
                />
                <BaseIcon
                    name={'checkbox-unselected'}
                    className={'base-checkbox__icon base-checkbox__unselected'}
                />
            </div>
            <BaseText
                className={'base-checkbox__text'}
                level="3"
                isRichText={isRichText}
                text={text}
                wrapper="span"
            />
        </label>
    );
});

BaseCheckbox.displayName = 'BaseCheckbox';

BaseCheckbox.defaultProps = {
    background: false,
};

BaseCheckbox.propTypes = {
    id: PropTypes.string,
    text: PropTypes.string,
    onBlur: PropTypes.func,
    onFocus: PropTypes.func,
    checked: PropTypes.bool,
    onChange: PropTypes.func,
    disabled: PropTypes.bool,
    required: PropTypes.bool,
    background: PropTypes.bool,
    className: PropTypes.string,
    onMouseEnter: PropTypes.func,
    onMouseleave: PropTypes.func,
    error: PropTypes.oneOfType([PropTypes.string, PropTypes.bool]),
    value: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
};

export default BaseCheckbox;
