import React from 'react';
import uuidv4 from 'uuid/v4';
import { ImageCarouselContainer, ImageCarouselItem } from './elements';
import { BaseTitle, BaseText } from '../base';

const ImageCarousel = (props) => {
    const { multifieldItems, heading, headingLevel, subtitle } = props;

    let render = () => {
        return (
            <>
                {multifieldItems &&
                    multifieldItems.map((a, i) => (
                        <ImageCarouselItem key={i} id={uuidv4()} item={a} />
                    ))}
            </>
        );
    };

    return (
        <>
            <BaseText
                className="ak-image-carousel__subtitle"
                text={subtitle}
                type={'sub'}
                alignment={'center'}
            />
            <BaseTitle
                text={heading}
                headingLevel={headingLevel}
                overrideStyle={'h2'}
                alignment={'center'}
            />
            <ImageCarouselContainer render={render} />
        </>
    );
};

ImageCarousel.displayName = 'ImageCarousel';

export default ImageCarousel;
