import React from 'react';
import { linkService } from '@konrad/reweb-aem';
import { BaseLink, BaseText, BaseIcon, Box } from '../base';

const BackButton = (props) => {
    const { text, url } = props;

    return (
        <div className="ak-back-button">
            <div className="ak-back-button__container">
                <BaseLink url={linkService.get(url)} className="ak-back-button__link">
                    <Box direction="row">
                        <BaseIcon name="white-arrow" className="ak-back-button__arrow" />
                        <BaseText text={text} level="3" className="ak-back-button__text" />
                    </Box>
                </BaseLink>
            </div>
        </div>
    );
};

BackButton.displayName = 'BackButton';

export default BackButton;
