import React from 'react';
import { BaseCarousel, BaseIcon } from '../../../base';

const ImageCarouselContainer = (props) => {
    const { render } = props;

    let arrowLeft = <BaseIcon className="ak-image-carousel__icon" name="arrow-left" />;
    let arrowRight = <BaseIcon className="ak-image-carousel__icon" name="arrow-right" />;

    return (
        <div>
            <BaseCarousel
                className={'ak-image-carousel'}
                slidesPerView={3}
                spaceBetween={24}
                paginationRequired={false}
                loop={true}
                render={render}
                prevButton={arrowLeft}
                nextButton={arrowRight}
            />
        </div>
    );
};

ImageCarouselContainer.displayName = 'ImageCarouselContainer';

export default ImageCarouselContainer;
