import { isRequired, emailIsValid, phoneNumberValid, isValidZipCode } from "../../../../lib/fieldValidations";
import {t} from "@konrad/reweb-aem";

const requiredFields = [isRequired()];
const requiredCaptcha = [isRequired(t('Please verify that you are a human'))];
const emailValid = [isRequired(), emailIsValid()];
const phoneValid = [isRequired(), phoneNumberValid()];
const zipCodeValid = [isRequired(), isValidZipCode()];
let isFieldFocus = true;
/**
 *  Returns if the input is valid
 * @param value to validateForm
 * @param validationArray According to the input type it'll validateForm
 * @returns {*}
 */
const validateFormInput = (value, validationArray) => {
	let validationMessage;

	for(let validator in validationArray) {
		validationMessage = validationArray[validator](value);

		if(validationMessage) {
			break;
		}
	}
	return validationMessage;
};

/**
 * Function which sets the error message state if needed
 * @param {*} validateFormArray Array of functions needed to validateForm input
 * @param {*} value Value to be validateFormd
 * @param {*} setErrorMessageFunction Function which will set the error message
 */
const validateForm = (validateFormArray, value, ref) => {
	let validationMessage = validateFormInput(value, validateFormArray, ref);
	if(ref && value === undefined && isFieldFocus) {
		ref.current.focus();
		isFieldFocus = false;
	}
	return validationMessage ? validationMessage : '';
};

export default function validate(values, ref){
	let errors = {}
	isFieldFocus = true;
	errors.firstName = validateForm(requiredFields, values.firstName, ref.firstName);
	errors.lastName = validateForm(requiredFields, values.lastName, ref.lastName);
	errors.emailAddress = validateForm(emailValid, values.emailAddress, ref.emailAddress);
	errors.phoneNumber = validateForm(phoneValid, values.phoneNumber, ref.phoneNumber);
	errors.jobTitle = validateForm(requiredFields, values.jobTitle, ref.jobTitle);
	errors.company = validateForm(requiredFields, values.company, ref.company);
	errors.zipCode = validateForm(zipCodeValid, values.zipCode, ref.zipCode);
	errors.utility = validateForm(requiredFields, values.utility, ref.utility);
	errors.approximateMonthlyBill = validateForm(requiredFields, values.approximateMonthlyBill, ref.approximateMonthlyBill);
	errors.captchaErrorMessage = validateForm(requiredCaptcha, values.captcha, null);

	return clean(errors);
};

function clean(obj) {
	let cleanObj = obj;
	for (var propName in cleanObj) {
		if (cleanObj[propName] === null || cleanObj[propName] === undefined || cleanObj[propName] === "") {
			delete cleanObj[propName];
		}
	}

	return cleanObj;
}
