import React from 'react';
import cx from 'classnames';
import { BaseTitle, Box, BaseImage, BaseText, BaseButton } from '../base';
import { linkService } from '@konrad/reweb-aem';
import PropTypes from 'prop-types';

const LogoPlusMoreInfo = (props) => {
    const {
        title,
        image,
        imageAltText,
        description,
        ctaLinkTarget,
        ctaUrl,
        ctaText,
        borderTop,
        borderBottom,
        headingLevel,
    } = props;

    return (
        <div
            className={cx({
                'ak-logo-plus-more-info--border-top': borderTop,
                'ak-logo-plus-more-info--border-bottom': borderBottom,
            })}
        >
            <Box
                direction="row"
                justify="center"
                alignment="center"
                className="ak-logo-plus-more-info__container"
            >
                <Box
                    direction="row"
                    justify="left"
                    alignment="left"
                    className="ak-logo-plus-more-info__body"
                >
                    <BaseImage
                        url={image}
                        altText={imageAltText}
                        className="ak-logo-plus-more-info__image"
                    />
                    <Box
                        direction="col"
                        justify="left"
                        alignment="left"
                        className="ak-logo-plus-more-info__right-panel"
                    >
                        <BaseTitle
                            text={title}
                            headingLevel={headingLevel}
                            overrideStyle="h4"
                            className="ak-logo-plus-more-info__title"
                        />
                        <BaseText
                            wrapper="div"
                            level="4"
                            className="ak-logo-plus-more-info__description"
                            text={description}
                            isRichText
                        />
                    </Box>
                </Box>
                {ctaUrl && (
                    <BaseButton
                        url={linkService.get(ctaUrl)}
                        type="primary"
                        altText={imageAltText}
                        external={ctaLinkTarget === 'external'}
                        children={ctaText}
                        className="ak-logo-plus-more-info__button"
                    />
                )}
            </Box>
        </div>
    );
};

LogoPlusMoreInfo.displayName = 'LogoPlusMoreInfo';

LogoPlusMoreInfo.propTypes = {
    title: PropTypes.string,
    image: PropTypes.string,
    imageAltText: PropTypes.string,
    description: PropTypes.string,
    ctaLinkTarget: PropTypes.string,
    ctaUrl: PropTypes.string,
    ctaText: PropTypes.string,
};

export default LogoPlusMoreInfo;
