import React from 'react';
import cx from 'classnames';
import PropTypes from 'prop-types';
import { IMAGE_WIDTHS } from '../../../lib/constants';

const BaseImage = (props) => {
    const { url, size, center, altText, className } = props;

    return (
        <img
            className={cx(
                'base-image',
                {
                    'base-image--center': center,
                    [`base-image--${size}`]: size,
                },
                className
            )}
            src={url}
            alt={altText || undefined}
            aria-hidden={!altText ? true : undefined}
        />
    );
};

BaseImage.propTypes = {
    altText: PropTypes.string,
    className: PropTypes.string,
    url: PropTypes.string.isRequired,
    size: PropTypes.oneOf(IMAGE_WIDTHS),
};

export default BaseImage;
