import React from 'react';
import { BaseText, BaseImage } from '../../../base';

const CarouselItem = (props) => {
    const { id, item } = props;

    return (
        <li className="swiper-slide ak-testimonial__item" id={id}>
            <div className="ak-testimonial__testimonial">
                <BaseText text={item.testimonial} level={'1'} isRichText={true}></BaseText>
            </div>
            <div className="ak-testimonial__user">
                <div className="ak-testimonial__image-container">
                    <BaseImage
                        url={item.image}
                        className="ak-testimonial__image"
                        alt={item.alternativeText}
                    />
                </div>
                <div className="ak-testimonial__text">
                    <BaseText
                        className="ak-testimonial__name"
                        text={item.name}
                        level={'1'}
                    ></BaseText>
                    <BaseText
                        className="ak-testimonial__description"
                        text={item.description}
                        level={'3'}
                    ></BaseText>
                </div>
            </div>
        </li>
    );
};

CarouselItem.displayName = 'CarouselItem';

export default CarouselItem;
