import React from 'react';
import withBreakpoint from '../../lib/utils/withBreakpoint';
import { BaseTitle, BaseText, BaseImage } from '../base';

const ArticleFrame = (props) => {
    const {
        title,
        // authorName,
        publishedDate,
        // authorImage,
        // authorImageAltText,
        articleImage,
        articleImageAltText,
        children,
    } = props;
    return (
        <div className="ak-article-frame">
            <div className="ak-article-frame__content">
                <div className="ak-article-frame__header">
                    <BaseTitle
                        className="ak-article-frame__title"
                        text={title}
                        headingLevel="h1"
                        overrideStyle="h2"
                    />
                    <div className="ak-article-frame__profile">
                        {/* <BaseImage className='ak-article-frame__profile-thumbnail' url={authorImage}
                                	altText={authorImageAltText}/>
                        <div className='ak-article-frame__profile-info'>
                            <BaseText className='ak-article-frame__profile-name' text={authorName}
                                      level='2'/> */}
                        <BaseText
                            className="ak-article-frame__profile-date"
                            text={publishedDate}
                            alignment="left"
                            level="4"
                        />
                        {/* </div> */}
                    </div>
                </div>
                <BaseImage
                    className="ak-article-frame__main-image"
                    url={articleImage}
                    altText={articleImageAltText}
                    size="full"
                />
                <div className="ak-article-frame__article-content">{children}</div>
            </div>
        </div>
    );
};

ArticleFrame.displayName = 'ArticleFrame';

export default withBreakpoint(ArticleFrame);
