import React from 'react';
import { BaseContainer, Box } from '../base';
import withBreakpoint from '../../lib/utils/withBreakpoint';

const Container = (props) => {
    const {
        bgType,
        bgColor,
        isMobile,
        alignment,
        bgImageUrl,
        bgImageUrlMobile,
        wcChildren,
    } = props;

    return (
        <BaseContainer
            bgType={bgType}
            bgColor={bgColor}
            bgImageUrl={isMobile && bgImageUrlMobile ? bgImageUrlMobile : bgImageUrl}
        >
            <Box direction="col" alignment={alignment} ref={wcChildren.appendRef} />
        </BaseContainer>
    );
};

Container.displayName = 'Container';

export default withBreakpoint(Container);
