import React, { useState } from 'react';
import { StatusBar, One, Two, Three, Four, Details } from './elements';
import { Message, OfferDetails } from '../index';
import { linkService, t } from '@konrad/reweb-aem';
import PropTypes from 'prop-types';
import uuidv4 from 'uuid/v4';
import { HEADING_LEVELS, TERMS_AND_CONDITIONS_FOLDER, CAPTCHA_VALUE } from '../../lib/constants';
import akronAPI from '../../api';

const EnrollmentFrame = (props) => {
    const {
        //PROPERTIES
        formType,

        //STEP ONE
        stepOneTitle,
        firstNameLabel,
        firstNamePlaceholder,
        lastNameLabel,
        lastNamePlaceholder,
        phoneNumberLabel,
        phoneNumberPlaceholder,
        emailAddressLabel,
        emailAddressPlaceholder,
        companyBusinessLabel,
        companyBusinessPlaceholder,
        textAlertCheckbox,
        backButtonLabelOne,
        CTALabelOne,
        fileReferenceStepOneInstrImage,
        stepOneInstrTitle,
        stepOneInstrDescription,

        //STEP TWO
        stepTwoTitle,
        streetAddressLabel,
        streetAddressPlaceholder,
        cityLabel,
        cityPlaceholder,
        stateLabel,
        statePlaceholder,
        zipCodeLabel,
        zipCodePlaceholder,
        backButtonLabelTwo,
        CTALabelTwo,
        fileReferenceStepTwoInstrImage,
        stepTwoInstrTitle,
        stepTwoInstrDescription,

        //STEP THREE
        stepThreeTitle,
        companyLabel,
        utilityModalLabel,
        customerNumberPlaceholder,
        customerNameModalLabel,
        residentialPurposeLabel,
        residentialPurposePlaceholder,
        residentialPurposeModalLabel,
        hearAboutUsLabel,
        hearAboutUsPlaceholder,
        differentServiceAddressCheckbox,
        electricServiceLabel,
        electricServicePlaceholder,
        cityLabelDiff,
        cityPlaceholderDiff,
        stateLabelDiff,
        statePlaceholderDiff,
        zipCodeLabelDiff,
        zipCodePlaceholderDiff,
        backButtonLabelThree,
        CTALabelThree,
        fileReferenceStepThreeInstrImage,
        stepThreeInstrTitle,
        stepThreeInstrDescription,

        //STEP FOUR
        stepFourTitle,
        stepOneTitleInFourth,
        stepTwoTitleInFourth,
        stepThreeTitleInFourth,
        specialOffersCheckboxText,
        disclaimer,
        backButtonLabelFour,
        CTALabelFour,
        fileReferenceStepFourInstrImage,
        stepFourInstrTitle,
        stepFourInstrDescription,

        //SUCCESS
        ctaLink,
        ctaLinkTarget,

        //OFFER, EDC & PRODUCT
        offer,
        productId,
        validOffer,

        isEdit,
        captchaToken,

        //ERROR
        title,
        ctaLinkError,
        ctaText,
        description,
        headingLevel,
        ctaLinkTargetError,
        fileReference,
    } = props;

    let edcData = {};
    let offerPriceData = {};
    let bundleOffers = [];

    const generateEdcData = (edcDetails) => {
        return {
            edcName: edcDetails && edcDetails.name ? edcDetails.name : '',
            edcCode: edcDetails && edcDetails.code ? edcDetails.code : '',
            cusNumLabel:
                edcDetails && edcDetails.cusNumLabel
                    ? edcDetails.cusNumLabel
                    : t('Customer Number'),
            cusNumLength: edcDetails && edcDetails.cusNumLength ? edcDetails.cusNumLength : '',
            cusNumChars: edcDetails && edcDetails.cusNumChars ? edcDetails.cusNumChars : '',
            edcState: edcDetails && edcDetails.state ? edcDetails.state : '',
            wrongUtilityModal:
                edcDetails && edcDetails.wrongUtilityModal ? edcDetails.wrongUtilityModal : '',
            fileNameHelpFindModal:
                edcDetails && edcDetails.helpFindModal ? edcDetails.helpFindModal : '',
            whatThisModal: edcDetails && edcDetails.whatThisModal ? edcDetails.whatThisModal : '',
        };
    };

    const generateOfferPriceData = (offer) => {
        return {
            planAmount: offer.price ? offer.price : '',
            planAmountTerm: offer.priceLabel ? offer.priceLabel : '',
        };
    };

    let edcList = [];
    let pdfLink = {};

    if (offer.isBundle) {
        offer.bundleGasOffer && bundleOffers.push(offer.bundleGasOffer);
        offer.bundleElectricityOffer && bundleOffers.push(offer.bundleElectricityOffer);

        bundleOffers.forEach((offerItem) => {
            let edcDetails = offerItem.edcDetails;
            edcData[offerItem.offerType] = generateEdcData(edcDetails);
            offerPriceData[offerItem.offerType] = generateOfferPriceData(offerItem);
        });

        if (offer.bundleGasOffer && offer.bundleGasOffer.edcDetails) {
            edcList.push({
                offerType: 'GAS',
                edcName: offer.bundleGasOffer.edcDetails.name,
                edcLogo: offer.bundleGasOffer.edcDetails.logo,
            });
        }

        if (offer.bundleElectricityOffer && offer.bundleElectricityOffer.edcDetails) {
            edcList.push({
                offerType: 'ELECTRICITY',
                edcName: offer.bundleElectricityOffer.edcDetails.name,
                edcLogo: offer.bundleElectricityOffer.edcDetails.logo,
            });
        }

        bundleOffers.forEach((offerItem) => {
            pdfLink[offerItem.offerType] =
                offerItem && offerItem.priorityCode
                    ? TERMS_AND_CONDITIONS_FOLDER + '/' + offerItem.priorityCode + '.pdf'
                    : null;
        });
    } else {
        edcData[offer.offerType] = generateEdcData(offer.edcDetails);
        offerPriceData[offer.offerType] = generateOfferPriceData(offer);
        pdfLink[offer.offerType] = TERMS_AND_CONDITIONS_FOLDER + '/' + offer.priorityCode + '.pdf';
    }

    const offerDataKeys = Object.keys(edcData);

    let planName = offer.title ? offer.title : '',
        planLength = offer.duration ? offer.duration : '',
        greenOfferImage = offer.greenOfferImage ? offer.greenOfferImage : '',
        greenOfferText = offer.greenOfferText ? offer.greenOfferText : '';

    //States to handle the current step in the form
    let [step, setStep] = useState(1);
    //Used when the edit icon is clicked next button should send the user to the final step
    let [goToFinalStep, setGoToFinalStep] = useState(false);

    //Form inputs information
    const [firstName, setFirstName] = useState('');
    const [lastName, setLastName] = useState('');
    const [phoneNumber, setPhoneNumber] = useState('');
    const [emailAddress, setEmail] = useState('');
    const [companyBusiness, setCompanyBusiness] = useState('');
    const [alertCheckbox, setAlertCheckbox] = useState(false);
    const [captcha, setCaptcha] = useState('');
    const [streetAddress, setStreetAddress] = useState('');
    const [city, setCity] = useState('');
    const [state, setState] = useState('');
    const [zipCode, setZipCode] = useState('');
    const [company, setCompany] = useState(
        Object.keys(edcData).reduce((acc, cur) => {
            acc[cur] = edcData[cur].edcName;
            return acc;
        }, {})
    );
    const [customerNumber, setCustomerNumber] = useState(
        Object.keys(edcData).reduce((acc, cur) => {
            acc[cur] = edcData[cur].cusNumChars;
            return acc;
        }, {})
    );
    const [residentialPurpose, setResidentialPurpose] = useState(
        Object.keys(edcData).reduce((acc, cur) => {
            acc[cur] = '';
            return acc;
        }, {})
    );
    const [hearAboutUs, setHearAboutUs] = useState(
        Object.keys(edcData).reduce((acc, cur) => {
            acc[cur] = '';
            return acc;
        }, {})
    );
    const [differentServiceAddress, setDifferentServiceAddress] = useState(false);
    const [serviceAddress, setServiceAddress] = useState('');
    const [cityDiff, setCityDiff] = useState('');
    const [stateDiff, setStateDiff] = useState('');
    const [zipCodeDiff, setZipCodeDiff] = useState('');
    const [disclaimerCheckbox, setDisclaimerCheckbox] = useState(false);
    const [submitting, setSubmitting] = useState(false);

    const [titleState] = useState(uuidv4()); //Keeps a unique id for the title plan in the details
    const [changedStep, setChangedStep] = useState(false); //when a user mover between steps it changes to true

    const [sentFormErrorMessage, setSentFormErrorMessage] = useState(); //Error message for form submission

    const [drawerShow, setDrawerShow] = useState(false); //Drawer

    const handleDrawerClose = () => {
        setDrawerShow(false);
    };

    const handleDrawerOpen = () => {
        setDrawerShow(true);
    };

    const AMOUNT_OF_STEPS = 4;

    /**
     * Validates a value against a validationArray
     * @param value
     * @param validationArray
     * @returns {*}
     */
    const validateInput = (value, validationArray) => {
        let validationMessage;

        for (let validator in validationArray) {
            validationMessage = validationArray[validator](value);

            if (validationMessage) {
                break;
            }
        }
        return validationMessage;
    };

    /**
     * Used over all the steps to validate an input and set the error
     * @param validateArray
     * @param value
     * @param setErrorMessageFunction
     * @param offerType
     * @returns {boolean}
     */
    const validate = (validateArray, value, setErrorMessageFunction, offerType) => {
        let validationMessage = validateInput(value, validateArray);

        if (validationMessage) {
            !!offerType
                ? setErrorMessageFunction(offerType, validationMessage)
                : setErrorMessageFunction(validationMessage);
            return false;
        } else {
            !!offerType ? setErrorMessageFunction(offerType, '') : setErrorMessageFunction('');
            return true;
        }
    };

    const completeEnrollment = () => {
        setSubmitting(true);
        setSentFormErrorMessage('');

        let phone = phoneNumber;
        if (phoneNumber) {
            phone = phoneNumber.replace('(', '').replace(')', '').replace(' ', '-');
        }

        let enrollmentData = offerDataKeys.map((key) => {
            return {
                form_type: formType,
                first_name: firstName,
                last_name: lastName,
                phone_number: phone,
                email_address: emailAddress,
                company_business: companyBusiness,
                accept_alerts: alertCheckbox,
                mailing_street_address: streetAddress,
                mailing_city: city,
                mailing_state: state,
                mailing_zip_code: zipCode,
                customer_number: customerNumber[key],
                residential_purpose: residentialPurpose[key],
                hear_about_us: hearAboutUs[key],
                different_service_address: differentServiceAddress,
                service_address: serviceAddress,
                service_city: cityDiff,
                service_state: stateDiff,
                service_zip_code: zipCodeDiff,
                accept_disclaimer: disclaimerCheckbox,
                //Offer component variables
                product_id: offer.isBundle
                    ? bundleOffers.find((offerItem) => offerItem.offerType === key).productId
                    : offer.productId,
                edc_code: edcData[key].edcCode,
            };
        });

        akronAPI
            .enrollmentForm({
                [CAPTCHA_VALUE]: captcha,
                enrollmentData,
            })
            .then(
                (result) => redirect(result),
                (error) => showErrorMessage(error)
            );
    };

    const redirect = (result) => {
        if (ctaLinkTarget === 'internal') {
            window.location.href = linkService.get(ctaLink);
        } else {
            window.open(linkService.get(ctaLink), '_blank');
        }
    };

    const showErrorMessage = () => {
        setSubmitting(false);
        setCaptcha('');
        setSentFormErrorMessage(t('There was an error submitting the form'));
    };

    return (
        <>
            {(!validOffer && !isEdit) ||
            (!offer.isBundle && !productId && !isEdit) ||
            (offer.isBundle && !offer.bundleId && !isEdit) ? (
                //If productId is not in the query params and it's on publish it should show the message
                //If productId is not associates to any offer and it's on publish it should show the message
                <Message
                    title={title}
                    ctaLink={ctaLinkError}
                    ctaText={ctaText}
                    description={description}
                    headingLevel={headingLevel}
                    ctaLinkTarget={ctaLinkTargetError}
                    fileReference={fileReference}
                />
            ) : (
                <div className="ak-enrollment-frame">
                    <Details
                        planName={planName}
                        planLength={planLength}
                        detailsPriceData={offerPriceData}
                        titleState={titleState}
                        changedStep={changedStep}
                        setChangedStep={setChangedStep}
                        handleDrawerOpen={handleDrawerOpen}
                        isBundle={offer.isBundle}
                    />
                    <StatusBar current={step} />
                    <form>
                        <One
                            title={stepOneTitle}
                            firstName={firstName}
                            setFirstName={setFirstName}
                            firstNameLabel={firstNameLabel}
                            firstNamePlaceholder={firstNamePlaceholder}
                            lastName={lastName}
                            setLastName={setLastName}
                            lastNameLabel={lastNameLabel}
                            lastNamePlaceholder={lastNamePlaceholder}
                            phoneNumber={phoneNumber}
                            setPhoneNumber={setPhoneNumber}
                            phoneNumberLabel={phoneNumberLabel}
                            phoneNumberPlaceholder={phoneNumberPlaceholder}
                            emailAddress={emailAddress}
                            setEmail={setEmail}
                            emailAddressLabel={emailAddressLabel}
                            emailAddressPlaceholder={emailAddressPlaceholder}
                            companyBusiness={companyBusiness}
                            setCompanyBusiness={setCompanyBusiness}
                            companyBusinessLabel={companyBusinessLabel}
                            companyBusinessPlaceholder={companyBusinessPlaceholder}
                            backButtonLabel={backButtonLabelOne}
                            CTALabelOne={CTALabelOne}
                            stepOneInstrImage={fileReferenceStepOneInstrImage}
                            stepOneInstrTitle={stepOneInstrTitle}
                            stepOneInstrDescription={stepOneInstrDescription}
                            setChangedStep={setChangedStep}
                            step={step}
                            amountOfSteps={AMOUNT_OF_STEPS}
                            goToFinalStep={goToFinalStep}
                            setStep={setStep}
                            validate={validate}
                            formType={formType}
                        />
                        <Two
                            title={stepTwoTitle}
                            streetAddress={streetAddress}
                            setStreetAddress={setStreetAddress}
                            streetAddressLabel={streetAddressLabel}
                            streetAddressPlaceholder={streetAddressPlaceholder}
                            city={city}
                            setCity={setCity}
                            cityLabel={cityLabel}
                            cityPlaceholder={cityPlaceholder}
                            state={state}
                            setState={setState}
                            stateLabel={stateLabel}
                            statePlaceholder={statePlaceholder}
                            zipCode={zipCode}
                            setZipCode={setZipCode}
                            zipCodeLabel={zipCodeLabel}
                            zipCodePlaceholder={zipCodePlaceholder}
                            differentServiceAddress={differentServiceAddress}
                            setDifferentServiceAddress={setDifferentServiceAddress}
                            differentServiceAddressCheckbox={differentServiceAddressCheckbox}
                            serviceAddress={serviceAddress}
                            setServiceAddress={setServiceAddress}
                            electricServiceLabel={electricServiceLabel}
                            electricServicePlaceholder={electricServicePlaceholder}
                            serviceCity={cityDiff}
                            setServiceCity={setCityDiff}
                            serviceCityLabel={cityLabelDiff}
                            serviceCityPlaceholder={cityPlaceholderDiff}
                            serviceState={stateDiff}
                            setServiceState={setStateDiff}
                            serviceStateLabel={stateLabelDiff}
                            serviceStatePlaceholder={statePlaceholderDiff}
                            serviceZipCode={zipCodeDiff}
                            setServiceZipCode={setZipCodeDiff}
                            serviceZipCodeLabel={zipCodeLabelDiff}
                            serviceZipCodePlaceholder={zipCodePlaceholderDiff}
                            backButtonLabel={backButtonLabelTwo}
                            CTALabelOne={CTALabelTwo}
                            stepTwoInstrImage={fileReferenceStepTwoInstrImage}
                            stepTwoInstrTitle={stepTwoInstrTitle}
                            stepTwoInstrDescription={stepTwoInstrDescription}
                            setChangedStep={setChangedStep}
                            step={step}
                            amountOfSteps={AMOUNT_OF_STEPS}
                            goToFinalStep={goToFinalStep}
                            setStep={setStep}
                            validate={validate}
                        />
                        <Three
                            title={stepThreeTitle}
                            company={company}
                            setCompany={setCompany}
                            companyLabel={companyLabel}
                            utilityModalLabel={utilityModalLabel}
                            customerNumber={customerNumber}
                            setCustomerNumber={setCustomerNumber}
                            customerNumberPlaceholder={customerNumberPlaceholder}
                            customerNameModalLabel={customerNameModalLabel}
                            residentialPurpose={residentialPurpose}
                            setResidentialPurpose={setResidentialPurpose}
                            residentialPurposeLabel={residentialPurposeLabel}
                            residentialPurposePlaceholder={residentialPurposePlaceholder}
                            residentialPurposeModalLabel={residentialPurposeModalLabel}
                            hearAboutUs={hearAboutUs}
                            setHearAboutUs={setHearAboutUs}
                            hearAboutUsLabel={hearAboutUsLabel}
                            hearAboutUsPlaceholder={hearAboutUsPlaceholder}
                            backButtonLabel={backButtonLabelThree}
                            CTALabelOne={CTALabelThree}
                            stepThreeInstrImage={fileReferenceStepThreeInstrImage}
                            stepThreeInstrTitle={stepThreeInstrTitle}
                            stepThreeInstrDescription={stepThreeInstrDescription}
                            setChangedStep={setChangedStep}
                            step={step}
                            amountOfSteps={AMOUNT_OF_STEPS}
                            goToFinalStep={goToFinalStep}
                            setStep={setStep}
                            validate={validate}
                            formType={formType}
                            edcData={edcData}
                            isBundle={offer.isBundle}
                            offerDataKeys={offerDataKeys}
                        />
                        <Four
                            sectionFourTitle={stepFourTitle}
                            stepOneTitle={stepOneTitleInFourth}
                            stepTwoTitle={stepTwoTitleInFourth}
                            stepThreeTitle={stepThreeTitleInFourth}
                            firstName={firstName}
                            lastName={lastName}
                            phoneNumber={phoneNumber}
                            emailAddress={emailAddress}
                            companyBusiness={companyBusiness}
                            streetAddress={streetAddress}
                            city={city}
                            state={state}
                            zipCode={zipCode}
                            company={company}
                            customerNumber={customerNumber}
                            residentialPurpose={residentialPurpose}
                            hearAboutUs={hearAboutUs}
                            differentServiceAddress={differentServiceAddress}
                            serviceAddress={serviceAddress}
                            cityDiff={cityDiff}
                            stateDiff={stateDiff}
                            zipCodeDiff={zipCodeDiff}
                            specialOffersCheckboxText={specialOffersCheckboxText}
                            disclaimerCheckbox={disclaimerCheckbox}
                            setDisclaimerCheckbox={setDisclaimerCheckbox}
                            disclaimer={disclaimer}
                            alertCheckbox={alertCheckbox}
                            setAlertCheckbox={setAlertCheckbox}
                            textAlertCheckbox={textAlertCheckbox}
                            fourthBackButton={backButtonLabelFour}
                            fourthCTALabel={CTALabelFour}
                            stepFourInstrImage={fileReferenceStepFourInstrImage}
                            stepFourInstrTitle={stepFourInstrTitle}
                            stepFourInstrDescription={stepFourInstrDescription}
                            setChangedStep={setChangedStep}
                            step={step}
                            amountOfSteps={AMOUNT_OF_STEPS}
                            setGoToFinalStep={setGoToFinalStep}
                            setStep={setStep}
                            pdfLink={pdfLink}
                            captchaToken={captchaToken}
                            setCaptcha={setCaptcha}
                            captcha={captcha}
                            completeEnrollment={completeEnrollment}
                            submitErrorMessage={sentFormErrorMessage}
                            submitting={submitting}
                            offerDataKeys={offerDataKeys}
                            isBundle={offer.isBundle}
                        />
                    </form>
                </div>
            )}
            <OfferDetails
                title={offer.title}
                description={offer.moreDetails}
                planDescription={offer.bestFor}
                priceBreakdownArray={offer.multifieldPriceBreakdown}
                amount={offer.price}
                amountTerm={offer.priceLabel}
                drawerShow={drawerShow}
                time={offer.duration}
                timeTerm={t('Term')}
                greenOfferImage={greenOfferImage}
                greenOfferText={greenOfferText}
                priorityCode={offer.priorityCode}
                handleDrawerClose={handleDrawerClose}
                edcLogo={offer.isBundle ? null : offer.edcDetails && offer.edcDetails.logo}
                edcName={offer.isBundle ? null : offer.edcDetails && offer.edcDetails.name}
                showCTA={false}
                isBundle={offer.isBundle}
                gasOffer={offer.bundleGasOffer}
                electricityOffer={offer.bundleElectricityOffer}
                edcList={edcList}
            />
        </>
    );
};

EnrollmentFrame.displayName = 'EnrollmentFrame';

EnrollmentFrame.defaultProps = {
    firstNameLabel: t('First Name'),
    firstNamePlaceholder: t('Enter first name'),
    lastNameLabel: t('Last Name'),
    lastNamePlaceholder: t('Enter last name'),
    phoneNumberLabel: t('Phone Number'),
    phoneNumberPlaceholder: t('Enter phone number'),
    emailAddressLabel: t('Email address'),
    emailAddressPlaceholder: t('Enter email address'),
    companyBusinessLabel: t('Company name'),
    companyBusinessPlaceholder: t('Enter your company name'),
    textAlertCheckbox: t(
        'I hereby consent to receive autodialed and or pre-recorded/artificial telemarketing calls and/or text messages from or on behalf of Energy Harbor at the telephone number listed above. I agree to receive account and marketing related communications from Energy Harbor. I understand that message and data rates may apply and that this consent is not a condition of purchasing any goods or services from Energy Harbor. This consent may be revoked at any time by calling Energy Harbor.'
    ),
    backButtonLabelOne: t('Back'),

    streetAddressLabel: t('Street Address'),
    streetAddressPlaceholder: t('Enter your address'),
    cityLabel: t('City'),
    cityPlaceholder: t('Enter your city'),
    stateLabel: t('State'),
    statePlaceholder: t('Select your state'),
    zipCodeLabel: t('Zip code'),
    zipCodePlaceholder: t('Enter your zip code'),
    backButtonLabelTwo: t('Back'),

    companyLabel: t('Current Utility Company'),
    cusNumLabel: t('Customer Number'),
    customerNumberPlaceholder: t('Enter your customer number'),
    residentialPurposeLabel: t('Is this used solely for your residential purposes?'),
    residentialPurposePlaceholder: t('Is this used solely for your residential purposes?'),
    hearAboutUsLabel: t('How did you hear about us?'),
    hearAboutUsPlaceholder: t('Choose an option'),
    differentServiceAddressCheckbox: t(
        'My electric service address is different than my mailing address'
    ),
    electricServiceLabel: t('Service Address'),
    electricServicePlaceholder: t('Enter your address'),
    cityLabelDiff: t('City'),
    cityPlaceholderDiff: t('Enter your city'),
    stateLabelDiff: t('State'),
    statePlaceholderDiff: t('Select your state'),
    zipCodeLabelDiff: t('Zip code'),
    zipCodePlaceholderDiff: t('Enter your zip code'),
    backButtonLabelThree: t('Back'),

    stepOneTitleInFourth: t('Contact Information'),
    stepTwoTitleInFourth: t('Address'),
    stepThreeTitleInFourth: t('Account Information'),
    specialOffersCheckboxText: t('Please send me special offers and updates from Energy Harbor'),
    backButtonLabelFour: t('Back'),

    title: t('Offer Page Not Found'),
    ctaLinkError: '/',
    ctaText: t('Return to Home Page'),
    description: t('Sorry, the offer you are searching for has either been closed or has expired.'),
    headingLevel: 'h1',
    ctaLinkTargetError: 'internal',
    fileReference: '',

    utilityModalLabel: 'Wrong Utility',
    customerNameModalLabel: 'Help me find this',
    residentialPurposeModalLabel: 'What is this?',
};

EnrollmentFrame.propTypes = {
    //STEP ONE
    stepOneTitle: PropTypes.string,
    firstNameLabel: PropTypes.string,
    firstNamePlaceholder: PropTypes.string,
    lastNameLabel: PropTypes.string,
    lastNamePlaceholder: PropTypes.string,
    phoneNumberLabel: PropTypes.string,
    phoneNumberPlaceholder: PropTypes.string,
    emailAddressLabel: PropTypes.string,
    emailAddressPlaceholder: PropTypes.string,
    textAlertCheckbox: PropTypes.string,
    backButtonLabelOne: PropTypes.string,
    CTALabelOne: PropTypes.string,
    fileReferenceStepOneInstrImage: PropTypes.string,
    stepOneInstrTitle: PropTypes.string,
    stepOneInstrDescription: PropTypes.string,

    //STEP TWO
    stepTwoTitle: PropTypes.string,
    streetAddressLabel: PropTypes.string,
    streetAddressPlaceholder: PropTypes.string,
    cityLabel: PropTypes.string,
    cityPlaceholder: PropTypes.string,
    stateLabel: PropTypes.string,
    statePlaceholder: PropTypes.string,
    zipCodeLabel: PropTypes.string,
    zipCodePlaceholder: PropTypes.string,
    backButtonLabelTwo: PropTypes.string,
    CTALabelTwo: PropTypes.string,
    fileReferenceStepTwoInstrImage: PropTypes.string,
    stepTwoInstrTitle: PropTypes.string,
    stepTwoInstrDescription: PropTypes.string,

    //STEP THREE
    stepThreeTitle: PropTypes.string,
    companyLabel: PropTypes.string,
    cusNumLabel: PropTypes.string,
    customerNumberPlaceholder: PropTypes.string,
    residentialPurposeLabel: PropTypes.string,
    residentialPurposePlaceholder: PropTypes.string,
    hearAboutUsLabel: PropTypes.string,
    hearAboutUsPlaceholder: PropTypes.string,
    differentServiceAddressCheckbox: PropTypes.string,
    electricServiceLabel: PropTypes.string,
    electricServicePlaceholder: PropTypes.string,
    cityLabelDiff: PropTypes.string,
    cityPlaceholderDiff: PropTypes.string,
    stateLabelDiff: PropTypes.string,
    statePlaceholderDiff: PropTypes.string,
    zipCodeLabelDiff: PropTypes.string,
    zipCodePlaceholderDiff: PropTypes.string,
    backButtonLabelThree: PropTypes.string,
    CTALabelThree: PropTypes.string,
    fileReferenceStepThreeInstrImage: PropTypes.string,
    stepThreeInstrTitle: PropTypes.string,
    stepThreeInstrDescription: PropTypes.string,

    //STEP FOUR
    stepFourTitle: PropTypes.string,
    stepOneTitleInFourth: PropTypes.string,
    stepTwoTitleInFourth: PropTypes.string,
    stepThreeTitleInFourth: PropTypes.string,
    specialOffersCheckboxText: PropTypes.string,
    disclaimer: PropTypes.string,
    backButtonLabelFour: PropTypes.string,
    CTALabelFour: PropTypes.string,
    fileReferenceStepFourInstrImage: PropTypes.string,
    stepFourInstrTitle: PropTypes.string,
    stepFourInstrDescription: PropTypes.string,

    //SUCCESS
    ctaLink: PropTypes.string,
    ctaLinkTarget: PropTypes.string,

    //ERROR
    title: PropTypes.string,
    ctaLinkError: PropTypes.string,
    ctaText: PropTypes.string,
    description: PropTypes.string,
    headingLevel: PropTypes.oneOf(HEADING_LEVELS),
    ctaLinkTargetError: PropTypes.string,
    fileReference: PropTypes.string,
};

export default EnrollmentFrame;
