import React, { useState } from 'react';
import withBreakpoint from '../../lib/utils/withBreakpoint';
import ImageCard from '../ak-image-card/ak-image-card';
import { t } from '@konrad/reweb-aem';
import { Pagination } from '../index';

const ArticleCardsList = (props) => {
    const [currentPage, setCurrentPage] = useState(1);
    const { articles, isDesktop } = props;

    const pageSize = isDesktop ? 6 : 3;
    const hasPagination = articles.length > pageSize;
    const totalPages = Math.ceil(articles.length / pageSize);
    let displayedArticles = [];

    if (hasPagination) {
        const pageFirstElement = (currentPage - 1) * pageSize;
        const pageLastElement = pageFirstElement + pageSize;
        displayedArticles = articles.slice(pageFirstElement, pageLastElement);
    } else {
        displayedArticles = articles;
    }

    const onPageChange = (page) => {
        const articlesContainer = document.querySelector('.ak-article-cards-list');
        articlesContainer.scrollIntoView(true);
        setCurrentPage(page);
    };

    const renderArticles = (article, index) => {
        const {
            title,
            authorImage,
            authorImageAltText,
            authorName,
            articleImage,
            articleImageAltText,
            articlePath,
        } = article;
        return (
            <li className="ak-article-cards-list__list-item" key={index}>
                <ImageCard
                    alignment="left"
                    mainImageReference={articleImage}
                    mainImageAltText={articleImageAltText}
                    title={title}
                    headingLevel="h3"
                    thumbnailReference={authorImage}
                    thumbnailText={authorName}
                    thumbnailAltText={authorImageAltText}
                    ctaLink={articlePath}
                    ctaText={t('Read Article')}
                />
            </li>
        );
    };

    return (
        <div className="ak-article-cards-list">
            <div className="ak-article-cards-list__container">
                <ul className="ak-article-cards-list__list">
                    {displayedArticles.map(renderArticles)}
                </ul>
                {hasPagination && (
                    <Pagination
                        sectionName={t('Articles')}
                        currentPage={currentPage}
                        totalPages={totalPages}
                        onPageChange={onPageChange}
                    />
                )}
            </div>
        </div>
    );
};

ArticleCardsList.displayName = 'ArticleCardsList';

export default withBreakpoint(ArticleCardsList);
