import React, { useEffect } from 'react';
import Swiper from 'swiper';
import uuid from 'uuid/v1';
import { t } from '@konrad/reweb-aem';
import withBreakpoint from '../../../lib/utils/withBreakpoint';
import PropTypes from 'prop-types';
import { Box, BaseButton } from '../';
import cx from 'classnames';

const BaseCarousel = (props) => {
    const {
        loop,
        render,
        isMobile,
        className,
        prevButton,
        nextButton,
        spaceBetween,
        slidesPerView,
        paginationRequired,
        showNavigationCarousel,
    } = props;

    let idCarousel = `${className}-${uuid()}`;
    let idPagination = `${className}-pagination-${uuid()}`;
    let idPrevBtn = `${className}-prev-btn-${uuid()}`;
    let idNextBtn = `${className}-next-btn-${uuid()}`;

    let currentSlide = ' (' + t('Current slide') + ')';
    let bulletMessage = t('Go to slide') + ' ';
    let activeSlide = 'swiper-slide-active';

    useEffect(() => {
        let swiper = new Swiper(`#${idCarousel}`, {
            slidesPerView: slidesPerView,
            spaceBetween: spaceBetween,
            loop: loop,
            centeredSlides: true,
            pagination: {
                el: `#${idPagination}`,
                clickable: true,
                renderBullet: function (index, classPagination) {
                    let i = parseInt(index) + 1;
                    //For the first slide
                    let slide = index === 0 ? currentSlide : '';
                    return (
                        '<span data-index=' +
                        index +
                        ' class="' +
                        className +
                        '__bullet ' +
                        classPagination +
                        '"><span role="button" aria-label="' +
                        bulletMessage +
                        i +
                        slide +
                        '"></span></span>'
                    );
                },
            },
            keyboard: {
                enabled: false,
                onlyInViewport: false,
            },
            navigation: {
                nextEl: `#${idNextBtn}`,
                prevEl: `#${idPrevBtn}`,
            },
            a11y: {
                prevSlideMessage: t('Previous slide'),
                nextSlideMessage: t('Next slide'),
                firstSlideMessage: t('This is the first slide'),
                lastSlideMessage: t('This is the last slide'),
                paginationBulletMessage: '',
            },
            on: {
                init: function () {
                    if (loop) {
                        let slides = document.getElementsByClassName('swiper-slide-duplicate');

                        for (let i = 0; i < slides.length; i++) {
                            let idSlide = slides[i].getAttribute('id');
                            idSlide += '_duplicated_' + i;
                            slides[i].setAttribute('id', idSlide);
                        }
                    }
                },
            },
        });

        /**
         * Checks the current active slide and adds the correspondent tabindex to the slides.
         */
        function checkSlides() {
            if (swiper.slides !== undefined) {
                let slides = Object.values(swiper.slides);
                for (let index = 0; index < slides.length - 1; index++) {
                    if (slides[index].className.indexOf(activeSlide) > -1) {
                        slides[index].setAttribute('tabindex', 0);
                    } else {
                        slides[index].setAttribute('tabindex', -1);
                    }
                }
            }
        }

        /**
         * When pagination updates, current slide text should be moved to the current bullet
         */
        swiper.on('paginationUpdate', function () {
            let bullets = Object.values(swiper.pagination.bullets);
            for (let index = 0; index < bullets.length - 1; index++) {
                let i = index + 1;
                //Cleans all the bullets
                bullets[index].innerHTML = '';
                if (index === swiper.activeIndex) {
                    bullets[index].setAttribute('aria-label', bulletMessage + i + currentSlide);
                } else {
                    bullets[index].setAttribute('aria-label', bulletMessage + i);
                }
            }
        });

        swiper.on('slideChangeTransitionEnd', function () {
            checkSlides();
        });

        /**
         * Adds to an event listener to document to allow swipe left or right only when it's on the carousel
         */
        document.addEventListener('keydown', (e) => {
            if (e.keyCode === 39) {
                if (document.activeElement.classList.toString().indexOf(className) > -1) {
                    swiper.slideNext();
                }
            }
            if (e.keyCode === 37) {
                if (document.activeElement.classList.toString().indexOf(className) > -1) {
                    swiper.slidePrev();
                }
            }
            //Stops propagation when the left/right buttons are pressed with the keyboard
            if (e.keyCode === 13) {
                if (document.activeElement.classList.toString().indexOf(className) > -1) {
                    e.preventDefault();
                    e.stopPropagation();
                }
            }
        });

        checkSlides();
    });

    return (
        <>
            <div className={cx('base-carousel', className)}>
                <div className={`${className}__container`}>
                    {showNavigationCarousel ? (
                        <>
                            <BaseButton
                                id={idPrevBtn}
                                type="tertiary"
                                children={prevButton}
                                className={`${className}__button-prev`}
                            ></BaseButton>
                            <BaseButton
                                id={idNextBtn}
                                type="tertiary"
                                children={nextButton}
                                className={`${className}__button-next`}
                            ></BaseButton>
                        </>
                    ) : (
                        <></>
                    )}
                    <div className="swiper-container" id={idCarousel}>
                        {!isMobile && paginationRequired && showNavigationCarousel ? (
                            <Box
                                className={`swiper-pagination ${className}__pagination`}
                                justify="center"
                                id={idPagination}
                            />
                        ) : (
                            <></>
                        )}
                        <ul className="swiper-wrapper">{render && render()}</ul>
                    </div>
                </div>
            </div>
        </>
    );
};

BaseCarousel.displayName = 'Base Carousel';

BaseCarousel.defaultProps = {
    slidesPerView: 1,
    spaceBetween: 0,
    paginationRequired: true,
    loop: false,
    showNavigationCarousel: true,
};

BaseCarousel.propTypes = {
    loop: PropTypes.bool,
    render: PropTypes.func,
    isMobile: PropTypes.bool,
    className: PropTypes.string,
    prevButton: PropTypes.object,
    nextButton: PropTypes.object,
    spaceBetween: PropTypes.number,
    slidesPerView: PropTypes.number,
    paginationRequired: PropTypes.bool,
    showNavigationCarousel: PropTypes.bool,
};

export default withBreakpoint(BaseCarousel);
