import React from 'react';
import cx from 'classnames';
import { wcmService } from '@konrad/reweb-aem';

const Space = (props) => {
    const { height } = props;

    return (
        <div
            className={cx(`ak-space--${height}`, { 'ak-space--edit': !wcmService.isDisabled() })}
        ></div>
    );
};

Space.displayName = 'Space';

Space.defaultProps = {
    height: 'medium',
};

export default Space;
