import React, { useState } from 'react';
import {
    BaseText,
    BaseTitle,
    BaseInput,
    BaseButton,
    BaseIcon,
    BaseSelect,
    BaseInstructions,
    BaseCheckbox,
} from '../../../base';
import uuidv4 from 'uuid/v4';
import { t } from '@konrad/reweb-aem';
import { STATES } from '../../../../lib/constants';
import { isRequired, isValidZipCode } from '../../../../lib/fieldValidations';
import PropTypes from 'prop-types';

const Two = (props) => {
    const {
        title,

        streetAddress,
        setStreetAddress,
        streetAddressLabel,
        streetAddressPlaceholder,

        city,
        setCity,
        cityLabel,
        cityPlaceholder,

        state,
        setState,
        stateLabel,
        statePlaceholder,

        zipCode,
        setZipCode,
        zipCodeLabel,
        zipCodePlaceholder,

        differentServiceAddress,
        setDifferentServiceAddress,
        differentServiceAddressCheckbox,

        serviceAddress,
        setServiceAddress,
        electricServiceLabel,
        electricServicePlaceholder,

        serviceCity,
        setServiceCity,
        serviceCityLabel,
        serviceCityPlaceholder,

        serviceState,
        setServiceState,
        serviceStateLabel,
        serviceStatePlaceholder,

        serviceZipCode,
        setServiceZipCode,
        serviceZipCodeLabel,
        serviceZipCodePlaceholder,

        backButtonLabel,
        CTALabelOne,
        stepTwoInstrImage,
        stepTwoInstrTitle,
        stepTwoInstrDescription,
        setChangedStep,
        step,
        amountOfSteps,
        setStep,
        goToFinalStep,
        validate,
    } = props;

    //Ids and references to the inputs
    const streetAddressId = uuidv4();
    const streetAddressRef = React.createRef();
    const cityId = uuidv4();
    const cityRef = React.createRef();
    const stateId = uuidv4();
    const stateRef = React.createRef();
    const zipCodeId = uuidv4();
    const zipCodeRef = React.createRef();
    const differentServiceAddressCheckboxId = uuidv4();
    const differentServiceAddressCheckboxRef = React.createRef();
    const serviceAddressId = uuidv4();
    const serviceAddressRef = React.createRef();
    const serviceCityId = uuidv4();
    const serviceCityRef = React.createRef();
    const serviceStateId = uuidv4();
    const serviceStateRef = React.createRef();
    const serviceZipCodeId = uuidv4();
    const serviceZipCodeRef = React.createRef();

    //States for the error messages on the inputs
    const [streetAddressErrorMessage, setStreetAddressErrorMessage] = useState('');
    const [cityErrorMessage, setCityErrorMessage] = useState('');
    const [stateErrorMessage, setStateErrorMessage] = useState('');
    const [zipCodeErrorMessage, setZipCodeErrorMessage] = useState('');
    const [serviceAddressErrorMessage, setServiceAddressErrorMessage] = useState('');
    const [serviceCityErrorMessage, setServiceCityErrorMessage] = useState('');
    const [serviceStateErrorMessage, setServiceStateErrorMessage] = useState('');
    const [serviceZipCodeErrorMessage, setServiceZipCodeErrorMessage] = useState('');

    //Validations for required inputs
    const requiredFields = [isRequired()];
    const zipCodeValid = [isRequired(), isValidZipCode()];

    /**
     * Goes through all the required fields validating the inputs
     * @returns {boolean}
     */ const validateInputs = () => {
        let valid = true;
        if (!validate(requiredFields, streetAddress, setStreetAddressErrorMessage)) {
            if (valid) {
                streetAddressRef && streetAddressRef.current.focus();
            }
            valid = false;
        }

        if (!validate(requiredFields, city, setCityErrorMessage)) {
            if (valid) {
                cityRef && cityRef.current.focus();
            }
            valid = false;
        }

        if (!validate(requiredFields, state, setStateErrorMessage)) {
            if (valid) {
                stateRef && stateRef.current.focus();
            }
            valid = false;
        }

        if (!validate(zipCodeValid, zipCode, setZipCodeErrorMessage)) {
            if (valid) {
                zipCodeRef && zipCodeRef.current.focus();
            }
            valid = false;
        }

        if (differentServiceAddress) {
            if (!validate(requiredFields, serviceAddress, setServiceAddressErrorMessage)) {
                if (valid) {
                    serviceAddressRef && serviceAddressRef.current.focus();
                }
                valid = false;
            }

            if (!validate(requiredFields, serviceCity, setServiceCityErrorMessage)) {
                if (valid) {
                    serviceCityRef && serviceCityRef.current.focus();
                }
                valid = false;
            }

            if (!validate(requiredFields, serviceState, setServiceStateErrorMessage)) {
                if (valid) {
                    serviceStateRef && serviceStateRef.current.focus();
                }
                valid = false;
            }

            if (!validate(zipCodeValid, serviceZipCode, setServiceZipCodeErrorMessage)) {
                if (valid) {
                    serviceZipCodeRef && serviceZipCodeRef.current.focus();
                }
                valid = false;
            }
        }
        return valid;
    };

    /**
     * Validates required inputs and changes the step
     * @param e
     */
    const handleNextClick = (e) => {
        e.stopPropagation();
        e.preventDefault();

        if (validateInputs()) {
            setChangedStep && setChangedStep(true);

            if (goToFinalStep) {
                setStep(4);
            } else {
                setStep(step + 1);
            }
        }
    };

    const handleBackClick = () => {
        setChangedStep && setChangedStep(true);
        setStep(step - 1);
    };

    return (
        <>
            {step === 2 && (
                <div className="ak-enrollment-frame__mailing">
                    <div className="ak-enrollment-frame__container">
                        <BaseText
                            className={'ak-enrollment-frame__label'}
                            level={'4'}
                            text={`${t('Step')} ${step} ${t('of')} ${amountOfSteps}`}
                        />
                        <BaseTitle headingLevel={'h2'} text={title} overrideStyle={'h4'} />
                    </div>
                    <div className="ak-enrollment-frame__container ak-enrollment-frame__container--flex">
                        <div className="ak-enrollment-frame__panel-left">
                            <div className={'ak-enrollment-frame__fields-row'}>
                                <div className={'ak-enrollment-frame__input'}>
                                    <BaseInput
                                        required
                                        type={'text'}
                                        id={streetAddressId}
                                        ref={streetAddressRef}
                                        value={streetAddress}
                                        label={streetAddressLabel}
                                        error={streetAddressErrorMessage}
                                        placeholder={streetAddressPlaceholder}
                                        onChange={(val) => setStreetAddress(val)}
                                    />
                                    <div className="ak-enrollment-frame__error-container">
                                        {streetAddressErrorMessage && (
                                            <BaseText
                                                id={`${streetAddressId}-error`}
                                                className="ak-enrollment-frame__error-message"
                                                text={streetAddressErrorMessage}
                                                level={'3'}
                                            />
                                        )}
                                    </div>
                                </div>
                                <div className={'ak-enrollment-frame__input'}>
                                    <BaseInput
                                        required
                                        type={'text'}
                                        id={cityId}
                                        ref={cityRef}
                                        value={city}
                                        label={cityLabel}
                                        error={cityErrorMessage}
                                        placeholder={cityPlaceholder}
                                        onChange={(val) => setCity(val)}
                                    />
                                    <div className="ak-enrollment-frame__error-container">
                                        {cityErrorMessage && (
                                            <BaseText
                                                id={`${cityId}-error`}
                                                className="ak-enrollment-frame__error-message"
                                                text={cityErrorMessage}
                                                level={'3'}
                                            />
                                        )}
                                    </div>
                                </div>
                            </div>

                            <div className={'ak-enrollment-frame__fields-row'}>
                                <div className={'ak-enrollment-frame__input'}>
                                    <BaseSelect
                                        required
                                        id={stateId}
                                        ref={stateRef}
                                        value={state}
                                        label={stateLabel}
                                        error={stateErrorMessage}
                                        ariaLabel={`${stateId}-label`}
                                        placeholder={statePlaceholder}
                                        onChange={(val) => setState(val)}
                                        className={'ak-enrollment-frame__select'}
                                        ariaLabelList={`${stateId}-label`}
                                        options={STATES}
                                    />
                                    <div className="ak-enrollment-frame__error-container">
                                        {stateErrorMessage && (
                                            <BaseText
                                                id={`${stateId}-error`}
                                                className="ak-enrollment-frame__error-message"
                                                text={stateErrorMessage}
                                                level={'3'}
                                            />
                                        )}
                                    </div>
                                </div>
                                <div className={'ak-enrollment-frame__input'}>
                                    <BaseInput
                                        required
                                        maskChar={''}
                                        type={'text'}
                                        mask={'99999'}
                                        id={zipCodeId}
                                        ref={zipCodeRef}
                                        value={zipCode}
                                        label={zipCodeLabel}
                                        error={zipCodeErrorMessage}
                                        placeholder={zipCodePlaceholder}
                                        onChange={(val) => setZipCode(val)}
                                    />
                                    <div className="ak-enrollment-frame__error-container">
                                        {zipCodeErrorMessage && (
                                            <BaseText
                                                id={`${zipCodeId}-error`}
                                                className="ak-enrollment-frame__error-message"
                                                text={zipCodeErrorMessage}
                                                level={'3'}
                                            />
                                        )}
                                    </div>
                                </div>
                            </div>

                            <div className={'ak-enrollment-frame__fields-row'}>
                                <div
                                    className={
                                        'ak-enrollment-frame__input ak-enrollment-frame__input--full'
                                    }
                                >
                                    <BaseCheckbox
                                        value={'alert'}
                                        checked={differentServiceAddress}
                                        id={differentServiceAddressCheckboxId}
                                        text={differentServiceAddressCheckbox}
                                        ref={differentServiceAddressCheckboxRef}
                                        className={'ak-enrollment-frame__checkbox'}
                                        onChange={(val) => setDifferentServiceAddress(val)}
                                    />
                                </div>
                            </div>

                            {differentServiceAddress ? (
                                <>
                                    <div className={'ak-enrollment-frame__fields-row'}>
                                        <div className={'ak-enrollment-frame__input'}>
                                            <BaseInput
                                                required
                                                type={'text'}
                                                id={serviceAddressId}
                                                ref={serviceAddressRef}
                                                value={serviceAddress}
                                                label={electricServiceLabel}
                                                error={serviceAddressErrorMessage}
                                                placeholder={electricServicePlaceholder}
                                                onChange={(val) => setServiceAddress(val)}
                                            />
                                            <div className="ak-enrollment-frame__error-container">
                                                {serviceAddressErrorMessage && (
                                                    <BaseText
                                                        id={`${serviceAddressId}-error`}
                                                        className="ak-enrollment-frame__error-message"
                                                        text={serviceAddressErrorMessage}
                                                        level={'3'}
                                                    />
                                                )}
                                            </div>
                                        </div>
                                        <div className={'ak-enrollment-frame__input'}>
                                            <BaseInput
                                                required
                                                type={'text'}
                                                id={serviceCityId}
                                                ref={serviceCityRef}
                                                value={serviceCity}
                                                label={serviceCityLabel}
                                                error={serviceCityErrorMessage}
                                                placeholder={serviceCityPlaceholder}
                                                onChange={(val) => setServiceCity(val)}
                                            />
                                            <div className="ak-enrollment-frame__error-container">
                                                {serviceCityErrorMessage && (
                                                    <BaseText
                                                        id={`${serviceCityId}-error`}
                                                        className="ak-enrollment-frame__error-message"
                                                        text={serviceCityErrorMessage}
                                                        level={'3'}
                                                    />
                                                )}
                                            </div>
                                        </div>
                                    </div>

                                    <div className={'ak-enrollment-frame__fields-row'}>
                                        <div className={'ak-enrollment-frame__input'}>
                                            <BaseSelect
                                                required
                                                id={serviceStateId}
                                                ref={serviceStateRef}
                                                value={serviceState}
                                                label={serviceStateLabel}
                                                error={serviceStateErrorMessage}
                                                ariaLabel={`${serviceStateId}-label`}
                                                placeholder={serviceStatePlaceholder}
                                                onChange={(val) => setServiceState(val)}
                                                className={'ak-enrollment-frame__select'}
                                                ariaLabelList={`${serviceStateId}-label`}
                                                options={STATES}
                                            />
                                            <div className="ak-enrollment-frame__error-container">
                                                {setServiceStateErrorMessage && (
                                                    <BaseText
                                                        id={`${serviceStateId}-error`}
                                                        className="ak-enrollment-frame__error-message"
                                                        text={serviceStateErrorMessage}
                                                        level={'3'}
                                                    />
                                                )}
                                            </div>
                                        </div>
                                        <div className={'ak-enrollment-frame__input'}>
                                            <BaseInput
                                                required
                                                maskChar={''}
                                                type={'text'}
                                                id={serviceZipCodeId}
                                                mask={'99999'}
                                                ref={serviceZipCodeRef}
                                                value={serviceZipCode}
                                                label={serviceZipCodeLabel}
                                                error={serviceZipCodeErrorMessage}
                                                placeholder={serviceZipCodePlaceholder}
                                                onChange={(val) => setServiceZipCode(val)}
                                            />
                                            <div className="ak-enrollment-frame__error-container">
                                                {setServiceZipCodeErrorMessage && (
                                                    <BaseText
                                                        id={`${serviceZipCodeId}-error`}
                                                        className="ak-enrollment-frame__error-message"
                                                        text={serviceZipCodeErrorMessage}
                                                        level={'3'}
                                                    />
                                                )}
                                            </div>
                                        </div>
                                    </div>
                                </>
                            ) : (
                                <></>
                            )}

                            <div className={'ak-enrollment-frame__btns-row'}>
                                <div className={'ak-enrollment-frame__element'}>
                                    {!goToFinalStep ? (
                                        <button
                                            type="button"
                                            className={'ak-enrollment-frame__back-btn'}
                                            onClick={() => handleBackClick()}
                                        >
                                            <BaseIcon
                                                className={'ak-enrollment-frame__chevron'}
                                                name={'chevron-down'}
                                            />
                                            {backButtonLabel}
                                        </button>
                                    ) : (
                                        <></>
                                    )}
                                </div>

                                <div
                                    className={
                                        'ak-enrollment-frame__element ak-enrollment-frame__input--justified-right'
                                    }
                                >
                                    <BaseButton
                                        className="ak-enrollment-frame__btn"
                                        onClick={(e) => handleNextClick(e)}
                                        altText={t('Next')}
                                        type="primary"
                                    >
                                        {CTALabelOne}
                                    </BaseButton>
                                </div>
                            </div>
                        </div>
                        <div className="ak-enrollment-frame__panel-right">
                            <BaseInstructions
                                fileReference={stepTwoInstrImage}
                                explanatoryTitle={stepTwoInstrTitle}
                                description={stepTwoInstrDescription}
                            />
                        </div>
                    </div>
                </div>
            )}
        </>
    );
};

Two.displayName = 'Two';

Two.propTypes = {
    title: PropTypes.string,

    streetAddress: PropTypes.string,
    setStreetAddress: PropTypes.func,
    streetAddressLabel: PropTypes.string,
    streetAddressPlaceholder: PropTypes.string,

    city: PropTypes.string,
    setCity: PropTypes.func,
    cityLabel: PropTypes.string,
    cityPlaceholder: PropTypes.string,

    state: PropTypes.string,
    setState: PropTypes.func,
    stateLabel: PropTypes.string,
    statePlaceholder: PropTypes.string,

    zipCode: PropTypes.string,
    setZipCode: PropTypes.func,
    zipCodeLabel: PropTypes.string,
    zipCodePlaceholder: PropTypes.string,

    backButtonLabel: PropTypes.string,
    CTALabelOne: PropTypes.string,
    stepTwoInstrImage: PropTypes.string,
    stepTwoInstrTitle: PropTypes.string,
    stepTwoInstrDescription: PropTypes.string,

    step: PropTypes.number,
    amountOfSteps: PropTypes.number,
    setStep: PropTypes.func,
    goToFinalStep: PropTypes.bool,
    validate: PropTypes.func,
};

export default Two;
