import React from 'react';
import { BaseText, BaseImage } from '../../../base';

const ImageCarouselItem = (props) => {
    const { id, item } = props;

    return (
        <li className="swiper-slide ak-image-carousel__item" id={id}>
            <div className="ak-image-carousel__image-container">
                <BaseImage
                    url={item.image}
                    className="ak-image-carousel__image"
                    alt={item.alternativeText}
                />
            </div>
            <div className="ak-image-carousel__text">
                <BaseText
                    className="ak-image-carousel__caption"
                    text={item.caption}
                    level={'4'}
                ></BaseText>
            </div>
        </li>
    );
};

ImageCarouselItem.displayName = 'ImageCarouselItem';

export default ImageCarouselItem;
