import React from 'react';
import PropTypes from 'prop-types';
import { BaseLink, BaseText, BaseImage } from '../base';

const EnrollByPhone = (props) => {
    const { title, fileReference, isStacked, iconAltText, phoneNumber } = props;

    return (
        <div className="ak-enroll-by-phone">
            <div className="ak-enroll-by-phone__container">
                <div
                    className={`ak-enroll-by-phone__title ak-enroll-by-phone__title--${
                        isStacked ? 'stacked' : ''
                    }`}
                >
                    <BaseText level="3" text={title} wrapper="span" />
                </div>
                <div
                    className={`ak-enroll-by-phone__phone ak-enroll-by-phone__phone--${
                        isStacked ? 'stacked' : ''
                    }`}
                >
                    <div className="ak-enroll-by-phone__phone-wrapper">
                        <BaseImage
                            url={fileReference}
                            className="ak-enroll-by-phone__icon"
                            altText={iconAltText}
                        />
                        <BaseLink url={`tel:+${phoneNumber && phoneNumber.replace(/-/g, '')}`}>
                            <BaseText
                                level="3"
                                text={phoneNumber}
                                wrapper="span"
                                className="ak-enroll-by-phone__phone-number"
                            />
                        </BaseLink>
                    </div>
                </div>
            </div>
        </div>
    );
};

EnrollByPhone.displayName = 'EnrollByPhone';

EnrollByPhone.defaultProps = {
    title: PropTypes.string,
    fileReference: PropTypes.string,
    isStacked: PropTypes.bool,
    iconAltText: PropTypes.string,
    phoneNumber: PropTypes.string,
};

export default EnrollByPhone;
